<!--
 * @Autor: linxu
 * @Date: 2022-03-14 10:05:41
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-22 14:58:15
 * @Description: 新增/编辑 维保策略弹窗
-->
<template>
  <div>
    <el-dialog
      :title="title"
      class="edit-strategy-dialog"
      width="880px"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <!-- 第一步 选择维保策略 -->
      <div class="select-strategy" v-if="step === 1">
        <!-- 筛选 -->
        <div class="search-box">
          <div class="search-item">
            <span>维保策略名称:</span>
            <el-input
              placeholder="请输入维保策略名称"
              v-model="searchForm.StrategyName"
              @change="resetCurrentParams()"
            >
            </el-input>
          </div>
          <div class="search-item">
            <span>发动机品牌:</span>
            <el-select
              v-model="searchForm.EngineBrandId"
              :popper-append-to-body="false"
              placeholder="请选择发动机品牌"
              @change="resetCurrentParams()"
            >
              <el-option label="全部" value=""></el-option>
              <el-option
                v-for="item in EngineBrandList"
                :key="item.EngineBrandId"
                :label="item.BrandName"
                :value="item.EngineBrandId"
              ></el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span>策略主备分类:</span>
            <el-select
              v-model="searchForm.Type"
              :popper-append-to-body="false"
              placeholder="请选择策略主备分类"
              @change="resetCurrentParams()"
            >
              <el-option label="全部" value=""></el-option>
              <el-option
                v-for="item in StrategyTypeEnum"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <!-- <div class="search-btn-box">
            <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
            <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
          </div> -->
        </div>

        <!-- 表格 -->
        <TableList
          :options="options"
          :columns="columns"
          :dataSource="dataSource"
          :dataTotal="dataTotal"
          :operates="operates"
          :pagination="paginationCfg"
          @handleChangePage="handleChangePage"
          @handleChangeSize="handleChangeSize"
          border
          v-loading="loading"
        >
          <!-- <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group">
                <el-button type="text" @click="handleSelect(scope.row)"> 选中 </el-button>
              </div>
            </template>
          </el-table-column> -->
        </TableList>
      </div>
      <!-- 第二步 优化维保策略 -->
      <div class="edit-strategy" v-else>
        <!-- 操作按钮 -->
        <div class="operation-box">
          <el-button type="primary" @click="prevStep()">上一步</el-button>
          <el-button class="right-btn" type="primary" @click="saveAsMould()">
            储存当前策略为新模板
          </el-button>
        </div>
        <div>
          <ul class="title">
            <li>序号</li>
            <li>维保事项</li>
            <li>运行时间</li>
            <li>绝对时间</li>
          </ul>
          <ul class="content">
            <li class="content-item" v-for="(item, index) in StrategyItemList" :key="index">
              <div>
                <span>{{ index + 1 }}</span>
              </div>
              <div>
                <span>{{ item.ItemContent }}</span>
              </div>
              <div>
                <el-input v-model="item.DefaultHourSetting" placeholder="请输入内容"></el-input
                ><em>小时</em>
              </div>
              <div>
                <el-input v-model="item.DefaultDaySetting" placeholder="请输入内容"></el-input
                ><em>天</em>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" v-if="step === 1">
        <p class="select-text">
          当前已选择：<span>{{ selectItem && selectItem.StrategyName }}</span>
        </p>
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="nextStep()">下一步</el-button>
      </div>
      <div slot="footer" class="dialog-footer" v-else>
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="handleSave()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import _data from './data'
const { StrategyTypeEnum } = _data

export default {
  name: 'StrategyEditDialog',
  mixins: [paginationConfigMixin],
  props: {
    // 机组ID
    gensetId: {
      type: Number,
      default: null
    },
    setStrategyType: String
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  data() {
    return {
      ProjectId: null,
      loading: false,
      dialigVisible: true,
      title: '第一步：选择维保策略',
      step: 1,
      EngineBrandList: [], // 发动机品牌
      StrategyTypeEnum,
      // 筛选条件
      searchForm: {
        StrategyName: '',
        EngineBrandId: '',
        Type: ''
      },
      // 表格
      columns: _data.strategyColumns(this),
      operates: _data.listOperates(this),
      dataSource: [],
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 86 // 操作列宽度
        }
      },
      StrategyItemList: [],

      selectItem: null, // 选中数据

      GensetSettingDetail: null
    }
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          console.log(nv)
          this.$set(this, 'projectId', nv.ProjectId)
          this.$set(this, 'projectStatus', nv.Status)
        }
      },
      deep: true,
      immediate: true
    },
    searchForm: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        // this.resetCurrentParams()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    this.getEngineBrandList()
    // 编辑策略 查询机组策略详情 设置默认选中策略模板
    if (this.setStrategyType == 'edit') {
      this.$api.project
        .GetMaintenanceGensetSettingDetail({}, { GensetId: this.gensetId })
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'GensetSettingDetail', res.Data)
            this.queryTableData(1)
          }
        })
    } else {
      this.queryTableData()
    }
  },
  methods: {
    // 查询发动机品牌
    getEngineBrandList() {
      this.$api.operation.GetMaintenanceGensetEngineBrand().then((res) => {
        if (res.Code === 1) {
          this.$set(this, 'EngineBrandList', res.Data)
        }
      })
    },

    // 查询维保策略
    queryTableData(type) {
      this.loading = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        ...this.searchForm
      }
      this.$api.operation
        .GetMaintenanceGensetStrategyManage(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
            // 编辑--设置默认选中
            if (type == 1) {
              res.Data.Data.forEach((item) => {
                if (item.StrategyId == this.GensetSettingDetail.StrategyId) {
                  this.$set(this, 'selectItem', item)
                }
              })
            }
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 选中
    handleSelect(row) {
      console.log(row)
      this.selectItem = row
    },

    // 下一步
    nextStep() {
      if (!!this.selectItem) {
        if (
          this.setStrategyType == 'add' ||
          (this.setStrategyType == 'edit' &&
            this.selectItem.StrategyId != this.GensetSettingDetail.StrategyId)
        ) {
          // 查询维保策略模板详情
          this.$api.operation
            .GetMaintenanceGensetStrategyDetail({}, { StrategyId: this.selectItem.StrategyId })
            .then((res) => {
              if (res.Code === 1) {
                // this.$set(this, 'StrategyItemList', res.Data.StrategyItemList)
                // this.title = '第二步：维保策略优化'
                // this.step = 2
                const StrategyItemList = res.Data.StrategyItemList.map((item) => {
                  return {
                    ItemContent: item.ItemContent,
                    DefaultHourSetting: item.DefaultHourSetting,
                    DefaultDaySetting: item.DefaultDaySetting,
                    MaintenanceProgramId: null,
                    MaintenanceItemId: null,
                    add: true
                  }
                })
                this.$router.push({
                  path: '/project/controller-configuration',
                  query: {
                    projectId: this.ProjectId,
                    gensetId: this.gensetId,
                    activeTab: 14,
                    setStrategyType: 'add',
                    StrategyId: this.selectItem.StrategyId,
                    StrategyItemList: JSON.stringify(StrategyItemList)
                  }
                })
              }
            })
        } else {
          this.$set(this, 'StrategyItemList', this.GensetSettingDetail.ProgramList)
          // this.title = '第二步：维保策略优化'
          // this.step = 2
          this.$router.push({
            path: '/project/controller-configuration',
            query: {
              projectId: this.ProjectId,
              gensetId: this.gensetId,
              activeTab: 14,
              setStrategyType: 'edit',
              StrategyId: this.selectItem.StrategyId,
              StrategyItemList: JSON.stringify(this.GensetSettingDetail.ProgramList)
            }
          })
        }

        return
      }
      this.$message.warning('请选择维保策略')
    },
    // 上一步
    prevStep() {
      this.title = '第一步：选择维保策略'
      this.step = 1
    },
    // 保存为新模板
    saveAsMould() {
      console.log(this.StrategyItemList)
      this.$router.push({
        path: '/setting/agreement-management/maintenance-strategy-new-add',
        query: { list: this.StrategyItemList }
      })
    },
    // 保存
    handleSave() {
      console.log(this.StrategyItemList)
      if (
        this.setStrategyType == 'add' ||
        (this.setStrategyType == 'edit' &&
          this.selectItem.StrategyId != this.GensetSettingDetail.StrategyId)
      ) {
        this.addSave()
        return
      }
      this.editSave()
    },
    // 新增保存
    addSave() {
      const query = { GensetId: this.gensetId }
      const params = {
        StrategyId: this.selectItem.StrategyId,
        Remark: '',
        ItemList: this.StrategyItemList.map((item) => {
          return {
            OrderBy: item.OrderBy,
            ItemContent: item.ItemContent,
            DefaultHourSetting: item.DefaultHourSetting,
            DefaultDaySetting: item.DefaultDaySetting,
            Remark: item.Remark
          }
        })
      }
      this.$api.project.AddMaintenanceGensetSetting(params, query).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.$message({
            type: 'success',
            message: '添加机组维保策略成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '添加机组维保策略失败，请重新提交!'
          })
        }

        this.$emit('closeDialog', { refresh: true })
      })
    },
    // 编辑保存
    editSave() {
      const query = { GensetId: this.gensetId }
      const params = {
        StrategyId: this.selectItem.StrategyId,
        ProgramList: this.StrategyItemList.map((item) => {
          return {
            MaintenanceProgramId: item.MaintenanceProgramId,
            MaintenanceItemId: item.MaintenanceItemId,
            ItemContent: item.ItemContent,
            DefaultHourSetting: item.DefaultHourSetting,
            DefaultDaySetting: item.DefaultDaySetting
          }
        })
      }
      this.$api.project.EditMaintenanceGensetSetting(params, query).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.$message({
            type: 'success',
            message: '编辑机组维保策略成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '编辑机组维保策略失败，请重新提交!'
          })
        }

        this.$emit('closeDialog', { refresh: true })
      })
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.edit-strategy-dialog {
  .search-box {
    background: transparent;
    border: 0;
    padding: 0;
    .search-item {
      width: 33.3%;
      padding-bottom: 0;
      &:last-child {
        padding-right: 0;
      }
    }
  }
  .select-text {
    float: left;
    line-height: 32px;
    span {
      color: #409eff;
      margin-left: 8px;
    }
  }
}
.edit-strategy {
  .operation-box {
    margin-bottom: 16px;
    .right-btn {
      float: right;
    }
  }
  ul.title,
  ul.content {
    margin-left: 1px;
  }
  ul.title {
    display: flex;
    line-height: 32px;
    text-align: center;
    > li {
      padding: 8px 12px;
      margin: 0 0 -1px -1px;
      border: 1px solid #e4e4e4;
    }
    > li:nth-child(1) {
      width: 10%;
    }
    > li:nth-child(2),
    > li:nth-child(3),
    > li:nth-child(4) {
      width: 30%;
    }
  }

  ul.content > li.content-item {
    display: flex;
    line-height: 32px;
    text-align: center;
    > div {
      padding: 8px 12px;
      margin: 0 0 -1px -1px;
      border: 1px solid #e4e4e4;
      text-align: center;
    }
    > div:nth-child(1) {
      width: 10%;
    }
    > div:nth-child(2) {
      width: 30%;
    }
    > div:nth-child(3),
    > div:nth-child(4) {
      width: 30%;
      display: flex;
      em {
        white-space: nowrap;
        margin-left: 8px;
      }
    }
  }
}
</style>



