<!--
 * @Autor: linxu
 * @Date: 2022-03-19 11:38:35
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 14:14:37
 * @Description: 项目工程进度
-->
<template>
  <div class="project-progress-manage">
    <p class="content-title">项目机组工程进度（整体）</p>
    <!-- 筛选 -->
    <!-- <div class="search-box">
      <div class="search-item">
        <span>事件类型:</span>
        <el-select
          v-model="searchForm.type"
          :popper-append-to-body="false"
          placeholder="请选择事件类型"
        >
          <el-option label="全部" value=""></el-option>
          <el-option label="项目启动" value="1"></el-option>
          <el-option label="项目完工" value="2"></el-option>
        </el-select>
      </div>
      <div class="search-item">
        <span>事件状态:</span>
        <el-select
          v-model="searchForm.type1"
          :popper-append-to-body="false"
          placeholder="请选择事件状态"
        >
          <el-option label="全部" value=""></el-option>
          <el-option label="待完成" value="1"></el-option>
          <el-option label="已完成" value="2"></el-option>
        </el-select>
      </div>
      <div class="search-btn-box">
        <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
        <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
      </div>
    </div> -->

    <!-- 表格 -->
    <TableList
      :options="options"
      :columns="columns"
      :dataSource="dataSource"
      border
      v-loading="loading"
    >
      <el-table-column
        slot="operation"
        label="操作"
        align="center"
        v-bind="options && options.props"
      >
        <template slot-scope="scope">
          <div class="operate-group" style="text-align: right">
            <el-button type="text" @click="handleProjectProgressClick(scope.row)">
              工程进度管理
            </el-button>
          </div>
        </template>
      </el-table-column>
    </TableList>

    <!-- 工程进度记录表 -->
    <el-dialog
      title="工程进度记录"
      class="progress-records-dialog"
      width="60%"
      :visible.sync="RecordsDialogVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
      @close="closeDialog('RecordsDialogVisible')"
    >
      <ProjectProgressRecords v-if="RecordsDialogVisible" :recordParmas="recordParmas" />
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import ProjectProgressRecords from './ProjectProgressRecords'
import _data from './components/data'

export default {
  name: 'GensetProgress',
  components: {
    ProjectProgressRecords
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          this.$set(this, 'ProjectId', nv.ProjectId)
          this.$set(this, 'projectStatus', nv.Status)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      ProjectId: null,
      projectStatus: null,
      crewColumns: _data.crewManageColumns(this),
      crewDataSource: [],
      /****** 工程进度 ******/
      // 筛选条件
      eventTypeEnum: _data.eventTypeEnum,
      eventStatusEnum: _data.eventStatusEnum,
      searchForm: {
        type: '',
        type1: ''
      },
      // 表格
      columns: _data.projectProgressColumns(this),
      dataSource: [],
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },

      selectItem: null, // 选中数据
      RecordsDialogVisible: false,
      recordParmas: null
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    this.initData()
  },
  methods: {
    initData() {
      this.queryTableData()
    },
    // 查询进度事件
    queryTableData() {
      this.loading = true
      const params = {
        // ...this.searchForm,
        ProjectId: this.ProjectId,
        ProgressType: 1
      }
      this.$api.project
        .GetGensetProjectProgressSetting(params)
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'dataSource', res.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    reset() {
      this.$set(this, 'searchForm', {
        type: '',
        type1: ''
      })
    },
    // 项目工程进度
    handleProjectProgressClick(row) {
      // this.selectItem = row
      this.recordParmas = {
        ProjectId: this.ProjectId, // 项目ID
        ProjectProgressId: row.ProgressId, // 项目事件ID
        IsGenset: row.IsGenset, // 是否绑定机组
        IsGensetMult: row.IsGensetMult, // 是否绑定多个机组
        projectStatus: this.projectStatus // 项目状态
      }
      this.RecordsDialogVisible = true

      // this.$router.push({
      //   path: '/project/project-progress-record',
      //   query: {
      //     ProjectId: this.ProjectId, // 项目ID
      //     ProjectProgressId: row.ProgressId, // 项目事件ID
      //     IsGenset: row.IsGenset, // 是否绑定机组
      //     IsGensetMult: row.IsGensetMult, // 是否绑定多个机组
      //     projectStatus: this.projectStatus // 项目状态
      //   }
      // })
    },
    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    },

    handleBackClick() {
      this.$router.push({ path: '/project/crew-manage', query: { ProjectId: this.ProjectId } })
    }
  }
}
</script>

<style lang="less" scoped>
.project-progress-manage {
  // margin-top: 32px;
  .search-box {
    background-color: transparent;
    padding: 0 16px;
    height: 32px;
    border: 0;
  }
}
</style>



