 * @LastEditors: linxu
 * @LastEditTime: 2022-03-21 15:30:37
 * @Description: 查看开支图片弹窗
-->
<template>
  <el-dialog
    title="查看开支图片"
    class="show-img-dialog"
    width="540px"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <div class="dialog-content">
      <template v-for="item in imgUrlArr">
        <el-image class="img-item" :src="previewUrl(item)" :fit="fit"></el-image>
      </template>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="close()">取 消</el-button>
      <el-button type="primary" @click="close()">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'SpendingImgDialog',
  props: {
    imgUrlArr: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      dialigVisible: true,
      fit: 'fill'
    }
  },
  // watch: {
  //   imgUrlArr: {
  //     handler: function (newVal, oldVal) {
  //       console.log(newVal, oldVal)
  //     },
  //     deep: true
  //   }
  // },
  mounted() {
    console.log(this.imgUrlArr, process.env.BASE_API)
  },
  methods: {
    previewUrl(url) {
      return `${process.env.BASE_API}/${url}`
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  display: flex;
  .img-item {
    width: 150px;
    height: 150px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}
</style>



