<!--
 * @Autor: linxu
 * @Date: 2022-03-03 16:17:06
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:15:14
 * @Description: 项目详情
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="project-detail">
      <!-- 基本信息 -->
      <div class="project-info-item">
        <p class="content-title">
          基本信息
          <el-button
            v-if="projectStatus !== 3"
            type="primary"
            class="comColor"
            @click="handleEditProject()"
            >编辑</el-button
          >
          <el-button
            v-if="projectStatus === 1"
            type="primary"
            class="comColor"
            @click="handleOpen()"
          >
            开启项目
          </el-button>
          <el-button
            v-if="projectStatus === 2"
            type="primary"
            class="comColor"
            @click="handleClose()"
          >
            完结项目
          </el-button>
        </p>
        <BasicInfoList :projectBasicInfo="projectBasicInfo" v-loading="loading_1" />
      </div>

      <!-- 合同文件 -->
      <div class="project-info-item">
        <p class="content-title">
          合同文件
          <el-button
            v-if="projectStatus !== 3"
            type="primary"
            class="comColor"
            @click="handleUploadFile()"
          >
            上传文件
          </el-button>
        </p>
        <div class="item-content contract-document">
          <TableList
            :options="options"
            :columns="contractColumns"
            :dataSource="fileTableData"
            v-loading="loading_2"
          >
            <el-table-column
              slot="operation"
              label="操作"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group">
                  <el-button type="text" @click="handleShowFileList(scope.row)"> 查看 </el-button>
                  <el-button
                    v-if="projectStatus !== 3"
                    type="text"
                    @click="handleDeleteFile(scope.row)"
                  >
                    删除
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
      </div>

      <!-- 项目人员 -->
      <div class="project-info-item">
        <p class="content-title">
          项目人员
          <el-button
            v-if="projectStatus !== 3"
            type="primary"
            class="comColor"
            @click="handleAddPersonnel()"
          >
            添加人员
          </el-button>
        </p>
        <div class="item-content personnel-list">
          <TableList
            :options="options"
            :columns="personnelColumns"
            :dataSource="userTableData"
            v-loading="loading_3"
          >
            <el-table-column
              v-if="projectStatus !== 3"
              slot="operation"
              label="操作"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group">
                  <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleEditPersonnel(scope.row)"
                  >
                    编辑
                  </el-button>
                  <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleDeletePersonnel(scope.row)"
                  >
                    删除
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
      </div>
    </div>

    <!-- 编辑项目弹窗 -->
    <AddOrEditProjectDialog
      v-if="dialogEditVisible"
      :projectId="ProjectId"
      @closeDialog="(val) => closeDialog('dialogEditVisible', val)"
    />

    <!-- 上传文件弹窗 -->
    <FileUploadDialog
      v-if="fileUploadVisible"
      :projectId="ProjectId"
      @closeDialog="(val) => closeDialog('fileUploadVisible', val)"
    />

    <!-- 文件查看弹窗 -->
    <FileListDialog
      v-if="fileListVisible"
      :fileRecordInfo="fileRecordInfo"
      @closeDialog="(val) => closeDialog('fileListVisible', val)"
    />

    <!-- 添加/编辑 人员弹窗 -->
    <AddOrEditPersonnelDialog
      v-if="personnelManageVisible"
      :projectId="ProjectId"
      :personnelInfo="personnelInfo"
      :CustomerTypeEnum="CustomerTypeEnum"
      :UserTypeEnum="UserTypeEnum"
      @closeDialog="(val) => closeDialog('personnelManageVisible', val)"
    />

    <!-- 开启项目弹窗 -->
    <OpenProjectDialog
      v-if="dialogOpenVisible"
      :projectInfo="projectBasicInfo"
      @handleOpenConfirm="handleOpenConfirm"
      @closeDialog="(val) => closeDialog('dialogOpenVisible', val)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import BasicInfoList from '../components/BasicInfoList.vue'
import AddOrEditProjectDialog from '../components/AddOrEditProjectDialog'
import OpenProjectDialog from '../components/OpenProjectDialog'
import FileUploadDialog from '../components/FileUploadDialog'
import FileListDialog from '../components/FileListDialog'
import AddOrEditPersonnelDialog from '../components/AddOrEditPersonnelDialog'

import _data from '../components/data.js'

export default {
  name: 'ProjectDetails',
  components: {
    AddOrEditProjectDialog,
    OpenProjectDialog,
    BasicInfoList,
    FileUploadDialog,
    FileListDialog,
    AddOrEditPersonnelDialog
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          this.$set(this, 'ProjectId', nv.ProjectId)
          this.$set(this, 'projectStatus', nv.Status)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading_1: false,
      loading_2: false,
      loading_3: false,

      dialogEditVisible: false, // 项目编辑弹窗

      dialogOpenVisible: false, // 开启项目弹窗
      // 项目基本信息projectBasicInfo
      ProjectId: null,
      projectBasicInfo: {},
      projectStatus: null, // 项目状态

      contractColumns: _data.contractColumns(this), // 合同文件 表头
      fileTableData: [],
      fileUploadVisible: false, // 上传文件弹窗
      fileListVisible: false, // 查看文件弹窗
      fileRecordInfo: null,

      CustomerTypeEnum: null, //公司类型枚举
      UserTypeEnum: null, //用户类型枚举
      personnelColumns: _data.personnelColumns(this), // 项目人员 表头
      userTableData: [],
      personnelManageVisible: false, // 新增/编辑 人员弹窗
      personnelInfo: null,

      options: {
        // 表格
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 120 // 操作列宽度
        }
      }
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    this.projectStatus = this.currentProject.Status
    this.initData()
  },
  methods: {
    initData() {
      // 查询项目基本信息
      this.getProjectBasicInfo()
      // 查询合同文件
      this.getProjectFileList()
      // 查询用户列表
      this.getCustomerAddUserType().then(() => {
        this.getProjectUserList()
      })
    },
    /*************** 基本信息 ***************/
    // 查询项目基本信息
    getProjectBasicInfo() {
      this.loading_1 = true
      const query = { id: this.ProjectId }
      this.$api.project.GetGensetProject(query).then((res) => {
        if (res.Code === 1) {
          this.$set(this, 'projectBasicInfo', res.Data)
          this.$set(this, 'projectStatus', res.Data.Status)
        }
        this.loading_1 = false
      })
    },
    // 编辑项目
    handleEditProject() {
      this.dialogEditVisible = true
    },

    // 开启项目
    handleOpen() {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '开启项目',
        message: h('div', [
          h('p', [
            h('span', '确定开启项目“'),
            h('em', { style: 'color: #1ABC9C;margin: 0 4px;' }, this.projectBasicInfo.ProjectName),
            h('span', '”吗？')
          ]),
          h('p', '开启操作不可取消，请谨慎操作。')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.changeProjectStatus(this.projectBasicInfo.ProjectId, 2)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
      // this.dialogOpenVisible = true
    },
    handleOpenConfirm(val) {
      this.changeProjectStatus(val.projectId, val.status)
    },

    // 完结项目
    handleClose() {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '完结项目',
        message: h('div', [
          h('p', [
            h('span', '确定完结项目“'),
            h('em', { style: 'color: #1ABC9C,margin: 0 4px;' }, this.projectBasicInfo.ProjectName),
            h('span', '”吗？')
          ]),
          h('p', '完结后，项目相关信息将不再支持编辑，操作不可取消，请谨慎操作。')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // this.$message({
          //   type: 'success',
          //   message: '完结项目成功!'
          // })
          this.changeProjectStatus(this.ProjectId, 3)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    changeProjectStatus(projectId, status) {
      const params = {
        id: projectId,
        status: status
      }
      this.$api.project.ChangeGensetProjectStatus(params).then((res) => {
        // console.log('project-status', res)
        if (res.Code === 1) {
          this.queryTableData()
          this.$message({
            type: 'success',
            message: res.Message
          })
        }
      })
    },

    /*************** 合同文件 ***************/
    // 查询合同文件列表
    getProjectFileList() {
      this.loading_2 = true
      const query = {
        Page: 1,
        PageSize: 99
      }
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectFileManage(params, query).then((res) => {
        if (res.Code === 1) {
          // console.log('合同文件', res)
          this.fileTableData = res.Data.Data
        }
        this.loading_2 = false
      })
    },
    // 上传文件
    handleUploadFile() {
      this.fileUploadVisible = true
    },
    // 查看文件
    handleShowFileList(row) {
      this.fileRecordInfo = row
      this.fileListVisible = true
    },
    // 删除文件
    handleDeleteFile(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '删除文件',
        message: h('div', [h('p', [h('span', '确定删除当前文件吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.ProjectFileId }
          this.$api.project.DeleteGensetProjectFile(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '删除文件成功!'
              })

              this.getProjectFileList()
            } else {
              this.$message({
                type: 'error',
                message: '删除文件失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    /*************** 项目人员 ***************/
    // 查询公司类型、用户类型
    async getCustomerAddUserType() {
      await this.$api.project.GetCustomerType().then((res) => {
        if (res.Code === 1) {
          this.CustomerTypeEnum = res.Data.map((item) => {
            return {
              value: item.Type,
              label: item.TypeName
            }
          })
        }
      })
      await this.$api.project.GetUserType().then((res) => {
        if (res.Code === 1) {
          this.UserTypeEnum = res.Data.map((item) => {
            return {
              value: item.Type,
              label: item.TypeName
            }
          })
        }
      })
    },
    // 查询用户列表
    getProjectUserList() {
      this.loading_3 = true
      const query = {
        Page: 1,
        PageSize: 99
      }
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectJoinUserManage(params, query).then((res) => {
        if (res.Code === 1) {
          // console.log('用户列表', res)
          this.userTableData = res.Data.Data.map((item) => {
            return {
              ID: item.ProjectJoinUserId,
              CustomerType: item.CustomerType,
              UserType: item.UserType,
              ...item.JoinUser
            }
          })
        }
        this.loading_3 = false
      })
    },
    // 添加人员
    handleAddPersonnel() {
      this.personnelInfo = null
      this.personnelManageVisible = true
    },
    // 编辑人员
    handleEditPersonnel(row) {
      this.personnelInfo = row
      this.personnelManageVisible = true
    },

    // 删除人员
    handleDeletePersonnel(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '删除人员',
        message: h('div', [h('p', [h('span', '确定删除当前项目人员吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.ID }
          this.$api.project.DeleteGensetProjectJoinUser(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '删除人员成功!'
              })
              this.getProjectUserList()
            } else {
              this.$message({
                type: 'error',
                message: '删除人员失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 返回项目列表
    handleBackClick() {
      this.$router.push({ path: '/project/project-list' })
    },

    closeDialog(key, val) {
      // console.log(key, val)
      this[key] = false
      if (val && val.refresh) {
        switch (key) {
          case 'dialogEditVisible': // 项目基本信息
            this.getProjectBasicInfo()
            break
          case 'fileUploadVisible': // 上传文件
            this.getProjectFileList()
            break
          case 'personnelManageVisible':
            this.getProjectUserList()
            break
          case 'crewManageVisible': // 项目机组
            this.getProjectGensetList()
            break
          case 'parallelCerwManageVisible': // 项目并机组
            this.getProjectParallelList()
            this.getProjectGensetList()
            break
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.project-detail {
  display: initial !important;
  .project-info-item {
    // height: 500px;
    .content-title {
      height: 48px;
      line-height: 48px;
      .comColor {
        float: right;
        margin: 8px 16px;
      }
    }

    .item-content {
      margin: 0 0 32px 0px;
    }
  }
}
</style>



