<!--
 * @Autor: linxu
 * @Date: 2022-03-15 10:12:21
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 10:56:22
 * @Description: 单机工程进度
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="genset-progress-manage">
      <!-- <p class="content-title">所选机组</p>
      <TableList
        class="crewTable"
        :options="options"
        :columns="crewColumns"
        :dataSource="crewDataSource"
        border
        v-loading="loading"
      >
      </TableList> -->
      <p class="content-title">工程进度</p>
      <!-- 筛选 -->
      <!-- <div class="search-box">
        <div class="search-item">
          <span>事件类型:</span>
          <el-select
            v-model="searchForm.type"
            :popper-append-to-body="false"
            placeholder="请选择事件类型"
          >
            <el-option label="全部" value=""></el-option>
            <el-option label="项目启动" value="1"></el-option>
            <el-option label="项目完工" value="2"></el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>事件状态:</span>
          <el-select
            v-model="searchForm.type1"
            :popper-append-to-body="false"
            placeholder="请选择事件状态"
          >
            <el-option label="全部" value=""></el-option>
            <el-option label="待完成" value="1"></el-option>
            <el-option label="已完成" value="2"></el-option>
          </el-select>
        </div>
        <div class="search-btn-box">
          <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
          <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
        </div>
      </div> -->

      <!-- 表格 -->
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        border
        v-loading="loading_1"
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group" style="text-align: right">
              <el-button
                type="text"
                :disabled="scope.row.RecordCount == 0"
                @click="handleProjectProgressClick(scope.row)"
              >
                工程进度管理
              </el-button>
            </div>
          </template>
        </el-table-column>
      </TableList>

      <!-- 工程进度记录表 -->
      <GensetProgressRecordsDialog
        v-if="RecordsDialogVisible"
        :ProjectProgressId="selectItem.ProgressId"
        :ProjectId="ProjectId"
        :GensetId="GensetId"
        @closeDialog="(val) => closeDialog('RecordsDialogVisible', val)"
      />
    </div>
  </div>
</template>
<script>
import GensetProgressRecordsDialog from './components/GensetProgressRecordsDialog.vue'
import _data from './components/data'

export default {
  name: 'GensetProgress',
  components: {
    GensetProgressRecordsDialog
  },
  props: ['configData'],
  data() {
    return {
      loading: false,
      loading_1: false,
      // ProjectId: parseInt(this.$route.query.ProjectId),
      // GensetId: parseInt(this.$route.query.GensetId),
      ProjectId: null,
      GensetId: null,

      crewColumns: _data.crewInfoColumns(this),
      crewDataSource: [],
      /****** 工程进度 ******/
      // 筛选条件
      searchForm: {
        type: '',
        type1: ''
      },
      // 表格
      columns: _data.gensetProgressColumns(this),
      dataSource: [],
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },

      selectItem: null, // 选中数据
      RecordsDialogVisible: false
    }
  },
  watch: {
    configData: {
      handler: function (newVal, oldVal) {
        console.log('configData', newVal)
        this.ProjectId = newVal.projectId
        this.GensetId = newVal.gensetId
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    console.log(this.ProjectId, this.GensetId)
    this.queryTableData()
    // this.getGensetDetail()
  },
  methods: {
    // 查询机组详情
    getGensetDetail() {
      const query = {
        GeneratorSetId: this.GensetId
      }
      this.$api.project.GetGensetProjectJoinGensetDetail({}, query).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.$set(this, 'crewDataSource', [res.Data])
        }
      })
    },
    // 查询机组进度事件
    queryTableData() {
      this.loading = true
      const params = {
        ProjectId: this.ProjectId,
        ProgressType: 1,
        GeneratorSetId: this.GensetId
      }
      this.$api.project
        .GetGensetProjectProgressSetting(params)
        .then((res) => {
          if (res.Code === 1) {
            // console.log(res)
            this.$set(this, 'dataSource', res.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    reset() {
      this.$set(this, 'searchForm', {
        type: '',
        type1: ''
      })
    },
    // 项目工程进度
    handleProjectProgressClick(row) {
      this.selectItem = row
      this.RecordsDialogVisible = true
    },
    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    },

    handleBackClick() {
      this.$router.push({ path: '/project/project-detail', query: { active: 2 } })
    }
  }
}
</script>

<style lang="less" scoped>
.genset-progress-manage {
  display: initial !important;
  .crewTable {
    margin-bottom: 16px;
  }
  .search-box {
    background-color: transparent;
    padding: 0 16px;
    height: 32px;
    border: 0;
  }
}
</style>



