<!--
 * @Autor: linxu
 * @Date: 2022-03-04 16:45:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-05-20 16:06:51
 * @Description: 查看文件弹窗
-->
<template>
  <div>
    <el-dialog
      title="合同文件查看"
      class="show-file-dialog"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <div class="dialog-content">
        <el-row>
          <el-col>
            <span>文件类型：</span
            ><span>{{
              ProjectFileTypeEnum.find((item) => item.value == fileRecordInfo.FileType).label
            }}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <span>文件名称：</span><span>{{ fileRecordInfo.FileName }}</span>
          </el-col>
          <el-col :span="12">
            <span>文件备注：</span><span>{{ fileRecordInfo.FileDetail }}</span>
          </el-col>
        </el-row>
        <p><span>文件列表：</span></p>
        <TableList
          class="file-list-table"
          :options="options"
          :columns="fileListColumns"
          :dataSource="dataSource"
          border
          v-loading="loading"
        >
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group">
                <el-button
                  v-if="!IsImgType(scope.row.FileType)"
                  type="text"
                  @click="handleDownloadClick(scope.row)"
                >
                  下载
                </el-button>
                <el-button
                  v-if="IsImgType(scope.row.FileType)"
                  type="text"
                  @click="handlePreviewClick(scope.row)"
                >
                  预览
                </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="close()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="图片预览"
      :visible.sync="previewDialogVisible"
      append-to-body
      class="previewDialog"
    >
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import _data from './data.js'
import { DownLoadFileByFileReader, DownLoadFileByCreateObjectURL } from '@/utils/util'
const { ProjectFileTypeEnum } = _data

export default {
  name: 'FileListDialog',
  props: {
    fileRecordInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      ProjectFileTypeEnum, // 合同类型
      fileListColumns: _data.fileListColumns(this), // 合同文件查看 表头
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 120 // 操作列宽度
        }
      },
      dataSource: [],

      previewDialogVisible: false,
      dialogImageUrl: ''
    }
  },
  mounted() {
    console.log(this.fileRecordInfo)
    this.getFileList()
  },
  methods: {
    // 获取文件列表
    getFileList() {
      this.loading = true
      const query = { id: this.fileRecordInfo.ProjectFileId }
      this.$api.project.GetGensetProjectFile(query).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.fileDetails = res.Data
          this.$set(this, 'dataSource', res.Data.FileUrlList)
        }
        this.loading = false
      })
    },
    IsImgType(type) {
      return ['png', 'jpg', 'jpge', 'gif'].includes(type)
    },
    close() {
      this.$emit('closeDialog')
    },
    // 下载点击
    handleDownloadClick(row) {
      const query = { id: row.ProjectFileUrlId }
      this.$api.project.DoDownloadGensetProjectFile(query).then((res) => {
        console.log(res)
        // DownLoadFileByFileReader(row.FileName, res.data)
        DownLoadFileByCreateObjectURL(res, row.FileName)
      })
    },
    // 预览点击
    handlePreviewClick(row) {
      this.previewDialogVisible = true
      this.dialogImageUrl = `${process.env.BASE_API}/${row.FileUrl}`
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  .el-row {
    line-height: 24px;
    margin-bottom: 16px;
  }
  p {
    margin-bottom: 8px;
  }
  .file-list-table {
    margin-bottom: 16px;
  }
}
</style>



