<!--
 * @Autor: linxu
 * @Date: 2022-03-04 16:45:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-04-29 14:45:03
 * @Description: 添加/编辑 开支弹窗
-->
<template>
  <div>
    <el-dialog
      :title="recordInfo ? '编辑开支' : '添加开支'"
      class="spending-addedit-dialog"
      width="600px"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <el-form :model="form" :rules="rules" ref="spendingForm" :label-width="formLabelWidth">
        <el-form-item label="开支类型:" prop="Type">
          <el-select v-model="form.Type" placeholder="请选择开支类型" @change="costTypeChange()">
            <!-- <el-option label="请选择" value=""></el-option> -->
            <el-option
              v-for="(item, index) in CostTypeEnum"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开支项目:" prop="Sort">
          <el-select v-model="form.Sort" placeholder="请选择开支项目">
            <!-- <el-option label="请选择" value=""></el-option> -->
            <el-option
              v-for="(item, index) in CostItemsEnum"
              :key="item.Sort"
              :label="item.SortName"
              :value="item.Sort"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="项目金额:" prop="Money">
          <el-input v-model="form.Money" placeholder="请输入项目金额" autocomplete="off"></el-input>
          <span class="unit">元</span>
        </el-form-item>
        <el-form-item label="开支说明:" prop="CostDetail">
          <el-input
            type="textarea"
            :rows="3"
            placeholder="字数不超过50个字"
            v-model="form.CostDetail"
            maxlength="50"
            show-word-limit
          >
          </el-input>
        </el-form-item>
        <el-form-item label="开支图片:" prop="fileList">
          <ImageUpload
            v-model="form.fileList"
            tip="（仅支持不超过5M的.jpg/jpge/png图片文件；最多上 传3张图片）"
            :limit="3"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('spendingForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('spendingForm')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- <el-dialog title="图片预览" :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog> -->
  </div>
</template>

<script>
import ImageUpload from '@/components/Upload/image'
import _data from './data.js'

export default {
  name: 'AddOrEditSpendingDialog',
  components: {
    ImageUpload
  },
  props: {
    ProjectId: {
      type: Number,
      default: null
    },
    recordInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      dialigVisible: true,
      formLabelWidth: '100px',
      form: {
        Type: '',
        Sort: '',
        Money: '',
        CostDetail: '',
        fileList: []
      },
      rules: {
        Type: [{ required: true, message: '请选择开支类型', trigger: 'change' }],
        Sort: [{ required: true, message: '请选择开支项目', trigger: 'change' }],
        Money: [
          { required: true, message: '请输入项目金额', trigger: 'blur' },
          {
            pattern: /^(0|[1-9][0-9]{0,7})([.][0-9]{1,2})?$/,
            message: '限制整数位不超过8位,小数点不超过2位',
            trigger: 'blur'
          }
        ],
        CostDetail: [{ required: true, message: '请输入开支说明', trigger: 'blur' }],
        fileList: [{ required: true, message: '请选择文件', trigger: 'change' }]
      },

      CostTypeEnum: _data.CostTypeEnum, // 开支类型
      CostItemsEnum: [], // 开支项目
      FileUrl: ''
    }
  },
  watch: {
    'form.fileList': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        this.FileUrl = newVal.map((item) => item.url).join(';')
        console.log(this.FileUrl)
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.GetGensetProjectCostType()
    // 编辑
    console.log(this.recordInfo)
    if (this.recordInfo) {
      this.form = Object.assign({}, this.recordInfo, {
        fileList: this.recordInfo.FileUrlArrays.map((item) => {
          return {
            url: item
          }
        })
      })
      console.log(this.form)
    }
  },
  methods: {
    // 查询开支项目
    GetGensetProjectCostType() {
      const params = {
        Type: this.form.Type || -1
      }
      this.$api.project.GetGensetProjectCostType(params).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.CostItemsEnum = res.Data
        }
      })
    },
    costTypeChange() {
      this.form.Sort = ''
      this.GetGensetProjectCostType()
    },
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            ...this.form,
            ProjectId: this.ProjectId,
            FileUrl: this.FileUrl
          }
          if (this.recordInfo) {
            const query = { id: this.recordInfo.ProjectCostId }
            this.editProjectCost(params, query)
            return
          }
          this.addProjectCost(params)
        } else {
          return false
        }
      })
    },

    // 添加开支
    addProjectCost(params) {
      this.$api.project.AddGensetProjectCost(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '添加用户成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '添加用户失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },

    // 编辑开支
    editProjectCost(params, query) {
      this.$api.project.EditGensetProjectCost(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '编辑用户成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '编辑用户失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.spending-addedit-dialog {
  .image-upload {
  }
}
</style>



