<!--
 * @Autor: linxu
 * @Date: 2022-03-03 16:17:06
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-21 17:27:52
 * @Description: 项目详情
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="project-detail">
      <!-- 项目机组 -->
      <div class="project-info-item">
        <p class="content-title">
          项目机组
          <el-button
            v-if="projectStatus !== 3"
            type="primary"
            class="comColor"
            @click="handleAddCrew()"
            >添加机组</el-button
          >
        </p>
        <div class="item-content crew-list">
          <TableList
            :options="options"
            :columns="crewColumns"
            :dataSource="gensetTableData"
            v-loading="loading_4"
          >
            <el-table-column slot="gensetConfig" label="机组配置情况 " width="280px" align="center">
              <template slot-scope="scope">
                <div class="config-list">
                  <!-- <div :class="scope.row.IsFenceOpen?'Completed':'noComplete'">
                    <el-button
                      type="text"
                      :disabled="projectStatus === 3"
                      @click="handleControllerConfiguration(scope.row, 0)"
                    >
                      <span class="text"> 数据标定{{scope.row.IsFenceOpen?'已开启':'未开启'}} <i class="el-icon-arrow-right"></i></span>
                    </el-button>
                  </div> -->
                  <div :class="scope.row.IsStrategy ? 'Completed' : 'noComplete'">
                    <el-button
                      type="text"
                      :disabled="projectStatus === 3"
                      @click="setStrategySwitch(scope.row, !scope.row.IsStrategy)"
                    >
                      <span class="text">
                        维保策略{{ scope.row.IsStrategy ? '已配置' : '未配置' }}
                        <i class="el-icon-arrow-right"></i
                      ></span>
                    </el-button>
                  </div>
                  <div :class="scope.row.IsFenceOpen ? 'Completed' : 'noComplete'">
                    <el-button
                      type="text"
                      :disabled="projectStatus === 3"
                      @click="setElectronicFenceSwitch(scope.row, !scope.row.IsFenceOpen)"
                    >
                      <span class="text">
                        电子围栏{{ scope.row.IsFenceOpen ? '已开启' : '未开启' }}
                        <i class="el-icon-arrow-right"></i
                      ></span>
                    </el-button>
                  </div>
                  <!-- <div :class="scope.row.IsFenceOpen?'Completed':'noComplete'">
                    <el-button
                      type="text"
                      :disabled="projectStatus === 3"
                      @click="handleControllerConfiguration(scope.row, 2)"
                    >
                      <span class="text"> 累计数据{{scope.row.IsFenceOpen?'已开启':'未开启'}} <i class="el-icon-arrow-right"></i></span>
                    </el-button>
                  </div> -->
                </div>
              </template>
            </el-table-column>
            <el-table-column
              slot="operation"
              label="操作"
              width="208px"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group" style="text-align: left">
                  <!-- <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleLimitCrew(scope.row)"
                  >
                    限功率
                  </el-button> -->
                  <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleControllerConfiguration(scope.row, 0)"
                  >
                    控制器配置
                  </el-button>
                  <el-button
                    type="text"
                    :disabled="scope.row.IsParallel || projectStatus === 3"
                    @click="handleDeleteCrew(scope.row)"
                  >
                    移除
                  </el-button>

                  <el-button type="text" @click="handleManageCrewProgress(scope.row)">
                    工程进度
                  </el-button>

                  <el-button
                    v-if="scope.row.IsFenceOpen && projectStatus !== 3"
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="setElectronicFenceInfo(scope.row, 'edit')"
                  >
                    修改围栏
                  </el-button>

                  <!-- <el-button
                    v-if="!scope.row.IsStrategy && projectStatus !== 3"
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleAddStrategy(scope.row)"
                  >
                    新增策略
                  </el-button> -->

                  <!-- <el-button
                    v-if="scope.row.IsStrategy && projectStatus !== 3"
                    type="text"
                    @click="handleShowStrategy(scope.row)"
                  >
                    查看策略
                  </el-button>

                  <el-button
                    v-if="scope.row.IsStrategy && projectStatus !== 3"
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleEditStrategy(scope.row)"
                  >
                    编辑策略
                  </el-button> -->
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
      </div>

      <!-- 项目并机组配置 -->
      <div class="project-info-item">
        <p class="content-title">
          并机组配置
          <el-button
            v-if="projectStatus !== 3"
            type="primary"
            class="comColor"
            @click="handleAddParallelCrew()"
          >
            新建并机
          </el-button>
        </p>
        <div class="item-content parallel-list">
          <TableList
            :options="options"
            :columns="parallelColumns"
            :dataSource="parallelTableData"
            v-loading="loading_5"
          >
            <el-table-column
              slot="operation"
              label="操作"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group">
                  <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleEditParallelCrew(scope.row)"
                  >
                    编辑
                  </el-button>
                  <el-button
                    type="text"
                    :disabled="projectStatus === 3"
                    @click="handleDeleteParallelCrew(scope.row)"
                  >
                    解除并机
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
      </div>
    </div>

    <!-- 添加机组弹窗 -->
    <AddCrewDialog
      v-if="crewManageVisible"
      :projectId="ProjectId"
      @closeDialog="(val) => closeDialog('crewManageVisible', val)"
    />

    <!-- 添加/编辑 并机组 弹窗 -->
    <AddOrEditParallelCrewDialog
      v-if="parallelCerwManageVisible"
      :projectId="ProjectId"
      :parallelCerwInfo="parallelCerwInfo"
      @closeDialog="(val) => closeDialog('parallelCerwManageVisible', val)"
    />

    <!-- 修改电子围栏弹窗 -->
    <SetElectronicFenceDialog
      v-if="electronicFenceDialogVisible"
      :setFenceType="setFenceType"
      :gensetId="gensetId"
      @closeDialog="(val) => closeCrewDialog('electronicFenceDialogVisible', val)"
    />

    <!-- 编辑策略弹窗 -->
    <StrategyEditDialog
      v-if="editStrategyDialogVisible"
      :setStrategyType="setStrategyType"
      :gensetId="gensetId"
      @closeDialog="(val) => closeCrewDialog('editStrategyDialogVisible', val)"
    />
    <!-- 查看策略弹窗 -->
    <StrategyShowDialog
      v-if="showStrategyDialogVisible"
      :gensetId="gensetId"
      @closeDialog="(val) => closeCrewDialog('showStrategyDialogVisible', val)"
    />

    <!-- 机组配置抽屉 -->
    <el-drawer
      title=""
      v-if="isExpand"
      :visible.sync="isExpand"
      :direction="direction"
      :with-header="false"
      :before-close="handleClose"
      class="genset-config-drawer"
    >
      <genset-info
        ref="gensetInfo"
        @handleClose="handleClose"
        :configData="configData"
      ></genset-info>
      <el-tabs v-model="activeName" class="genset-tabs">
        <el-tab-pane key="1" label="机组配置" name="1"></el-tab-pane>
      </el-tabs>
      <ControllerConfiguration :configData="configData"></ControllerConfiguration>
    </el-drawer>

    <!-- 单机工程进度抽屉 -->
    <el-drawer
      title=""
      v-if="isExpandProgress"
      :visible.sync="isExpandProgress"
      :direction="direction"
      :with-header="false"
      :before-close="handleCloseDrawer"
    >
      <genset-info
        ref="gensetInfo"
        @handleClose="handleCloseDrawer"
        :configData="configData"
      ></genset-info>
      <GensetProgress :configData="configData"></GensetProgress>
    </el-drawer>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AddCrewDialog from '../components/AddCrewDialog'
import AddOrEditParallelCrewDialog from '../components/AddOrEditParallelCrewDialog'
// 机组操作弹窗
import SetElectronicFenceDialog from './components/SetElectronicFenceDialog.vue'
import StrategyEditDialog from './components/StrategyEditDialog.vue'
import StrategyShowDialog from './components/StrategyShowDialog.vue'

import GensetInfo from './components/GensetInfo'
import ControllerConfiguration from '@/views/equipmentManage/gensetConfig/ControllerConfiguration.vue'
import GensetProgress from '../projectProgressManage/genset-progress.vue'
import _data from '../components/data.js'

export default {
  name: 'ProjectGensets',
  components: {
    AddCrewDialog,
    AddOrEditParallelCrewDialog,

    SetElectronicFenceDialog,
    StrategyEditDialog,
    StrategyShowDialog,

    GensetInfo,
    ControllerConfiguration,
    GensetProgress
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          // console.log(nv)
          this.$set(this, 'ProjectId', nv.ProjectId)
          this.$set(this, 'projectStatus', nv.Status)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading_4: false,
      loading_5: false,
      dialogEditVisible: false, // 项目编辑弹窗
      // 项目基本信息
      ProjectId: null,
      projectStatus: null, // 项目状态

      crewColumns: _data.crewColumns(this), // 项目机组表头 表头
      gensetTableData: [],
      crewManageVisible: false, // 添加机组弹窗

      parallelColumns: _data.parallelColumns(this), // 并机组配置 表头
      parallelTableData: [],
      parallelCerwManageVisible: false, // 新增/编辑 并机弹窗
      parallelCerwInfo: null,
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 120 // 操作列宽度
        }
      },

      // 机组操作弹窗
      gensetId: null, //机组ID
      electronicFenceDialogVisible: false, // 电子围栏弹窗
      setFenceType: null,
      editStrategyDialogVisible: false, //编辑策略弹窗
      setStrategyType: null,
      showStrategyDialogVisible: false, //查看策略弹窗

      // 机组配置抽屉
      isExpand: false,
      direction: 'rtl',
      activeName: '1',
      configData: {},

      // 单机工程进度抽屉
      isExpandProgress: false
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    this.projectStatus = this.currentProject.Status
    this.initData()
  },
  methods: {
    initData() {
      // 查询机组列表
      this.getProjectGensetList()
      // 查询并机组列表
      this.getProjectParallelList()
    },
    /*************** 项目机组 ***************/
    // 查询项目机组
    getProjectGensetList() {
      /** 分页查询项目机组列表 扩展数据 */
      this.loading_4 = true
      const query = {
        Page: 1,
        PageSize: 99
        // Page: this.paginationCfg.currentPage,
        // PageSize: this.paginationCfg.pageSize
      }
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectJoinGensetManageExpand(params, query).then((res) => {
        if (res.Code === 1) {
          // console.log('机组',res)
          this.dataTotal = res.Data.TotalItemCount
          this.gensetTableData = res.Data.Data.map((item) => {
            return {
              ID: item.ProjectJoinGensetId,
              IsParallel: item.IsParallel, // 是否并机
              IsFenceOpen: item.IsFenceOpen, // 是否开启电子围栏
              IsStrategy: item.IsStrategy, // 是否维保策略
              ...item.GeneratorSet
            }
          })
          // console.log(this.gensetTableData)
        }
        this.loading_4 = false
      })
    },
    // 添加机组
    handleAddCrew() {
      this.crewManageVisible = true
    },
    // 限功率
    handleLimitCrew(row) {},

    // 控制器配置
    handleControllerConfiguration(row, type) {
      this.configData = {
        activeTab: type == 0 ? 11 : 15,
        projectId: this.ProjectId,
        gensetId: row.GeneratorSetId,
        type: type
      }
      this.isExpand = true

      // this.$router.push({
      //   path: '/project/controller-configuration',
      //   query: {
      //     projectId: this.ProjectId,
      //     gensetId: row.GeneratorSetId
      //   }
      // })
    },
    // 移除机组
    handleDeleteCrew(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '移除机组',
        message: h('div', [
          h('p', [
            h('span', '确定从当前项目移除'),
            h('em', { style: 'color: #1ABC9C;margin: 0 4px;' }, row.CollectorToken),
            h('span', '吗？')
          ])
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.ID }
          this.$api.project.DeleteGensetProjectJoinGenset(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '移除机组成功!'
              })
              this.getProjectGensetList()
            } else {
              this.$message({
                type: 'error',
                message: '移除机组失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 设置电子围栏
    setElectronicFenceSwitch(row, value) {
      if (value) {
        this.setElectronicFenceInfo(row, 'add')
        return
      }
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '关闭电子围栏',
        message: h('div', [h('p', [h('span', '确定关闭当前机组电子围栏吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.closeFenceClick(row)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    closeFenceClick(row) {
      // 关闭窗口，显示遮罩
      const params = {
        IsOpen: false
      }
      const query = { id: row.GeneratorSetId }
      this.$api.project
        .GensetFenceSetting(params, query)
        .then((res) => {
          this.$message.success(res.Message)
          row.IsFenceOpen = false
        })
        .catch((error) => {
          this.$message.error(error)
        })
    },
    // 修改围栏
    setElectronicFenceInfo(row, type) {
      this.setFenceType = type
      this.gensetId = row.GeneratorSetId
      this.electronicFenceDialogVisible = true
    },
    // 维保策略开关
    setStrategySwitch(row, value) {
      this.handleAddStrategy(row)
      // if (value) {
      //   this.handleAddStrategy(row)
      //   return
      // }
      // const h = this.$createElement
      // this.$confirm('操作提示', {
      //   title: '关闭维保策略',
      //   message: h('div', [h('p', [h('span', '确定关闭当前机组维保策略吗？')])]),
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   type: 'warning'
      // })
      //   .then(() => {
      //     this.handleCloseStrategy(row)
      //   })
      //   .catch(() => {
      //     this.$message({
      //       type: 'info',
      //       message: '已取消'
      //     })
      //   })
    },
    // 策略配置
    handleAddStrategy(row) {
      // this.setStrategyType = 'add'
      // this.gensetId = row.GeneratorSetId
      // this.editStrategyDialogVisible = true
      this.configData = {
        activeTab: 14,
        projectId: this.ProjectId,
        gensetId: row.GeneratorSetId,
        type: 1
      }
      this.isExpand = true
    },

    // 编辑策略
    handleEditStrategy(row) {
      // this.setStrategyType = 'edit'
      // this.gensetId = row.GeneratorSetId
      // this.editStrategyDialogVisible = true
      this.configData = {
        activeTab: 14,
        projectId: this.ProjectId,
        gensetId: row.GeneratorSetId,
        type: 1
      }
      this.isExpand = true
    },

    // 关闭维保策略
    handleCloseStrategy(row) {
      const query = { GensetId: row.GeneratorSetId }
      this.$api.project
        .CloseMaintenanceGensetSetting({}, query)
        .then((res) => {
          this.$message.success(res.Message)
          row.IsStrategy = false
        })
        .catch((error) => {
          this.$message.error(error)
        })
    },

    // 查看策略
    handleShowStrategy(row) {
      this.gensetId = row.GeneratorSetId
      this.showStrategyDialogVisible = true
    },

    handleClose(done) {
      // done()
      this.isExpand = false
      this.initData()
    },

    // 工程进度
    handleManageCrewProgress(row) {
      this.configData = {
        projectId: this.ProjectId,
        gensetId: row.GeneratorSetId
      }
      this.isExpandProgress = true
      // this.$router.push({
      //   path: '/project/genset-progress-manage',
      //   query: {
      //     ProjectId: this.ProjectId,
      //     GensetId: row.GeneratorSetId
      //   }
      // })
    },
    handleCloseDrawer() {
      this.isExpandProgress = false
    },
    // 弹框组件关闭
    closeCrewDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.getProjectGensetList()
      }
    },

    /*************** 并机组配置 ***************/
    getProjectParallelList() {
      this.loading_5 = true
      const query = {
        Page: 1,
        PageSize: 99
      }
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectParallelManage(params, query).then((res) => {
        if (res.Code === 1) {
          // console.log('并机组',res)
          this.parallelTableData = res.Data.Data.map((item) => {
            return {
              ID: item.ProjectParallelId,
              ParallelName: item.ParallelName,
              // CollectorToken 标识  DisplayName 名称
              ParallelJoinGensetIsMain: item.ParallelJoinGensetIsMain.map(
                (item) => item.Genset.DisplayName
              ).join(),
              ParallelJoinGensetIsNotMain: item.ParallelJoinGensetIsNotMain.map(
                (item) => item.Genset.DisplayName
              ).join(),
              GensetTotal:
                item.ParallelJoinGensetIsMain.length + item.ParallelJoinGensetIsNotMain.length,

              selectHostIds: item.ParallelJoinGensetIsMain.map(
                (item) => item.Genset.GeneratorSetId
              ),
              selectExtensionIds: item.ParallelJoinGensetIsNotMain.map(
                (item) => item.Genset.GeneratorSetId
              )
            }
          })
          // console.log(this.parallelTableData)
        }
        this.loading_5 = false
      })
    },
    // 新建并机
    handleAddParallelCrew() {
      this.parallelCerwInfo = null
      this.parallelCerwManageVisible = true
    },
    // 编辑
    handleEditParallelCrew(row) {
      this.parallelCerwInfo = row
      this.parallelCerwManageVisible = true
    },
    // 解除并机
    handleDeleteParallelCrew(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '解除并机',
        message: h('div', [
          h('p', '确定解除当前并机吗？'),
          h('p', [
            h('span', '解除后，相关并机将变更为'),
            h('em', { style: 'color: #1ABC9C;margin: 0 4px;' }, '单机组'),
            h('span', '状态，仍存在于本项目。')
          ])
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.ID }
          this.$api.project.DeleteGensetProjectParallel(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '解除并机成功!'
              })
              this.getProjectParallelList()
              this.getProjectGensetList()
            } else {
              this.$message({
                type: 'error',
                message: '解除并机失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 返回项目列表
    handleBackClick() {
      this.$router.push({ path: '/project/project-list' })
    },

    closeDialog(key, val) {
      // console.log(key, val)
      this[key] = false
      if (val && val.refresh) {
        switch (key) {
          case 'dialogEditVisible': // 项目基本信息
            this.getProjectBasicInfo()
            break
          case 'fileUploadVisible': // 上传文件
            this.getProjectFileList()
            break
          case 'personnelManageVisible':
            this.getProjectUserList()
            break
          case 'crewManageVisible': // 项目机组
            this.getProjectGensetList()
            break
          case 'parallelCerwManageVisible': // 项目并机组
            this.getProjectParallelList()
            this.getProjectGensetList()
            break
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.project-detail {
  display: initial !important;
  .project-info-item {
    // height: 500px;
    .content-title {
      height: 48px;
      line-height: 48px;
      .comColor {
        float: right;
        margin: 8px 16px;
      }
    }

    .item-content {
      margin: 0 0 32px 0px;
    }
  }
  .config-list {
    display: flex;
    flex-wrap: wrap;
    /deep/ & > div {
      width: 50%;
    }
    .noComplete {
      .text {
        color: #ec808d !important;
      }
    }
  }
}
.el-drawer__wrapper {
  /deep/ .el-drawer.rtl {
    width: calc(100% - 180px) !important;
    min-width: 1220px;
    background: @contentBg;
    padding: 24px;
  }
  &.genset-config-drawer {
    /deep/ .el-drawer__body {
      display: flex;
      flex-direction: column;
    }
  }
}
</style>



