<!--
 * @Autor: linxu
 * @Date: 2022-03-19 14:22:48
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 13:49:59
 * @Description: 机组关联弹窗
-->
<template>
  <div>
    <el-dialog
      :title="title"
      class="progress-records-dialog"
      width="60%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
      @close="close()"
    >
      <div class="dialog-content">
        <TableList
          class="genset-list-table"
          :options="options"
          :columns="columns"
          :dataSource="gensetTableData"
          :defaultSelectData="selectList"
          @handleSelectionChange="handleSelectionChange"
          border
          v-loading="loading"
        >
          <!-- <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
            v-if="OperateType == 2"
          >
            <template slot-scope="scope">
              <div class="operate-group">
                <el-button
                  v-if="!scope.row.flag"
                  type="text"
                  :disabled="projectStatus === 3"
                  @click="handleRelated(scope.row)"
                >
                  关联
                </el-button>
                <el-button
                  v-else
                  type="text"
                  :disabled="projectStatus === 3"
                  @click="handleCancelRelated(scope.row)"
                >
                  取消关联
                </el-button>
              </div>
            </template>
          </el-table-column> -->
        </TableList>

        <!-- 操作按钮 -->
        <!-- <div class="operation-box" v-if="OperateType == 2">
          <el-button type="primary" :disabled="projectStatus === 3" @click="handleBatchRelated()"
            >批量关联</el-button
          >
          <el-button
            type="primary"
            :disabled="projectStatus === 3"
            @click="handleBatchCancelRelated()"
            >批量取消关联</el-button
          >
        </div> -->
      </div>
      <div slot="footer" class="dialog-footer">
        <p class="select-text SingleLineHiding">
          <em>当前已选择：</em> <span :title="selectText">{{ selectText }}</span>
        </p>
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="nextStep()" v-if="OperateType == 1">下一步</el-button>
        <el-button
          type="primary"
          v-if="OperateType == 2 && projectStatus !== 3"
          :disabled="!(this.selectList && this.selectList.length > 0)"
          @click="handleBatchRelated()"
          >保存</el-button
        >
      </div>
    </el-dialog>

    <!-- 记录表单弹窗 -->
    <RecordFormDialog
      v-if="RecordFormDialogVisible"
      :recordParmas="recordParmas"
      :RecordId="RecordId"
      :OperateType="OperateType"
      :GensetList="selectList"
      @closeDialog="(val) => closeDialog('RecordFormDialogVisible', val)"
    />
  </div>
</template>

<script>
import RecordFormDialog from './RecordFormDialog.vue'
import _data from './data.js'

export default {
  name: 'GensetProgressRecordsDialog',
  props: {
    recordParmas: {
      type: Object,
      default: null
    },
    RecordId: {
      type: Number,
      default: null
    },
    OperateType: {
      type: Number,
      default: null
    } // 操作类型 1--添加选择机组 2--编辑关联
  },
  components: {
    RecordFormDialog
  },
  watch: {
    recordParmas: {
      handler: function (newVal, oldVal) {
        console.log('recordParmas', newVal)
        this.projectStatus = newVal.projectStatus // 项目状态
        this.ProjectId = newVal.ProjectId
        this.ProjectProgressId = newVal.ProjectProgressId
        this.IsGensetMult = newVal.IsGensetMult
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      title: '第一步：关联机组',

      projectStatus: null,
      ProjectId: null,
      ProjectProgressId: null,
      IsGensetMult: null, // 是否多选

      columns: _data.GensetRalatedColumns(this),
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        selection: true,
        props: {
          width: 80 // 操作列宽度
        }
      },
      gensetTableData: [],
      selectList: [], // 已选择机组
      RecordFormDialogVisible: false // 记录表单弹窗
    }
  },
  computed: {
    selectText() {
      return this.selectList.map((item) => item.CollectorToken).join(' 、 ')
    },
    GensetList() {
      return this.selectList.map((item) => {
        return { GeneratorSetId: item.GeneratorSetId }
      })
    }
  },
  mounted() {
    console.log(this.recordParmas)
    if (this.OperateType == 1) {
      this.title = '第一步：关联机组'
    } else {
      this.title = '关联机组'
    }
    this.queryTableData()
  },
  methods: {
    // 编辑--查询已关联机组
    async querySelectList() {
      const query = {
        RecordId: this.RecordId
      }
      const list = await this.$api.project
        .GetGensetProjectProgressRecordDetail({}, query)
        .then((res) => {
          if (res.Code === 1) {
            const data = res.Data
            return data.GensetList
              ? data.GensetList.map((item) => {
                  return {
                    ...item.GeneratorSet
                  }
                })
              : []
          }
        })
      this.$set(this, 'selectList', list)
    },
    queryTableData() {
      this.loading = true
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project
        .GetGensetProjectJoinGensetByAll(params)
        .then((res) => {
          if (res.Code === 1) {
            this.gensetTableData = res.Data.map((item) => {
              return {
                ID: item.ProjectJoinGensetId,
                IsParallel: item.IsParallel,
                ...item.GeneratorSet
              }
            })
            if (this.OperateType == 2) {
              this.querySelectList()
            }
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 批量关联
    handleBatchRelated(row) {
      const query = {
        id: this.RecordId
      }
      const params = {
        GensetList: this.GensetList
      }
      this.$api.project.EditGensetProjectProgressRecordGenset(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message.success(res.Message)
          this.$emit('closeDialog', { refresh: true })
        }
      })
    },

    // 获取选中行
    handleSelectionChange(val) {
      this.selectList = val
    },
    // 下一步
    nextStep() {
      if (this.selectList.length == 0) {
        this.$message.warning('请选择机组！')
        return
      }
      if (this.selectList.length > 1 && this.IsGensetMult == 'false') {
        this.$message.warning('请选择一台机组！')
        return
      }
      this.RecordFormDialogVisible = true
    },
    close() {
      this.$emit('closeDialog')
    },

    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.$emit('closeDialog', { refresh: true })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.progress-records-dialog {
  /deep/.el-dialog {
    // min-height: 400px;
    .el-dialog__body {
      padding: 25px;
    }
  }
  .operation-box {
    margin-top: 16px;
  }
  .select-text {
    float: left;
    line-height: 32px;
    width: 75%;
    text-align: left;
    span {
      color: #409eff;
      margin-left: 8px;
    }
  }
}
.theme-dark .progress-records-dialog {
  .select-text em {
    color: #fff;
  }
}
</style>



