<!--
 * @Autor: linxu
 * @Date: 2022-03-04 10:10:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-06-06 10:58:26
 * @Description: 添加机组 弹窗
-->
<template>
  <el-dialog
    title="添加机组"
    class="add-crew-dialog"
    width="90%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <div class="dialog-content">
      <div class="left-box">
        <p class="title">全部可选机组</p>
        <div class="search-box">
          <div class="search-item">
            <span>机组位置:</span>
            <!-- 省市县级联选择器 -->
            <position-cascader
              placeholder="请选择机组位置"
              :defaultPosition="searchForm.InstallationSite"
              @positionChange="positionChange"
            />
          </div>
          <div class="search-item">
            <span>发动机型号:</span>
            <el-input
              placeholder="请输入发动机型号"
              maxlength="20"
              v-model="searchForm.EngineModel"
            >
            </el-input>
          </div>
          <div class="search-item power-item">
            <span>功率:</span>
            <el-input type="number" placeholder="最低功率" v-model="searchForm.RatedPowerMin">
            </el-input>
            <span>-</span>
            <el-input type="number" placeholder="最高功率" v-model="searchForm.RatedPowerMax">
            </el-input>
          </div>
          <div class="search-btn-box">
            <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
            <el-button type="primary" class="query-btn" @click="queryTableData()">
              查 询
            </el-button>
          </div>
        </div>
        <!-- 可选表格 -->
        <TableList
          :options="options"
          :columns="columns"
          :dataSource="searchData"
          :dataTotal="dataTotal"
          border
          v-loading="loading"
          :pagination="paginationCfg"
          @handleChangePage="handleChangePage"
          @handleChangeSize="handleChangeSize"
        >
          <!-- -->
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button
                  :disabled="scope.row.isSelect"
                  type="text"
                  @click="handleSelect(scope.row)"
                >
                  选中
                </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
      <div class="right-box">
        <p class="title">已选择机组</p>
        <!-- 已选表格 -->
        <TableList :options="options" :columns="columns" :dataSource="selectData" border>
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button type="text" @click="handleRemove(scope.row)"> 移除 </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="close()">取 消</el-button>
      <el-button type="primary" @click="submitForm()">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import PositionCascader from '@/components/PositionCascader'
import _data from './data.js'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'

export default {
  name: 'AddCrewDialog',
  mixins: [paginationConfigMixin],
  components: {
    PositionCascader
  },
  props: {
    projectId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      // 筛选条件
      searchForm: {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      },

      // 表格
      columns: _data.selectCrewColumns(this),
      dataSource: [], // 全部可选 数据源
      selectData: [], // 选中 数据源
      options: {
        index: true, // 序号列
        labelIndex: '序号',
        props: {
          width: 60 // 操作列宽度
        }
      },
      searchData: [],

      // 分页
      dataTotal: 0,
      paginationCfg: {
        currentPage: 1,
        pageSize: 10,
        layout: 'prev, pager, next,jumper',
        pageSizes: [10, 20, 30, 40],
        pagerCount: 5
      }
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.loading = true
      // console.log('searchForm------', this.searchForm)
      const params = { ...this.searchForm }
      this.$api.project
        .GetGensetProjectGensetInfoByAll(params)
        .then((res) => {
          if (res.Code === 1) {
            console.log(res)
            const data = res.Data.map((item) => {
              return {
                ...item.GeneratorSet
              }
            })
            this.$set(this, 'dataSource', data)
            this.dataTotal = res.Data.length
            this.loadData()
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    loadData() {
      console.log('loadData', this.dataSource, this.paginationCfg)
      const data = this.dataSource.slice(
        this.paginationCfg.pageSize * (this.paginationCfg.currentPage - 1),
        this.paginationCfg.pageSize * this.paginationCfg.currentPage
      )
      this.$set(this, 'searchData', data)
      console.log(this.searchData)
    },
    reset() {
      this.$set(this, 'searchForm', {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      })
    },
    // 选中
    handleSelect(row) {
      this.selectData.push(row)
      this.dataSource.splice(this.dataSource.indexOf(row), 1, { ...row, isSelect: true })
      this.$set(this, 'dataSource', this.dataSource)

      this.searchData.splice(this.searchData.indexOf(row), 1, { ...row, isSelect: true })
      this.$set(this, 'searchData', this.searchData)
    },
    // 移除
    handleRemove(row) {
      this.selectData.splice(this.selectData.indexOf(row), 1)
      this.dataSource.splice(
        this.dataSource.findIndex((item) => item.GeneratorSetId == row.GeneratorSetId),
        1,
        { ...row, isSelect: false }
      )
      this.searchData.splice(
        this.searchData.findIndex((item) => item.GeneratorSetId == row.GeneratorSetId),
        1,
        { ...row, isSelect: false }
      )
    },

    // 表单提交
    submitForm() {
      // console.log('save')
      const params = {
        ProjectId: this.projectId,
        JoinGensetList: this.selectData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        })
      }
      this.$api.project.AddGensetProjectJoinGenset(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '添加机组成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '添加机组失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    close() {
      this.$emit('closeDialog')
    },

    // 分页 当前页改变
    handleChangePage(val) {
      this.paginationCfg.currentPage = val
      this.$set(this.paginationCfg, 'currentPage', val)
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.loadData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      console.log(111111111)
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      this.$set(this.paginationCfg, 'currentPage', 1)
      this.$set(this.paginationCfg, 'pageSize', val)
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.loadData()
    },

    // 筛选条件--级联选择器change事件
    positionChange(data) {
      // console.log(data)
      this.searchForm.InstallationSite = data.crewPosition
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  display: flex;
  justify-content: space-between;
  .left-box,
  .right-box {
    width: calc(50% - 8px);
    border: 1px solid #e4e4e4;
    > div {
      margin: 8px 16px;
    }
    .title {
      line-height: 40px;
      font-weight: 700;
      border-bottom: 1px solid #e4e4e4;
      text-indent: 8px;
    }
  }

  .search-box .search-item {
    width: 50%;
    &.power-item {
      width: 60%;
    }
  }
}
</style>



