<!--
 * @Autor: linxu
 * @Date: 2022-03-19 13:46:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 13:41:08
 * @Description: 项目工程进度记录
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="project-progress-records">
      <!-- 操作按钮 -->
      <!-- <div class="operation-box">
        <el-button type="primary" @click="addProgressRecord()">添 加</el-button>
      </div> -->
      <p class="content-title">
        项目工程进度记录
        <el-button
          type="primary"
          class="comColor"
          :disabled="projectStatus === 3"
          @click="addProgressRecord()"
          >添 加</el-button
        >
      </p>
      <TableList
        class="record-list-table"
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        border
        v-loading="loading"
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group">
              <el-button type="text" @click="showProgressRecord(scope.row)"> 查看 </el-button>
              <!-- <el-button type="text" @click="handleDownloadClick(scope.row)"> 下载 </el-button> -->
              <el-button
                type="text"
                :disabled="projectStatus === 3"
                @click="deleteProgressRecord(scope.row)"
              >
                删除
              </el-button>
              <el-button type="text" v-if="IsGenset == true" @click="relatedGenset(scope.row)">
                关联机组
              </el-button>
            </div>
          </template>
        </el-table-column>
      </TableList>

      <!-- <p class="content-title">单机工程进度记录</p>
      <TableList
        class="file-list-table"
        :options="options"
        :columns="gensetRecord"
        :dataSource="data"
        border
        v-loading="loading_1"
      >
        <el-table-column slot="operation" label="操作" align="center" width="120">
          <template slot-scope="scope">
            <div class="operate-group">
              <el-button
                :disabled="scope.row.RecordCount == 0"
                type="text"
                @click="showGensetProgressRecord(scope.row)"
              >
                查看
              </el-button>
            </div>
          </template>
        </el-table-column>
      </TableList> -->
    </div>
    <!-- 关联机组弹窗 -->
    <GensetRelatedDialog
      v-if="GensetRelatedDialogVisible"
      :recordParmas="recordParmas"
      :ProjectProgressId="ProjectProgressId"
      :RecordId="RecordId"
      :OperateType="OperateType"
      @closeDialog="(val) => closeDialog('GensetRelatedDialogVisible', val)"
    />
    <!-- 记录表单弹窗 -->
    <RecordFormDialog
      v-if="RecordFormDialogVisible"
      :recordParmas="recordParmas"
      :RecordId="RecordId"
      :OperateType="OperateType"
      @closeDialog="(val) => closeDialog('RecordFormDialogVisible', val)"
    />

    <!-- 单机 工程进度记录表 -->
    <GensetProgressRecordsDialog
      v-if="RecordsDialogVisible"
      :ProjectProgressId="ProjectProgressId"
      :GensetId="GensetId"
      @closeDialog="(val) => closeDialog('RecordsDialogVisible', val)"
    />
  </div>
</template>

<script>
import GensetRelatedDialog from './components/GensetRelatedDialog.vue'
import RecordFormDialog from './components/RecordFormDialog.vue'
import GensetProgressRecordsDialog from './components/GensetProgressRecordsDialog.vue'
import _data from './components/data.js'

export default {
  name: 'ProjectProgressRecords',
  props: {
    recordParmas: {
      type: Object,
      default: null
    }
  },
  components: {
    GensetRelatedDialog,
    RecordFormDialog,
    GensetProgressRecordsDialog
  },
  data() {
    return {
      loading: false,
      loading_1: false,
      dialigVisible: true,

      projectStatus: null, // 项目状态
      ProjectId: null,
      ProjectProgressId: null,
      IsGenset: null,
      IsGensetMult: null,
      GensetId: null,

      columns: _data.projectProgressRecordsColumns(this), //项目工程进度记录
      gensetRecord: _data.progressRecordsColumns(this), // 单机工程进度记录
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 240 // 操作列宽度
        }
      },
      dataSource: [],
      data: [],

      GensetRelatedDialogVisible: false, // 关联机组弹窗

      RecordFormDialogVisible: false, // 记录表单弹窗
      OperateType: null, // 操作类型 1--项目添加 2--项目查看 3--单机查看
      RecordId: null, // 记录ID

      RecordsDialogVisible: false // 单机 工程进度记录弹窗
    }
  },
  watch: {
    recordParmas: {
      handler: function (newVal, oldVal) {
        console.log('recordParmas', newVal)
        this.projectStatus = newVal.projectStatus // 项目状态
        this.ProjectId = newVal.ProjectId
        this.ProjectProgressId = newVal.ProjectProgressId
        this.IsGenset = newVal.IsGenset
        this.IsGensetMult = newVal.IsGensetMult
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.queryTableData()
    // this.queryGensetTableData()
  },
  methods: {
    // 查询项目进度记录
    queryTableData() {
      this.loading = true
      const params = {
        // ...this.searchForm,
        ProjectId: this.ProjectId,
        ProgressId: this.ProjectProgressId
      }
      this.$api.project
        .GetGensetProjectProgressRecord(params)
        .then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$set(this, 'dataSource', res.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 查询单机记录
    queryGensetTableData() {
      this.loading_1 = true
      const params = {
        ProjectId: this.ProjectId,
        ProgressId: this.ProjectProgressId
      }
      this.$api.project.GetGensetProjectProgressGensetByAll(params).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          const data = res.Data.map((item) => {
            return {
              IsParallel: item.IsParallel,
              ProgressName: item.ProgressName,
              ProjectJoinGensetId: item.ProjectJoinGensetId,
              RecordCount: item.RecordCount,
              Type: item.Type,
              ...item.GeneratorSet
            }
          })
          this.$set(this, 'data', data)
        }
        this.loading_1 = false
      })
    },
    close() {
      this.$emit('closeDialog')
    },

    // 添加记录
    addProgressRecord() {
      this.RecordId = null
      this.OperateType = 1
      if (this.IsGenset == true) {
        // 绑定机组
        this.GensetRelatedDialogVisible = true
      } else {
        // 不绑定机组
        this.RecordFormDialogVisible = true
      }
    },

    // 查看记录
    showProgressRecord(row) {
      this.RecordId = row.RecordId
      this.OperateType = 2
      this.RecordFormDialogVisible = true
    },

    // 删除
    deleteProgressRecord(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '删除记录',
        message: h('div', [h('p', [h('span', '确定删除当前记录吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.RecordId }
          this.$api.project.DeleteGensetProjectProgressRecord(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '删除记录成功!'
              })
              this.queryTableData()
            } else {
              this.$message({
                type: 'error',
                message: '删除记录失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 下载
    handleDownloadClick(row) {
      console.log(row)
    },

    // 关联机组
    relatedGenset(row) {
      this.RecordId = row.RecordId
      this.OperateType = 2
      // this.RecordFormDialogVisible = true
      this.GensetRelatedDialogVisible = true
    },

    // 单机 查看
    showGensetProgressRecord(row) {
      console.log(row)
      this.GensetId = row.GeneratorSetId
      this.RecordsDialogVisible = true
    },
    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    },

    handleBackClick() {
      this.$router.push({ path: '/project/project-detail', query: { active: 4 } })
    }
  }
}
</script>

<style lang="less" scoped>
.project-progress-records {
  display: initial !important;
  .content-title {
    height: 48px;
    line-height: 48px;
    .comColor {
      float: right;
      margin: 8px 16px;
    }
  }
  .record-list-table {
    margin-bottom: 32px;
  }
}
</style>



