<!-- 
 * @Autor: linxu
 * @Date: 2022-06-29 09:34:48
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 10:16:25
 * @Description:  机组基本信息
-->
<template>
  <div class="genset-info">
    <el-row type="flex">
      <el-col class="genset-info-items genset-info-basic" :span="12">
        <span class="genset-info-item genset-name">{{ GensetInfo && GensetInfo.DisplayName }}</span>
        <div class="genset-info-item" v-if="GensetInfo && GensetInfo.Status != 0">
          <el-image
            v-if="GensetInfo && GensetInfo.NetworkType == 2"
            style="width: 20px; height: 20px"
            :src="url"
          ></el-image>
          <el-image
            v-else-if="GensetInfo && GensetInfo.NetworkType == 1"
            style="width: 20px; height: 20px"
            :src="getUrl(`${GensetInfo.NetworkValue}`)"
          ></el-image>
        </div>
        <el-tag
          class="genset-info-item genset-tag"
          :style="{
            background: showByCrewStatus(GensetInfo && GensetInfo.Status).color,
            color:
              GensetInfo && (GensetInfo.Status == 1 || GensetInfo.Status == 2) ? '#000' : '#fff'
          }"
          >{{ showByCrewStatus(GensetInfo && GensetInfo.Status).text }}
        </el-tag>
        <el-tag class="genset-info-item genset-tag genset-altitude">
          海拔{{ (GensetInfo && GensetInfo.Altitude) || '-' }}m
        </el-tag>
      </el-col>
      <el-col class="genset-info-items operate-btns" :span="12">
        <i
          class="genset-info-item el-icon-close"
          title="关闭"
          @click="() => this.$emit('handleClose')"
        ></i>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col class="genset-info-items genset-info-more">
        <span>机组编号：{{ GensetInfo && GensetInfo.CollectorToken }}</span>
        <span>发动机型号：{{ (GensetInfo && GensetInfo.EngineModel) || '-' }}</span>
        <span>功率(KW)：{{ (GensetInfo && GensetInfo.RatedPower) || '-' }}</span>
        <span>累计运行时长：{{ (GensetInfo && GensetInfo.TotalRunTime) || '-' }}</span>
        <span>当前位置：{{ (GensetInfo && GensetInfo.InstallationSite) || '-' }}</span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { showByCrewStatus } from '@/utils/common'

export default {
  name: 'GensetInfo',
  inject: ['reload'], // 注入App中注册的变量
  props: {
    configData: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      GensetInfo: null, // 机组数据
      showByCrewStatus,
      url: require('@/assets/img/crewmanage/ethernet_1.png'),
      isShowDrawer: false,
      direction: 'rtl',
      activeName: '1'
    }
  },
  watch: {},
  mounted() {
    this.getGeneratorSet()
  },
  methods: {
    getGeneratorSet() {
      // 获取设备信息
      this.$api.operation.FindGeneratorSet({ id: this.configData.gensetId }).then((res) => {
        if (res.Code == 1) {
          this.GensetInfo = res.Data
        }
      })
    },
    // 获取机组状态对应的信号图片资源
    getUrl(value) {
      return require(`@/assets/img/crewmanage/signal_${value}.png`)
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.genset-info {
  > .el-row {
    margin-bottom: 15px;
    &:first-child {
      height: 24px;
    }
  }
  .genset-info-items {
    display: flex;
    align-items: center;
    .el-tag {
      height: 24px;
      line-height: 22px;
      border: 0;
    }
    .genset-info-item {
      &:not(:last-child) {
        margin-right: 12px;
      }
      &.genset-tag {
        font-size: 1rem;
      }
      &.genset-temperature {
        background-color: @theme;
        color: #fff;
      }
      &.genset-windage {
        background-color: rgba(2, 167, 240, 1);
        color: #fff;
      }
      &.genset-altitude {
        background-color: rgba(215, 215, 215, 1);
        color: #000;
      }
    }
    &.genset-info-basic {
      .genset-name {
        font-size: 1.3rem;
        font-weight: 700;
      }
    }
    &.operate-btns {
      justify-content: flex-end;
      .genset-info-item {
        margin: 0 0 0 8px;
        font-size: 18px;
        cursor: pointer;
        &.back-btn {
          width: 46px;
          span {
            font-size: 14px;
            position: absolute;
            margin-top: 2px;
            white-space: nowrap;
          }
        }
      }
    }
    &.genset-info-more {
      font-size: 0.8rem;
      > span {
        margin-right: 20px;
      }
    }
  }
}

.toggle-crew-list {
  height: calc(100% - 32px);
  margin: 16px 0 16px 16px;
  box-sizing: border-box;
  border: 1px solid #e4e4e4;

  display: flex;
  flex-direction: column;
  .content-title {
    margin-bottom: 0;
    padding-right: 16px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    i {
      cursor: pointer;
    }
    > i {
      float: right;
      margin-top: 13px;
    }
  }
  .crew-status {
    padding: 8px 0;
    .el-col {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      line-height: 20px;
      border-bottom: 1px solid transparent;
      span {
        width: 20px;
        height: 20px;
        margin-bottom: 4px;
        cursor: pointer;
      }
      &.active {
        border-color: @theme !important;
      }
    }
  }

  .search-container {
    margin: 10px 2px;
    /deep/ .input-with-select {
      .el-input-group__append {
        overflow: hidden;
        background-color: transparent;
      }
      .search-btn {
        border-radius: 0 4px 4px 0;
      }
    }
  }

  .genset-container {
    flex: 1;
    height: 0;
    display: flex;
    flex-direction: column;
    .genset-list {
      flex: 1;
      height: 0;
      overflow: auto;
    }
    /deep/.item-box {
      padding: 5px;
      border-bottom: 1px solid #e4e4e4;
      cursor: pointer;
      &:first-child {
        border-top: 1px solid #e4e4e4;
      }
      &.active {
        background-color: @theme; //  #46ae5a
        border-color: @theme;
      }
      > div {
        display: flex;
        justify-content: space-between;
        height: 20px;
        line-height: 20px;
      }
      > div:first-child {
        margin-bottom: 4px;
        .color_type {
          span {
            width: 20px;
            height: 20px;
            display: inline-block;
            margin: 0 6px;
          }
        }
        .name-text {
          flex: 1;
          margin: 0 8px;
        }
      }
      > div:last-child {
        span.status-text {
          width: 32px;
        }
        span.address-text {
          flex: 1;
          margin: 0 8px;
        }
      }
    }
    p.no-data {
      line-height: 60px;
      text-align: center;
    }
    .pagination {
      text-align: center;
      margin-top: 16px;
      padding: 8px 1px 16px;
      white-space: normal;
      .el-pagination .el-pager li {
        margin: 2px 4px !important;
      }
      /deep/.el-pagination__editor.el-input {
        .el-input__inner {
          height: 28px !important;
        }
      }
    }
  }

  // 机组状态对应标签颜色设置
  // 运行
  .color_type_4 {
    span {
      background: @crew_status_4;
    }
  }
  // 报警
  .color_type_2 {
    span {
      background: @crew_status_2;
    }
  }
  // 警告
  .color_type_3 {
    span {
      background: @crew_status_3;
    }
  }
  // 停机
  .color_type_5 {
    span {
      background: @crew_status_5;
    }
  }
  // 离线
  .color_type_6 {
    span {
      background: @crew_status_6;
    }
  }
  // 全部
  .color_type_0 {
    span {
      background: @crew_status_0;
    }
  }
}
.theme-dark {
  // .toggle-crew-list,
  .toggle-crew-list .genset-container .item-box {
    border-color: hsla(0, 0%, 100%, 0.3);
  }
  .el-drawer__wrapper {
    /deep/.el-drawer {
      background-color: @menuBg;
    }
  }
}
</style>



