<!--
 * @Autor: linxu
 * @Date: 2022-03-04 10:10:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-04 14:53:36
 * @Description: 添加/编辑 并机组 弹窗
-->
<template>
  <el-dialog
    :title="parallelCerwInfo ? '编辑并机组' : '添加并机组'"
    class="add-crew-dialog"
    width="90%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <div class="dialog-content">
      <div class="left-box">
        <p class="title">当前项目全部机组</p>
        <div class="search-box">
          <div class="search-item">
            <span>机组位置:</span>
            <!-- 省市县级联选择器 -->
            <position-cascader
              placeholder="请选择机组位置"
              :defaultPosition="searchForm.InstallationSite"
              @positionChange="positionChange"
            />
          </div>
          <div class="search-item">
            <span>发动机型号:</span>
            <el-input
              placeholder="请输入发动机型号"
              maxlength="20"
              v-model="searchForm.EngineModel"
            >
            </el-input>
          </div>
          <div class="search-item power-item">
            <span>功率:</span>
            <el-input type="number" placeholder="最低功率" v-model="searchForm.RatedPowerMin">
            </el-input>
            <span>-</span>
            <el-input type="number" placeholder="最高功率" v-model="searchForm.RatedPowerMax">
            </el-input>
          </div>
          <div class="search-btn-box">
            <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
            <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
          </div>
        </div>
        <!-- 可选表格 -->
        <TableList
          :options="options"
          :columns="columns"
          :dataSource="dataSource"
          border
          v-loading="loading"
        >
          <!-- :pagination="paginationCfg"
          @handleChangePage="handleChangePage"
          @handleChangeSize="handleChangeSize" -->
          <el-table-column slot="operation" label="操作" align="center" width="100">
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button
                  :disabled="scope.row.isSelect"
                  type="text"
                  @click="handleSelect(scope.row, 1)"
                >
                  添加为主机
                </el-button>

                <el-button
                  :disabled="scope.row.isSelect"
                  type="text"
                  @click="handleSelect(scope.row, 2)"
                >
                  添加为分机
                </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
      <div class="right-box">
        <p class="title">当前并机组</p>
        <!-- 已选表格 -->
        <p class="select-type"><span></span>并机组主机</p>
        <TableList :options="options" :columns="columns" :dataSource="selectHostData" border>
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button type="text" @click="handleRemove(scope.row, 1)"> 移除 </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>

        <p class="select-type"><span></span> 并机组分机</p>
        <TableList :options="options" :columns="columns" :dataSource="selectExtensionData" border>
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button type="text" @click="handleRemove(scope.row, 2)"> 移除 </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>

        <p class="all-power">
          当前总功率：<span>{{ TotalPower }}</span
          >Kw
        </p>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close()">取 消</el-button>
      <el-button type="primary" @click="submitForm()">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import PositionCascader from '@/components/PositionCascader'
import _data from './data.js'

export default {
  name: 'AddOrEditParallelCrewDialog',

  components: {
    PositionCascader
  },
  props: {
    projectId: {
      type: Number,
      default: null
    },
    parallelCerwInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      // 筛选条件
      searchForm: {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      },

      // 表格
      columns: _data.setParallelCrewColumns(this),
      dataSource: [], // 全部可选 数据源
      selectHostData: [], //选中主机 数据源
      selectExtensionData: [], //选中分机 数据源
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 60 // 操作列宽度
        }
      },
      ParallelName: '',
      TotalPower: 0
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.loading = true
      // console.log('searchForm------', this.searchForm, this.parallelCerwInfo)
      const params = {
        ...this.searchForm,
        ProjectId: this.projectId,
        ParallelType: 0 //是否并机机组 空或者0:全部;1:并机,2:单机
      }
      this.$api.project
        .GetGensetProjectJoinGensetByAll(params)
        .then((res) => {
          if (res.Code === 1) {
            // 全部机组
            const data = res.Data.map((item) => {
              return {
                ...item.GeneratorSet,
                IsParallel: item.IsParallel,
                isSelect: false
              }
            })
            //新增--全部单机组 编辑--全部单机组和当前并机组数据
            const newData = [],
              hostData = [],
              extensionData = []
            if (!!this.parallelCerwInfo) {
              console.log('edit')
              data.forEach((item) => {
                if (!item.IsParallel) {
                  // 单机
                  newData.push(item)
                } else if (this.parallelCerwInfo.selectHostIds.includes(item.GeneratorSetId)) {
                  // 主机
                  newData.push({ ...item, isSelect: true })
                  hostData.push({ ...item, isSelect: true })
                } else if (this.parallelCerwInfo.selectExtensionIds.includes(item.GeneratorSetId)) {
                  // 并机
                  newData.push({ ...item, isSelect: true })
                  extensionData.push({ ...item, isSelect: true })
                }
              })
              console.log(this.parallelCerwInfo, hostData, extensionData)
              this.$set(this, 'selectHostData', hostData)
              this.$set(this, 'selectExtensionData', extensionData)

              this.countTotalPower()
            } else {
              data.forEach((item) => {
                if (!item.IsParallel) {
                  newData.push(item)
                }
              })
            }
            this.$set(this, 'dataSource', newData)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    reset() {
      this.$set(this, 'searchForm', {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      })
    },
    // 选中
    /**
     * @type 1:主机 2.分机
     */
    handleSelect(row, type) {
      if (type === 1) {
        if (this.selectHostData.length > 0) {
          this.$message.warning('并机组只能设置一个主机！')
          return
        }
        this.selectHostData.push(row)
      } else {
        this.selectExtensionData.push(row)
      }
      this.dataSource.splice(this.dataSource.indexOf(row), 1, { ...row, isSelect: true })
      this.$set(this, 'dataSource', this.dataSource)
      this.countTotalPower()
    },
    // 移除
    /**
     * @type 1:主机 2.分机
     */
    handleRemove(row, type) {
      if (type === 1) {
        this.selectHostData.splice(this.selectHostData.indexOf(row), 1)
      } else {
        this.selectExtensionData.splice(this.selectExtensionData.indexOf(row), 1)
      }
      this.dataSource.splice(
        this.dataSource.findIndex((item) => item.GeneratorSetId == row.GeneratorSetId),
        1,
        { ...row, isSelect: false }
      )
      this.countTotalPower()
    },

    // 计算总功率
    countTotalPower() {
      console.log(this.selectHostData, this.selectExtensionData)
      const allSelect = this.selectHostData.concat(this.selectExtensionData)
      let sum = 0
      allSelect.forEach((item) => {
        sum += item.RatedPower
      })
      this.TotalPower = sum
    },

    // 表单提交
    submitForm() {
      if (this.selectHostData.length == 0) {
        this.$message.warning('并机组必须设置一个主机！')
        return
      }
      if (this.selectExtensionData.length == 0) {
        this.$message.warning('并机组必须设置一个分机！')
        return
      }
      if (!!this.parallelCerwInfo) {
        console.log('save----编辑')
        this.editProjectParallel()
        return
      }
      console.log('save----新建')
      this.addProjectParallel()
    },
    // 新建
    addProjectParallel() {
      const params = {
        ProjectId: this.projectId,
        JoinGensetIsMainList: this.selectHostData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        JoinGensetIsNotMainList: this.selectExtensionData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        ParallelName: this.ParallelName,
        TotalPower: this.TotalPower
      }
      this.$api.project.AddGensetProjectParallel(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '添加并机组成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '添加并机组失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    // 编辑
    editProjectParallel() {
      const query = {
        id: this.parallelCerwInfo.ID
      }
      const params = {
        // ProjectId: this.projectId,
        JoinGensetIsMainList: this.selectHostData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        JoinGensetIsNotMainList: this.selectExtensionData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        ParallelName: this.ParallelName,
        TotalPower: this.TotalPower
      }
      this.$api.project.EditGensetProjectParallel(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '编辑并机组成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '编辑并机组失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    close() {
      this.$emit('closeDialog')
    },

    // 筛选条件--级联选择器change事件
    positionChange(data) {
      // console.log(data)
      this.searchForm.InstallationSite = data.crewPosition
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  display: flex;
  justify-content: space-between;
  .left-box,
  .right-box {
    width: calc(50% - 8px);
    border: 1px solid #e4e4e4;
    > div {
      margin: 8px 16px 8px;
    }
    .title {
      line-height: 40px;
      font-weight: 700;
      border-bottom: 1px solid #e4e4e4;
      text-indent: 8px;
    }
  }

  .search-box .search-item {
    width: 50%;
    &.power-item {
      width: 60%;
    }
  }
  .right-box {
    .select-type {
      padding: 8px 16px 0;
      line-height: 20px;
      span {
        display: inline-block;
        width: 4px;
        height: 16px;
        background-color: #1abc9c;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .all-power {
      margin: 16px 16px 8px;
      text-align: right;
      span {
        color: #1abc9c;
        font-weight: 700;
        margin-right: 4px;
      }
    }
  }
}
</style>



