/*
 * @Autor: linxu
 * @Date: 2022-03-04 15:11:31
 * @LastEditors: linxu
 * @LastEditTime: 2022-04-21 11:36:49
 * @Description: 项目费用 相关枚举
 */
// 开支类型
const CostTypeEnum = [
  { value: 1, label: '收入' },
  { value: 2, label: '支出' }
]
// 开支项目
const CostItemsEnum = []
export default {
  /******** 项目费用管理页 ********/
  CostTypeEnum,

  costManageColumns: (that) => [
    {
      label: '开支类型',
      prop: 'Type',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Type ? CostTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '开支项目',
      prop: 'SortName',
      minWidth: '20%'
    },
    {
      label: '项目金额',
      prop: 'Money',
      minWidth: '20%',
      unit: '元'
    },
    {
      label: '开支说明',
      prop: 'CostDetail',
      minWidth: '35%'
    },

    {
      label: '开支图片',
      prop: 'FileUrl',
      minWidth: '18%',
      formatter: (row, column, index) => {
        // #3BB19C
        return row.FileUrl ? '<span style="color:#409EFF;cursor: pointer">点击查看</span>' : ''
      },
      click: (row, index) => {
        console.log(row, index, that)
        that.handleClickShowImg(row, index)
      }
    },
    {
      label: '修改人',
      prop: 'CreateUserName',
      minWidth: '20%'
    },
    {
      label: '修改时间',
      prop: 'CreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    }
  ],
  // 费用汇总表格
  costAggregationColumns: (that) => [
    {
      label: '开支类型',
      prop: 'Type',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.Type ? CostTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '开支项目',
      prop: 'SortName',
      minWidth: '25%'
    },
    {
      label: '项目总金额',
      prop: 'AllMoney',
      minWidth: '25%',
      unit: '元',
      formatter: (row, column, index) => {
        return row.AllMoney || 0
      }
    },
    {
      label: '项目笔数',
      prop: 'AllCount',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.AllCount || 0
      }
    }
  ],

  // 项目完工报告
  CompletionReportColumns: (that) => [
    {
      label: '完工报告',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '完成情况',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '待完成' : '已完成'
      }
    },
    {
      label: '修改人',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '修改时间',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ],

  // 客户满意度
  CustomerSatisfactionColumns: (that) => [
    {
      label: '客户满意度',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '完成情况',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '待完成' : '已完成'
      }
    },
    {
      label: '修改人',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '修改时间',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ],

  // 运维报告
  OperationReportColumns: (that) => [
    {
      label: '运维报告',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '完成情况',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '待完成' : '已完成'
      }
    },
    {
      label: '上传人',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '上传时间',
      prop: 'LastCreateTime',
      minWidth: '50%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ]
}



