<!--
 * @Autor: linxu
 * @Date: 2022-08-16 08:31:48
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 14:23:23
 * @Description: 项目基本信息
-->
<template>
  <div class="project-info">
    <el-row type="flex">
      <el-col class="project-info-items project-info-basic" :span="18">
        <!-- 项目名称 -->
        <span class="project-info-item project-name">
          {{ basicInfo && basicInfo.ProjectName }}</span
        >

        <!-- 项目类型 -->
        <el-tag class="project-info-item project-tag">
          {{ basicInfo && basicInfo.ProjectType }}
        </el-tag>

        <!-- 项目状态 -->
        <el-tag class="project-info-item project-tag">
          {{ basicInfo && basicInfo.Status }}
        </el-tag>

        <!-- 项目到期时间 -->
        <el-tag class="project-info-item project-tag time">
          {{
            basicInfo && (basicInfo.EndDateLast ? `${basicInfo.EndDateLast} 天后到期` : '已到期')
          }}
        </el-tag>

        <div class="alert-container" v-show="alertShowFlag">
          <el-carousel
            ref="carousel"
            height="30px"
            :interval="3000"
            direction="vertical"
            indicator-position="none"
          >
            <!-- 报警信息 -->
            <el-carousel-item
              v-for="(aItem, aIndex) in warningDatas.d1"
              :key="'warningKey' + aIndex"
            >
              <el-row align="middle" justify="start" type="flex">
                <img class="img-warning" src="@/assets/img/screenData/Warning-dataScreen.svg" />
                <span>{{ aItem.msg }}</span>
              </el-row>
              <el-button
                type="text"
                @click="handleClickLinkTo(aItem.id, aItem)"
                style="position: absolute; right: 5px"
                title="点击跳转日运维图"
                >故障诊断</el-button
              >
            </el-carousel-item>
            <!-- 保养提醒 -->
            <el-carousel-item
              v-for="(rItem, rIndex) in warningDatas.d2"
              :key="'warningKeys' + rIndex"
            >
              <el-row align="middle" justify="start" type="flex">
                <img class="img-warning" src="@/assets/img/screenData/Repair.svg" />
                <span>{{ rItem }}</span>
                <el-button type="text" title="点击跳转日运维图">故障诊断</el-button>
              </el-row>
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-col>
      <el-col class="project-info-items operate-btns" :span="6">
        <i
          class="project-info-item el-icon-refresh"
          title="点击刷新页面"
          @click="() => this.$emit('handleRefresh')"
        ></i>
        <i
          v-show="hasBackBtn"
          class="project-info-item back-btn el-icon-arrow-left"
          title="返回"
          @click="handleBackClick()"
        >
          <span>返回</span>
        </i>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col class="project-info-items project-info-more">
        <span>项目机组功率（Kw）：{{ basicInfo && basicInfo.TotalPower }}</span>
        <span>项目机组数量（台）：{{ basicInfo && basicInfo.Quantity }}</span>
        <span>项目创建时间：{{ basicInfo && basicInfo.CreateTime }}</span>
        <span>项目地址：{{ basicInfo && basicInfo.ProjectAddress }}</span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { formatTimeDayjs } from '@/utils/common'
import _data from '@/enum/projectEnumData'
const { ProjectStatusEnum, ProjectTypeEnum } = _data

export default {
  name: 'ProjectInfo',
  inject: ['reload'], // 注入App中注册的变量
  props: {
    hasBackBtn: {
      type: Boolean,
      default: null
    }
  },
  data() {
    return {
      basicInfo: null, // 机组数据
      alertShowFlag: true,
      warningDatas: {}
    }
  },

  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (newVal, oldVal) {
        const basicInfo = {
          ...newVal,
          ProjectType:
            newVal.ProjectType &&
            ProjectTypeEnum.find((item) => item.value == newVal.ProjectType).label,
          Status:
            newVal.Status && ProjectStatusEnum.find((item) => item.value == newVal.Status).label,
          CreateTime: this.$moment(newVal.CreateTime).format('yyyy-MM-DD HH:mm:ss')
        }
        this.$set(this, 'basicInfo', basicInfo)
        this.getMchInfo()
      },
      // 深度观察监听
      deep: true,
      immediate: true
    }
  },
  mounted() {
    // const newVal = this.projectBasicInfo
    // const basicInfo = {
    //   ...newVal,
    //   ProjectType:
    //     newVal.ProjectType &&
    //     ProjectTypeEnum.find((item) => item.value == newVal.ProjectType).label,
    //   Status:
    //     newVal.Status && ProjectStatusEnum.find((item) => item.value == newVal.Status).label,
    //   CreateTime: this.$moment(newVal.CreateTime).format('yyyy-MM-DD HH:mm:ss')
    // }
    // this.$set(this, 'basicInfo', basicInfo)
    // TODO 暂不定时刷新
    this.animation = setInterval(() => {
      this.getMchInfo()
    }, 1000 * 30 * 1)
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    handleClickLinkTo(id, item) {
      this.$api.operation.FindGeneratorSet({ id: id }).then((value) => {
        const data = value.Data
        this.toggleSelectCrew(data)
        // this.$router.push('/operation/operationalChart/operationalChartDaily')
        window.open(
          `/#/operation/operationalChart/operationalChartDaily?` +
            `type=newPage&crewId=${id}&projectId=${this.currentProject.ProjectId}`
        )
      })
    },
    // 查询详细数据
    getMchInfo() {
      this.$api.project
        .getGensetProjectJoinGensetNowData({ ProjectId: this.currentProject.ProjectId || 3 })
        .then((value) => {
          const data = value.Data
          if (data) {
            const gensetAlarmList = data.GensetAlarmList

            if (gensetAlarmList) {
              const alarmList = gensetAlarmList || []
              if (alarmList.length === 0) {
                this.alertShowFlag = false
              } else {
                this.alertShowFlag = true
              }
              const remindList = []
              const warningData = []
              const keepData = []
              alarmList.forEach((e, i) => {
                const obj = {
                  order: i,
                  time: formatTimeDayjs(e.Time).format('yyyy-MM-DD HH:mm:ss'),
                  mchname: e.GensetName,
                  divisionmch: '测试分机组',
                  warningcontent: e.Content,
                  warningtype: e.Type,
                  operation: '查看',
                  GeneratorSetId: e.GeneratorSetId
                }
                warningData.push(obj)
              })
              remindList.forEach((e, i) => {
                const obj = {
                  order: i,
                  unit: e.GensetName,
                  plandate: formatTimeDayjs(e.Time).format('YYYY-MM-DD'),
                  type: e.Type,
                  operation: '查看',
                  keepContent: e.Content
                }
                keepData.push(obj)
              })
              this.warningDatas = {
                d1: [
                  ...warningData.map((item) => {
                    let msg = '实时报警：' + item.mchname + '-' + item.warningcontent
                    let id = item.GeneratorSetId
                    return { msg, id }
                  })
                ],
                d2: [
                  ...keepData.map((item) => {
                    let msg = '保养提醒：' + item.unit + item.keepContent
                    return msg
                  })
                ]
              }
            }
          }
        })
    },

    // 刷新
    handleRefresh() {
      this.reload()
    },
    handleBackClick() {
      this.$router.go(-1)
    }
  },
  beforeDestroy() {
    clearInterval(this.animation)
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.project-info {
  > .el-row {
    margin-bottom: 15px;
    &:first-child {
      height: 24px;
    }
  }
  .project-info-items {
    display: flex;
    align-items: center;
    .el-tag {
      height: 24px;
      line-height: 22px;
      border: 0;
    }
    .project-info-item {
      &:not(:last-child) {
        margin-right: 12px;
      }
    }
    &.project-info-basic {
      .project-name {
        font-size: 1.3rem;
        font-weight: 700;
      }
      .project-tag {
        background-color: rgba(215, 215, 215, 1);
        color: #000;
        font-size: 1rem;
        &.time {
          background: #f39931;
          color: #fff;
        }
      }
      .alert-container {
        flex: 1;
        height: 30px;
        border: 1px solid #fff;
        border-radius: 4px;
        .img-warning {
          width: 20px;
          margin: 0 5px 0 10px;
        }
        /deep/ .el-carousel__item {
          display: flex;
        }
      }
    }
    &.operate-btns {
      justify-content: flex-end;
      .project-info-item {
        margin: 0 0 0 8px;
        font-size: 18px;
        cursor: pointer;
        &.back-btn {
          width: 46px;
          span {
            font-size: 14px;
            position: absolute;
            margin-top: 2px;
            white-space: nowrap;
          }
        }
      }
    }
    &.project-info-more {
      font-size: 0.8rem;
      > span {
        margin-right: 20px;
      }
    }
  }
}

.theme-dark {
  // .toggle-crew-list,
  .toggle-crew-list .genset-container .item-box {
    border-color: hsla(0, 0%, 100%, 0.3);
  }
  .el-drawer__wrapper {
    /deep/.el-drawer {
      background-color: @menuBg;
    }
  }
}
</style>



