<!--
 * @Autor: linxu
 * @Date: 2022-03-04 14:53:23
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-29 17:13:30
 * @Description: 项目基本信息列表
-->
<template>
  <div class="item-content basic-info">
    <el-row>
      <el-col :span="8">
        <span>项目名称:</span>
        <span> {{ basicInfo.ProjectName }} </span>
      </el-col>
      <el-col :span="8">
        <span>项目编号:</span>
        <span> {{ basicInfo.ProjectNumber }} </span>
      </el-col>
      <el-col :span="8">
        <span>项目类型:</span>
        <span> {{ basicInfo.ProjectType }} </span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <span>项目地址:</span>
        <span> {{ basicInfo.ProjectAddress }} </span>
      </el-col>
      <el-col :span="8">
        <span>项目起止时间:</span>
        <span> {{ basicInfo.contractTime }} </span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <span>功率需求:</span>
        <span> {{ basicInfo.TotalPower }} Kw</span>
      </el-col>
      <el-col :span="8">
        <span>租赁台数:</span>
        <span> {{ basicInfo.Quantity }} 台</span>
      </el-col>
      <el-col :span="8">
        <span>项目租金:</span>
        <span> {{ basicInfo.TotalMoney }} 元</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <span>创建时间:</span>
        <span> {{ basicInfo.CreateTime }} </span>
      </el-col>
      <el-col :span="8">
        <span>合同状态:</span>
        <span> {{ basicInfo.Status }} </span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import _data from './data.js'
const { ProjectStatusEnum, ProjectTypeEnum } = _data

export default {
  name: 'BasicInfoList',
  props: {
    projectBasicInfo: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      basicInfo: {}
    }
  },
  watch: {
    projectBasicInfo: {
      handler: function (newVal, oldVal) {
        const basicInfo = {
          ...newVal,
          ProjectType:
            newVal.ProjectType &&
            ProjectTypeEnum.find((item) => item.value == newVal.ProjectType).label,
          Status:
            newVal.Status && ProjectStatusEnum.find((item) => item.value == newVal.Status).label,
          contractTime:
            newVal.StartDate && newVal.EndDate
              ? `${this.$moment(newVal.StartDate).format('YYYY.MM.DD')} - ${this.$moment(
                  newVal.EndDate
                ).format('YYYY.MM.DD')}`
              : '',
          CreateTime: this.$moment(newVal.CreateTime).format('yyyy-MM-DD HH:mm:ss')
        }
        // console.log(basicInfo)
        this.$set(this, 'basicInfo', basicInfo)
      },
      deep: true
    }
  }
}
</script>

<style lang="less" scoped>
.basic-info {
  padding-left: 8px !important;
  .el-col {
    line-height: 32px;
    span + span {
      margin-left: 8px;
    }
  }
}
</style>



