<!--
 * @Autor: linxu
 * @Date: 2022-03-04 16:45:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-23 10:37:27
 * @Description: 上传文件弹窗
-->
<template>
  <el-dialog
    title="上传合同文件"
    class="upload-file-dialog"
    width="52%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <el-form :model="form" :rules="rules" ref="projectForm" :label-width="formLabelWidth">
      <el-form-item label="文件类型" prop="FileType">
        <el-select v-model="form.FileType" placeholder="请选择文件类型">
          <!-- <el-option label="请选择" value=""></el-option> -->
          <el-option
            v-for="(item, index) in ProjectFileTypeEnum"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="文件上传" prop="fileList">
        <el-upload
          class="upload-file"
          ref="upload"
          action="#"
          :file-list="form.fileList"
          :auto-upload="false"
          :on-change="getValChange"
          :on-exceed="exceedLimit"
          :on-remove="handleRemove"
          :limit="limit"
          multiple
        >
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <!-- <el-button style="margin-left: 10px" size="small" type="success" @click="submitUpload">
            上传到服务器
          </el-button> -->
          <div slot="tip" class="el-upload__tip">
            (支持不超过10M的.doc、docx、xls、xlsx、pdf、rar、zip、jpg、jpge、png文件；最多上传10个文件)
          </div>
        </el-upload>
      </el-form-item>
      <el-row>
        <el-col :span="12">
          <el-form-item label="文件名称" prop="FileName">
            <el-input
              v-model="form.FileName"
              placeholder="请输入文件名称"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="备注" prop="FileDetail">
            <el-input
              v-model="form.FileDetail"
              placeholder="请输入备注"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('projectForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('projectForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import _data from './data.js'
const { ProjectFileTypeEnum } = _data

export default {
  name: 'FileUploadDialog',
  props: {
    projectId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      dialigVisible: true,
      formLabelWidth: '100px',
      form: {
        FileType: '',
        FileName: '',
        fileList: [],
        FileDetail: ''
      },
      rules: {
        FileType: [{ required: true, message: '文件类型', trigger: 'change' }],
        fileList: [{ required: true, message: '请选择文件', trigger: 'change' }],

        FileName: [
          { type: 'string', required: true, message: '请输入文件名称', trigger: 'blur' },
          { max: 15, message: '长度不超过 15 个字符', trigger: 'blur' }
        ]
        // FileDetail: [
        //   { required: true, message: '请输入备注', trigger: 'blur' },
        //   { max: 40, message: '长度不超过 40 个字符', trigger: 'blur' }
        // ]
      },
      limit: 3,

      ProjectFileTypeEnum // 文件类型枚举
    }
  },
  mounted() {},
  methods: {
    exceedLimit(files, fileList) {
      console.log(files, fileList)
      this.$message.error('上传文件个数不能超过' + this.limit + '个')
    },
    // 文件值改变时触发 change事件
    getValChange(file, fileList) {
      console.log('getValChange', file.type, fileList, this.limit)
      const type = file.raw.type
      const isCorrectType = [
        'image/jpeg',
        'image/png',
        'image/bmp',
        'image/gif',
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        'application/vnd.ms-excel',
        'application/pdf',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'application/msword',
        'application/vnd.openxmlformats-officedocument.presentationml.presentation',
        'application/vnd.ms-powerpoint'
      ].includes(type)
      const isLt10M = file.size / 1024 <= 10240
      if (!isCorrectType) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件格式有误!')
        return false
      }
      if (!isLt10M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 10MB!')
        return false
      }
      this.$set(this.form, 'fileList', fileList)
    },
    handleRemove(file, fileList) {
      console.log(file, fileList)
      this.$set(this.form, 'fileList', fileList)
    },

    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form)
          let formData = new FormData()
          this.form.fileList.forEach((item) => {
            formData.append('file', item.raw)
          })

          formData.append('ProjectId', this.projectId)
          formData.append('FileType', this.form.FileType)
          formData.append('FileName', this.form.FileName)
          formData.append('FileDetail', this.form.FileDetail)
          this.$api.project.AddGensetProjectFile(formData).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '文件上传成功!'
              })
            } else {
              this.$message({
                type: 'error',
                message: '文件上传失败，请重新提交!'
              })
            }
            this.$emit('closeDialog', { refresh: true })
          })
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped></style>



