<!--
 * @Autor: linxu
 * @Date: 2022-03-03 16:17:06
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:29:17
 * @Description: 完工记录
-->
<template>
  <div class="completion-record-container">
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="completion-record">
      <!-- <div class="project-info">
        <p class="content-title">项目基本信息</p>
        <BasicInfoList v-loading="loading" :projectBasicInfo="projectBasicInfo" />
      </div> -->
      <div class="cost-info">
        <p class="content-title">费用明细</p>
        <!-- 筛选 -->
        <div class="search-box">
          <div class="search-item">
            <span>开支类型:</span>
            <el-select
              v-model="searchForm.Type"
              :popper-append-to-body="false"
              placeholder="请选择开支类型"
              @change="costTypeChange()"
            >
              <el-option label="全部" value=""></el-option>
              <el-option
                v-for="item in CostTypeEnum"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span>开支项目:</span>
            <el-select
              v-model="searchForm.Sort"
              :popper-append-to-body="false"
              placeholder="请选择开支项目"
            >
              <el-option label="全部" value=""></el-option>

              <el-option
                v-for="item in CostItemsEnum"
                :key="item.Sort"
                :label="item.SortName"
                :value="item.Sort"
              ></el-option>
              <!-- <el-option label="租金" value="1"></el-option>
                  <el-option label="运费" value="2"></el-option>
                  <el-option label="吊装费" value="3"></el-option>
                  <el-option label="机油" value="4"></el-option>
                  <el-option label="柴油" value="5"></el-option>
                  <el-option label="耗材" value="6"></el-option>
                  <el-option label="人员" value="7"></el-option> -->
            </el-select>
          </div>
          <div class="search-btn-box">
            <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
            <el-button type="primary" class="query-btn" @click="resetCurrentParams()"
              >查 询</el-button
            >
          </div>
        </div>
        <div class="operation-box">
          <el-button type="primary" class="add-btn" @click="handleAdd()">添加开支</el-button>
        </div>
        <!-- 表格 -->
        <TableList
          :options="options"
          :columns="columns"
          :dataSource="dataSource"
          :dataTotal="dataTotal"
          :pagination="paginationCfg"
          @handleChangePage="handleChangePage"
          @handleChangeSize="handleChangeSize"
          border
          v-loading="loading_1"
        >
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: right">
                <el-button type="text" @click="handleEdit(scope.row)"> 编辑 </el-button>
                <el-button type="text" @click="handleDelete(scope.row)"> 删除 </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
        <p class="content-title cost-aggregation">费用汇总</p>
        <!-- 表格 -->
        <TableList
          :options="options"
          :columns="costAggregationColumns"
          :dataSource="data"
          border
          v-loading="loading_1"
        >
        </TableList>
        <p class="cost-summary">
          <span>
            项目总收入：<em>{{ allIncome || 0 }}</em> 元
          </span>
          <span>
            项目总支出：<em>{{ allExpend || 0 }}</em> 元
          </span>
        </p>
        <!-- <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="费用明细" name="first"> </el-tab-pane>
          <el-tab-pane label="费用汇总" name="second" class="cost-summary"> </el-tab-pane>
        </el-tabs> -->
      </div>
      <!-- 项目完工报告和客户满意度回馈 -->
      <ProjectCompletion ref="projectCompletion"></ProjectCompletion>
    </div>

    <!-- 添加/编辑 开支弹窗 -->
    <AddOrEditSpendingDialog
      v-if="spendingDialogVisible"
      :ProjectId="ProjectId"
      :recordInfo="recordInfo"
      @closeDialog="(val) => closeDialog('spendingDialogVisible', val)"
    />

    <!-- 点击查看图片弹窗 -->
    <SpendingImgDialog
      v-if="spendingImgDialogVisible"
      :imgUrlArr="imgUrlArr"
      @closeDialog="(val) => closeDialog('spendingImgDialogVisible', val)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import BasicInfoList from '../components/BasicInfoList.vue'
import AddOrEditSpendingDialog from './components/AddOrEditSpendingDialog.vue'
import SpendingImgDialog from './components/SpendingImgDialog.vue'
import ProjectCompletion from './ProjectCompletion.vue'
import _data from './components/data.js'

export default {
  name: 'ProjectCostManage',
  mixins: [paginationConfigMixin],
  components: {
    BasicInfoList,
    AddOrEditSpendingDialog,
    SpendingImgDialog,
    ProjectCompletion
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          this.$set(this, 'ProjectId', nv.ProjectId)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      loading_1: false,
      // 项目基本信息
      ProjectId: null,
      projectBasicInfo: {
        id: 20 // 项目ID
      },
      activeName: 'first',
      // 筛选条件
      searchForm: {
        Type: '',
        Sort: ''
      },
      CostTypeEnum: _data.CostTypeEnum, // 开支类型
      CostItemsEnum: [], // 开支项目
      // 表格
      columns: _data.costManageColumns(this),
      dataSource: [],
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 86 // 操作列宽度
        }
      },
      data: [],
      allIncome: null, // 总收入
      allExpend: null, // 总支出

      costAggregationColumns: _data.costAggregationColumns(this),
      // 添加/编辑 开支弹窗
      recordInfo: null, //操作行数据ID
      spendingDialogVisible: false,

      // 点击查看图片弹窗
      spendingImgDialogVisible: false,
      imgUrlArr: []
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    // console.log('searchForm------', this.searchForm, this.$route.query.ProjectId)
    // this.getProjectBasicInfo()
    this.GetGensetProjectCostType()
    this.queryTableData()

    this.GetGensetProjectCostStats()
  },
  methods: {
    initData() {
      // this.getProjectBasicInfo()
      this.GetGensetProjectCostType()
      this.queryTableData()
      this.GetGensetProjectCostStats()
      this.$refs.projectCompletion.initData()
    },
    // 查询项目基本信息
    getProjectBasicInfo() {
      this.loading = true
      const query = { id: this.ProjectId }
      this.$api.project
        .GetGensetProject(query)
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'projectBasicInfo', res.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 查询开支项目
    GetGensetProjectCostType() {
      const params = {
        Type: this.searchForm.Type || -1
      }
      this.$api.project.GetGensetProjectCostType(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.CostItemsEnum = res.Data
        }
      })
    },
    // 查询费用列表
    queryTableData() {
      this.loading_1 = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        ...this.searchForm,
        ProjectId: this.ProjectId
      }
      this.$api.project
        .GetGensetProjectCostManage(params, query)
        .then((res) => {
          if (res.Code === 1) {
            // console.log(res)
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
          }
          this.loading_1 = false
        })
        .catch(() => {
          this.loading_1 = false
        })
    },
    costTypeChange() {
      this.searchForm.Sort = ''
      this.GetGensetProjectCostType()
    },

    // 获取项目费用统计
    GetGensetProjectCostStats() {
      this.loading_1 = true
      const params = {
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectCostStats(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.allIncome = res.Data.AllTypeOne
          this.allExpend = res.Data.AllTypeTwo
          this.$set(this, 'data', res.Data.allList)
        }
        this.loading_1 = false
      })
    },
    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        Type: '',
        Sort: ''
      })
    },
    // 新增
    handleAdd() {
      this.recordInfo = null
      this.spendingDialogVisible = true
    },
    // 编辑
    handleEdit(row) {
      this.recordInfo = row
      this.spendingDialogVisible = true
    },

    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    },
    // 删除
    handleDelete(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '删除开支项目',
        message: h('div', [h('p', [h('span', '确定删除当前开支项目吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.ProjectCostId }
          this.$api.project.DeleteGensetProjectCost({}, query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '删除开支项目成功!'
              })

              this.queryTableData()
            } else {
              this.$message({
                type: 'error',
                message: '删除开支项目失败，请重新提交!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    // 点击查看图片弹窗
    handleClickShowImg(row, index) {
      // console.log('点击查看', row, index)
      if (row.FileUrl) {
        this.$set(this, 'imgUrlArr', row.FileUrlArrays)
        this.spendingImgDialogVisible = true
      }
    },

    handleBackClick() {
      this.$router.push({ path: '/project/project-list' })
    },
    // 标签切换
    handleClick(tab, event) {
      // console.log(tab, event)
      if (this.activeName == 'second') {
        this.GetGensetProjectCostStats()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.completion-record-container {
  padding-bottom: 24px;
}
.completion-record {
  display: initial !important;
  .operation-box {
    margin-bottom: 6px;
    .add-btn {
      width: 98px;
      background-color: #1abc9c;
      color: #fff;
      margin-bottom: 10px;
      display: flex;
    }
  }
  .cost-info {
    .content-title.cost-aggregation {
      margin-top: 32px;
    }
  }
  .project-info {
    .content-title {
      height: 48px;
      line-height: 48px;
    }

    .item-content {
      padding: 0 0 16px 8px;
    }
  }

  .search-box {
    background-color: transparent;
    padding: 0 16px;
    height: 32px;
    border: 0;
    .search-item {
      // padding: 0 24px 0 0;
      // height: 32px;
    }
  }
  p.cost-summary {
    text-align: right;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    padding: 8px 0;
    span {
      margin: 0 4px 0 24px;
    }
    em {
      color: #1abc9c;
    }
  }
}
</style>



