<!--
 * @Autor: linxu
 * @Date: 2022-03-14 10:05:41
 * @LastEditors: linxu
 * @LastEditTime: 2022-06-27 10:13:27
 * @Description: 查看维保策略弹窗
-->
<template>
  <div>
    <el-dialog
      title="查看维保策略"
      class="show-strategy-dialog"
      width="880px"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="confirmClick()"
    >
      <div class="strategy-box">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="运行时间维度" name="first"></el-tab-pane>
          <el-tab-pane label="绝对时间维度" name="second"> </el-tab-pane>
        </el-tabs>
        <div class="data-table">
          <ul class="title">
            <li>维保事项\节拍时间</li>
            <li v-for="(head, index) in columnHead" :key="index">
              {{ `${head} ${activeName === 'first' ? 'h' : '天'}` }}
            </li>
          </ul>
          <ul class="content">
            <li class="content-item" v-for="(item, index) in dataSource" :key="index">
              <div>
                <span>{{ index + 1 }}</span>
              </div>
              <div v-for="(cItem, index) in columnHead" :key="index">
                <!-- <span>{{ item[cItem] }}</span> -->
                <el-tooltip
                  v-if="item[cItem]"
                  class="item"
                  effect="dark"
                  :content="`${item[cItem]} ${activeName === 'first' ? 'h' : '天'}`"
                  placement="top"
                >
                  <div class="circle-tip"></div>
                </el-tooltip>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmClick()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
const dataSource1 = [
  { name: '更换润滑油', 100: 72, 200: 120, 800: 720 },
  { name: '更换防冻液', 100: 72, 200: 120, 600: 600 },
  { name: '更换空气滤清器', 100: 100, 200: 120 },
  { name: '更换机油滤清器', 100: 100, 200: 120, 700: 648 }
]
const dataSource2 = [
  { name: '更换润滑油', 5: 3, 10: 8, 40: 35 },
  { name: '更换防冻液', 5: 3, 10: 8, 30: 30 },
  { name: '更换空气滤清器', 5: 5, 10: 10 },
  { name: '更换机油滤清器', 5: 5, 10: 10, 35: 32 }
]
export default {
  name: 'StrategyShowDialog',
  props: {
    // 机组ID
    gensetId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      dataSource: null,
      activeName: 'first',
      columnHead: []
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.$set(this, 'columnHead', [100, 200, 300, 400, 500, 600, 700, 800, 900, 1000])
      this.dataSource = dataSource1
    },
    // 标签切换
    handleClick(tab, event) {
      console.log(tab, event)
      if (this.activeName === 'first') {
        this.$set(this, 'columnHead', [100, 200, 300, 400, 500, 600, 700, 800, 900, 1000])
        this.dataSource = dataSource1
      } else {
        this.$set(this, 'columnHead', [5, 10, 15, 20, 25, 30, 35, 40, 45, 50])
        this.dataSource = dataSource2
      }
    },
    // 确定
    confirmClick() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.show-strategy-dialog {
  .data-table {
    overflow: auto;
    padding-bottom: 4px;
  }
  ul.title,
  ul.content {
    margin-left: 1px;
  }
  ul.title {
    width: max-content;
    line-height: 32px;
    text-align: center;
    > li {
      float: left;
      padding: 8px 12px;
      margin: 0 0 -1px -1px;
      border: 1px solid #e4e4e4;
      background: #f5f7fa;
      height: 48px;
      line-height: 48px;
    }
    > li:nth-child(1) {
      width: 160px;
    }
    > li:not(:first-child) {
      width: 68px;
    }
  }

  ul.content > li.content-item {
    width: max-content;
    line-height: 32px;
    text-align: center;
    > div {
      float: left;
      padding: 8px 12px;
      margin: 0 0 -1px -1px;
      border: 1px solid #e4e4e4;
      text-align: center;
      height: 48px;
      line-height: 48px;
    }
    > div:nth-child(1) {
      width: 160px;
      background: #f5f7fa;
    }
    > div:not(:first-child) {
      width: 68px;
      display: flex;
      align-items: center;
      justify-content: center;
      .circle-tip {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: @theme;
        cursor: pointer;
      }
    }
  }
}
.theme-dark .show-strategy-dialog {
  ul.title > li {
    background: transparent;
  }
  ul.content > li.content-item {
    > div:nth-child(1) {
      background: transparent;
    }
  }
}
</style>



