<!--
 * @Autor: linxu
 * @Date: 2022-03-07 15:21:04
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-16 15:00:13
 * @Description: 电子围栏设置弹窗
-->
<template>
  <el-dialog
    title="修改电子围栏设置"
    class="electronic-fence-dialog"
    width="70%"
    v-loading="loading"
    :element-loading-text="loadingMsg"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <!-- 重置按钮 -->
    <i class="resetBtn el-icon-aim" title="重置" @click="renderFenceMap('reset')"></i>
    <div id="crew-location-fence-box"></div>
    <el-form :model="form" :rules="rules" ref="projectForm" :label-width="formLabelWidth">
      <el-form-item label="围栏中心:">
        <span slot="label">
          <span><i class="require_tip">*</i>围栏中心:</span>
        </span>
        <el-row type="flex">
          <el-col :span="12">
            <el-form-item label="" prop="longitude">
              <el-input
                v-model="form.longitude"
                placeholder="请输入中心点经度"
                autocomplete="off"
                @input="formInput()"
              ></el-input>
              <span class="unit">经度</span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="latitude">
              <el-input
                v-model="form.latitude"
                placeholder="请输入中心点纬度"
                autocomplete="off"
                @input="formInput()"
              ></el-input>
              <span class="unit">纬度</span>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item label="围栏半径:" prop="radius">
        <el-input
          v-model="form.radius"
          placeholder="请输入围栏半径"
          autocomplete="off"
          @input="formInput()"
        ></el-input>
        <span class="unit">米</span>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('projectForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('projectForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import loadBMap from '@/utils/loadBMap'
import { coordinateHandler } from '@/utils/common'

export default {
  name: 'AddOrEditPersonnelDialog',
  props: {
    gensetId: {
      type: Number,
      default: null
    },
    setFenceType: String
  },
  data() {
    return {
      dialigVisible: true,
      formLabelWidth: '100px',
      form: {
        longitude: '',
        latitude: '',
        radius: ''
      },
      rules: {
        longitude: [{ required: true, message: '请输入中心点经度', trigger: 'blur' }],
        latitude: [{ required: true, message: '请输入中心点纬度', trigger: 'blur' }],
        radius: [{ required: true, message: '请输入围栏半径', trigger: 'blur' }]
      },

      loading: false,
      loadingMsg: '地图加载中，请您稍候...',
      gensetMap: null,
      // 电子围栏的半径,默认500米
      fenceRadius: 500,
      // 电子围栏的中心点，默认当前位置
      fenceCenterPoint: null,
      beginDate: this.$moment().add(-3, 'days').format(`YYYY-MM-DD`),
      endTime: this.$moment().format(`YYYY-MM-DD HH:mm`),
      dataObj: null,
      fenceMap: null
    }
  },
  // watch: {
  //   gensetId: function (newVal, oldVal) {
  //     console.log('watch----gensetId', newVal, oldVal)
  //     this.renderFenceMap()
  //   }
  // },
  mounted() {
    this.renderFenceMap()
  },
  methods: {
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form)
          this.commitHandler()
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },

    // 渲染电子围栏地图
    renderFenceMap(type) {
      /* global BMAP_ANIMATION_BOUNCE */
      this.loading = true
      this.$api.project
        .GetGensetFenceSetting({
          id: this.gensetId
        })
        .then((res) => {
          this.loading = false
          this.dataObj = res.Data
          const _this = this
          loadBMap(`o7gALXwtch6SNk9jjH4DmFffcFt1PhFL`)
            .then((e) => {
              const BMap = e || undefined
              const map = new BMap.Map('crew-location-fence-box', {
                enableMapClick: false
              })
              _this.gensetMap = _this.dataObj.Genset
              // 机组位置
              let gensetPoint = new BMap.Point(_this.gensetMap.Longitude, _this.gensetMap.Latitude)
              let point = null
              // 新增/重置 设置中心点为机组经纬度
              if (this.setFenceType == 'add' || type == 'reset') {
                point = new BMap.Point(_this.gensetMap.Longitude, _this.gensetMap.Latitude)
              } else {
                // 编辑回显 半径和经纬度
                _this.fenceRadius = _this.dataObj.Range
                point = new BMap.Point(_this.dataObj.Longitude, _this.dataObj.Latitude)
              }
              _this.fenceCenterPoint = point
              _this.setFormData() // 填充表单数据
              const circleOpt = {
                opt: {
                  strokeColor: 'red',
                  strokeWeight: 2,
                  strokeOpacity: 0.5,
                  fillOpacity: 0.1,
                  fillColor: 'tomato'
                }
              }
              const fixedMarker = new BMap.Marker(gensetPoint, {
                offset: new BMap.Size(0, 5)
              })
              const clickEvent = (params) => {
                const newPoint = params.point
                _this.fenceCenterPoint = newPoint
                // console.log('clickEvent',newPoint, _this.fenceRadius, circleOpt.opt)
                const circle = new BMap.Circle(newPoint, _this.fenceRadius, circleOpt.opt)
                map.clearOverlays()
                map.addOverlay(circle)
                _this.setOverlayEditing(circle)
                circle.addEventListener(`lineupdate`, (e) => {
                  _this.fenceRadius = e.target.getRadius().toFixed(0)
                  _this.fenceCenterPoint = e.target.getCenter()
                  // console.log('lineupdate', _this.fenceRadius, _this.fenceCenterPoint)
                  _this.setFormData()
                })
                _this.setFormData()
              }
              map.disableDoubleClickZoom()
              map.enableScrollWheelZoom()
              map.centerAndZoom(point, 15)
              clickEvent({ point })
              map.addEventListener(`click`, clickEvent)
              // 固定当前机组的位置，禁止被清除
              fixedMarker.disableMassClear()
              fixedMarker.setAnimation(BMAP_ANIMATION_BOUNCE)
              map.addOverlay(fixedMarker)
              // document.querySelector(`.crew-location-fence-box`).append(html)
              _this.fenceMap = map
            })
            .catch((err) => {
              alert(err)
            })
        })
    },
    // 设置地图覆盖物可编辑
    setOverlayEditing(overlay) {
      setTimeout(() => {
        overlay.enableEditing()
      }, 0)
    },
    // 填充表单数据
    setFormData() {
      this.$set(this, 'form', {
        longitude: this.fenceCenterPoint.lng,
        latitude: this.fenceCenterPoint.lat,
        radius: this.fenceRadius
      })
    },
    // 提交围栏数据
    commitHandler() {
      const newCoordinate = coordinateHandler.bd_gps_encrypt(this.form.latitude, this.form.longitude)
      const newCoordinate2 = coordinateHandler.gcj02_gps_encrypt(newCoordinate.Longitude,newCoordinate.Latitude)
      // 关闭窗口，显示遮罩
      const params = {
        IsOpen: true,
        Range: this.form.radius,
        Longitude: newCoordinate2.Longitude,
        Latitude: newCoordinate2.Latitude
      }
      const query = { id: this.gensetId }
      this.$api.project
        .GensetFenceSetting(params, query)
        .then((res) => {
          this.$message.success(res.Message)
          this.close(true)
        })
        .catch((error) => {
          this.$message.error(error)
          this.close()
        })
    },
    // 关闭弹窗
    close(val) {
      // 重置地图
      this.loading = false
      this.fenceMap = null
      if (val) {
        this.$emit('closeDialog', { refresh: true })
      } else {
        this.$emit('closeDialog')
      }
    },

    // 表单输入框 输入值变化
    formInput() {
      const newVal = this.form
      const newPoint = {
        lat: newVal.latitude,
        lng: newVal.longitude
      }
      const fenceRadius = newVal.radius
      const circleOpt = {
        strokeColor: 'red',
        strokeWeight: 2,
        strokeOpacity: 0.5,
        fillOpacity: 0.1,
        fillColor: 'tomato'
      }
      const circle = new BMap.Circle(newPoint, fenceRadius, circleOpt)
      this.fenceMap.clearOverlays()
      this.fenceMap.addOverlay(circle)
      this.setOverlayEditing(circle)
      circle.addEventListener(`lineupdate`, (e) => {
        this.fenceRadius = e.target.getRadius().toFixed(0)
        this.fenceCenterPoint = e.target.getCenter()
        this.setFormData()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.electronic-fence-dialog {
  .resetBtn {
    position: absolute;
    right: 33px;
    top: 34px;
    z-index: 1;
    cursor: pointer;
    // color: #409eff;
    color: #1abc9d !important;
    font-size: 36px;
    &:hover {
      color: #66b1ff;
    }
  }
  #crew-location-fence-box {
    height: 460px;
    margin-bottom: 16px;
  }
  /deep/.el-form {
    > .el-form-item:first-child {
      margin-bottom: 0;
    }
    .el-form-item__content {
      flex: 1;
    }
    .el-row {
      width: 100%;
      .el-col:not(:last-child) {
        margin-right: 16px;
      }
    }
    .require_tip {
      content: '*';
      color: #f56c6c;
      margin-right: 4px;
    }
  }
}
</style>



