<script>
/**
 * @desc project monitoring page
 * @auth Demon3443002624@qq.com
 * @date 2022-8-5 11:00:55
 */

import { mapActions, mapGetters } from 'vuex'

import color from '@/enum/color'
import { formatTimeDayjs } from '@/utils/common'
import ProjectSelect from '@/components/ProjectSelect'

export default {
  name: 'ProjectMonitoringNew',
  components: {
    ProjectSelect
  },
  data() {
    return {
      times: 0,
      crewList: [],
      myCharts: [],
      crewListMu: [],
      realTimeData: {},
      projectInfo: {},
      editType: false,
      animation: null,
      resizeFun: null,
      ecuCodeData: {
        flag: false,
        dataArr: [],
        dataObj: {}
      },
      projectInfo2: {},
      warningDatas: {},
      defaultGensetData: {},
      refreshInterval: 1000 * 10,
      firstRenderChartFlag: false,
      nowTime: new Date().toLocaleString(),
      mapping: {
        projectType: ['', '运维', '代运维'],
        projectStatus: ['', '准备中', '进行中', '已完成'],
        // crew status 0离线 1停机 2警告 3报警 4运行
        crewStatus: ['离线', '停机', '警告', '报警', '运行'],
        crewStatusColor: [
          color.crewStatusMapping.offline,
          color.crewStatusMapping.stop,
          color.crewStatusMapping.warning,
          color.crewStatusMapping.error,
          color.crewStatusMapping.running
        ]
      },
      queryOpt: {
        workType: [
          {
            label: '电站管理模式',
            value: 0
          },
          {
            label: '就地并机模式',
            value: 1
          },
          {
            label: '单机模式',
            value: 2
          },
          {
            label: '单机并网模式',
            value: 3
          },
          {
            label: '单机AMF模式',
            value: 4
          }
        ],
        dispatchType: [
          {
            label: '默认调度',
            value: 0
          },
          {
            label: '云端+本地调度',
            value: 1
          },
          {
            label: '多台开机调度',
            value: 2
          }
        ]
      },
      option_1: {
        grid: {
          top: '16px',
          right: '16px',
          bottom: '16px',
          left: '16px',
          containLabel: true
        },
        series: [
          {
            type: 'gauge',
            radius: '90%',
            min: 0,
            max: 2000,
            // splitNumber: 20,
            axisLine: {
              lineStyle: {
                width: 5,
                color: [
                  [0.3, '#67e0e3'],
                  [0.7, '#37a2da'],
                  [1, '#fd666d']
                ]
              }
            },
            pointer: {
              width: 2,
              itemStyle: {
                color: 'auto'
              }
            },
            axisTick: {
              distance: -5,
              length: 2,
              lineStyle: {
                color: '#fff',
                width: 1
              }
            },
            splitLine: {
              distance: -5,
              length: 5,
              lineStyle: {
                color: '#fff',
                width: 2
              }
            },
            axisLabel: {
              color: '#0099FF',
              distance: 8,
              fontSize: 8,
              formatter: function (v) {
                return v.toFixed(0)
              }
            },
            detail: {
              valueAnimation: true,
              formatter: ' {value} RPM',
              color: 'inherit',
              fontSize: 10,
              width: 60,
              lineHeight: 16,
              overflow: 'break',
              offsetCenter: [0, '40%']
            },
            data: [
              {
                value: 0,
                name: '',
                title: {
                  fontSize: 10,
                  offsetCenter: ['0%', '40%']
                }
              }
            ]
          }
        ]
      },
      queryForm: {
        workType: '',
        dispatchType: '',
        dispatchNumber: 0
      }
    }
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          nv && (this.projectInfo = nv)
          this.projectInfo.timeStr = formatTimeDayjs(nv.CreateTime).format('YYYY-MM-DD HH:mm:ss')
          this.firstRenderChartFlag = false
          this.getMchInfo()
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    checkEcuInfo(e) {
      this.$api.project
        .SearchGensetConfigureEcuCodeByNowData(
          {},
          // TODO test code
          {
            type: 1,
            // value: '0000B520005'
            value: '000100000001'
          }
        )
        .then((value) => {
          const data = value.Data
          if (data) {
            const ecuCodeData = {
              flag: false,
              dataArr: [],
              dataObj: {}
            }
            if (data.List && data.List.length > 0) {
              ecuCodeData.flag = true
              ecuCodeData.dataArr = data.List
            } else {
              ecuCodeData.flag = false
              ecuCodeData.dataObj = data
            }
            this.ecuCodeData = ecuCodeData
          }
        })
    },
    initData() {
      this.getMchInfo()
    },
    progressFormat(p) {
      return 5 * p + 'kw'
    },
    linkTo(crewId, path) {
      // this.$api.screenData.getsingleMchInfo({ id: crewId }).then((value) => {
      //   const data = value.Data
      //   data && this.toggleSelectCrew(data)
      //   this.$router.push(path)
      // })
      window.open(`/#${path}?` + `type=newPage&crewId=${crewId}`)
    },
    ...mapActions(['toggleSelectCrew']),
    // 查询详细数据
    getMchInfo() {
      this.$api.project
        .getGensetProjectJoinGensetNowData({ ProjectId: this.projectInfo.ProjectId || 3 })
        .then((value) => {
          const data = value.Data
          if (data) {
            this.realTimeData = data
            this.defaultGensetData = data.DefaultGenset
            this.projectInfo.allP = this.defaultGensetData.RatedPower
            this.projectInfo.realP = Number(this.defaultGensetData.PowerSection) || 0
            this.projectInfo.percentageP =
              Number(this.projectInfo.allP) === 0
                ? 0
                : Number(((this.projectInfo.realP * 100) / this.projectInfo.allP).toFixed(0))
            const gensetAlarmList = data.GensetAlarmList
            // single genset
            const projectGensetList = data.ProjectGenSetList
            // combined genset
            const projectGensetParallelList = data.ProjectGenSetParallelList
            if (projectGensetList) {
              const crewList = new Array()
              projectGensetList.forEach((e) => {
                const obj = {
                  ...e,
                  DisplayName: e.GeneratorSet.DisplayName,
                  ...e.GeneratorSet,
                  ...e.NowData
                }
                let statusFlagLabel = ''
                if (obj.fpie_001_11 && obj.fpie_001_11.Value == '1') {
                  statusFlagLabel = '磨合期'
                }
                if (obj.fpie_001_11 && obj.fpie_001_12.Value == '1') {
                  statusFlagLabel = '健康期'
                }
                if (obj.fpie_001_11 && obj.fpie_001_13.Value == '1') {
                  statusFlagLabel = '老化期'
                }
                if (obj.fpie_001_11 && obj.fpie_001_14.Value == '1') {
                  statusFlagLabel = '延寿期'
                }
                obj.statusFlagLabel = statusFlagLabel
                crewList.push(obj)
              })
              this.crewList = crewList
              // TODO 项目信息暂时用机组信息替代
              if (crewList.length > 0) {
                const obj = crewList[0]
                const projectInfo2 = {}
              }
            }
            if (projectGensetParallelList) {
              const crewListMu = new Array()
              projectGensetParallelList.forEach((e) => {
                const aItem = []
                e.ParallelJoinGensetIsMain &&
                  e.ParallelJoinGensetIsMain.forEach((e2) => {
                    const obj = {
                      ...e2,
                      DisplayName: e2.Genset.DisplayName,
                      ...e2.NowData,
                      ...e2.Genset
                    }
                    let statusFlagLabel = ''
                    if (obj.fpie_001_11 && obj.fpie_001_11.Value == '1') {
                      statusFlagLabel = '磨合期'
                    }
                    if (obj.fpie_001_11 && obj.fpie_001_12.Value == '1') {
                      statusFlagLabel = '健康期'
                    }
                    if (obj.fpie_001_11 && obj.fpie_001_13.Value == '1') {
                      statusFlagLabel = '老化期'
                    }
                    if (obj.fpie_001_11 && obj.fpie_001_14.Value == '1') {
                      statusFlagLabel = '延寿期'
                    }
                    obj.statusFlagLabel = statusFlagLabel
                    aItem.push(obj)
                  })
                e.ParallelJoinGensetIsNotMain &&
                  e.ParallelJoinGensetIsNotMain.forEach((e2) => {
                    const obj = {
                      ...e2,
                      DisplayName: e2.Genset.DisplayName,
                      ...e2.NowData,
                      ...e2.Genset
                    }
                    let statusFlagLabel = ''
                    if (obj.fpie_001_11 && obj.fpie_001_11.Value == '1') {
                      statusFlagLabel = '磨合期'
                    }
                    if (obj.fpie_001_12 && obj.fpie_001_12.Value == '1') {
                      statusFlagLabel = '健康期'
                    }
                    if (obj.fpie_001_13 && obj.fpie_001_13.Value == '1') {
                      statusFlagLabel = '老化期'
                    }
                    if (obj.fpie_001_14 && obj.fpie_001_14.Value == '1') {
                      statusFlagLabel = '延寿期'
                    }
                    obj.statusFlagLabel = statusFlagLabel
                    aItem.push(obj)
                  })
                crewListMu.push(aItem)
              })
              this.crewListMu = crewListMu
            }
            if (gensetAlarmList) {
              const alarmList = gensetAlarmList || []
              const remindList = []
              const warningData = []
              const keepData = []
              alarmList.forEach((e, i) => {
                const obj = {
                  order: i,
                  time: formatTimeDayjs(e.Time).format('yyyy-MM-DD HH:mm:ss'),
                  mchname: e.GensetName,
                  divisionmch: '测试分机组',
                  warningcontent: e.Content,
                  warningtype: e.Type,
                  operation: '查看'
                }
                warningData.push(obj)
              })
              remindList.forEach((e, i) => {
                const obj = {
                  order: i,
                  unit: e.GensetName,
                  plandate: formatTimeDayjs(e.Time).format('YYYY-MM-DD'),
                  type: e.Type,
                  operation: '查看',
                  keepContent: e.Content
                }
                keepData.push(obj)
              })
              this.warningDatas = {
                d1: [
                  ...warningData.map((item) => {
                    let msg = '实时报警：' + item.mchname + '&nbsp;-&nbsp;' + item.warningcontent
                    return msg
                  })
                ],
                d2: [
                  ...keepData.map((item) => {
                    let msg = '保养提醒：' + item.unit + item.keepContent
                    return msg
                  })
                ]
              }
            }
          }
          this.$nextTick(() => {
            this.firstRenderChartFlag || (this.init(), (this.firstRenderChartFlag = true))
            this.firstRenderChartFlag && this.refreshData()
          })
        })
    },
    // ECharts 初始化
    init() {
      this.crewList.forEach((item) => {
        const chartDom = document.getElementById(`crew-speed-echart-${item.GeneratorSetId}`)
        const myChart = this.$echarts.init(chartDom)
        this.option_1.series[0].data[0].value = (item.r_032 && item.r_032.Value) || 0
        this.option_1.series[0].detail.formatter = function (v) {
          return v.toFixed(0) + ((item.r_032 && item.r_032.Unit) || '')
        }
        myChart.setOption(this.option_1)
        this.myCharts.push(myChart)
      })
      this.crewListMu.forEach((e) => {
        e.forEach((eS) => {
          const chartDom = document.getElementById(`crew-speed-echart-${eS.GeneratorSetId}`)
          const myChart = this.$echarts.init(chartDom)
          this.option_1.series[0].data[0].value = (eS.r_032 && eS.r_032.Value) || 0
          this.option_1.series[0].detail.formatter = function (v) {
            return v.toFixed(0) + ((eS.r_032 && eS.r_032.Unit) || '')
          }
          myChart.setOption(this.option_1)
          this.myCharts.push(myChart)
        })
      })
    },
    // ECharts 刷新
    refreshData() {
      this.crewList.forEach((item, index) => {
        this.option_1.series[0].data[0].value = (item.r_032 && item.r_032.Value) || 0
        this.myCharts[index].setOption(this.option_1)
      })
      this.crewListMu.forEach((e) => {
        e.forEach((item, index) => {
          this.option_1.series[0].data[0].value = (item.r_032 && item.r_032.Value) || 0
          this.myCharts[index].setOption(this.option_1)
        })
      })
    }
  },
  mounted() {
    if (this.animation) {
      clearInterval(this.animation)
      this.animation = null
    }
    this.animation = setInterval(() => {
      this.times++
      console.info(
        `%crefresh %c${this.times}%c times`,
        'color:green;',
        'color:tomato;font-weight:bold',
        'color:green;'
      )
      this.getMchInfo()
    }, this.refreshInterval)

    this.resizeFun = () => {
      this.myCharts.forEach((item) => {
        item.resize()
      })
    }
    window.addEventListener('resize', this.resizeFun)
  },
  beforeDestroy() {
    clearInterval(this.animation)
    window.removeEventListener('resize', this.resizeFun)
    this.resizeFun = null
  }
}
</script>

<template>
  <div>
    <!-- <BMainTopComponent /> -->
    <div id="project-monitoring-new">
      <!-- <div class="project-info">
        <div class="name">{{ projectInfo.ProjectName }}</div>
        <div class="type">{{ mapping.projectType[Number(projectInfo.ProjectType)] }}</div>
        <div class="status">{{ mapping.projectStatus[Number(projectInfo.Status)] }}</div>
        <div class="time">{{ projectInfo.EndDateLast }}天后到期</div>
        <div class="alert-container">
          <el-carousel
            ref="carousel"
            height="30px"
            :interval="3000"
            direction="vertical"
            indicator-position="none"
          >
            <el-carousel-item
              v-for="(aItem, aIndex) in warningDatas.d1"
              :key="'warningKey' + aIndex"
            >
              <el-row align="middle" justify="start" type="flex">
                <img class="img-warning" src="@/assets/img/screenData/Warning-dataScreen.svg" />
                <span>{{ aItem }}</span>
              </el-row>
              <el-button type="text" style="position: absolute; right: 5px">故障诊断</el-button>
            </el-carousel-item>
            <el-carousel-item
              v-for="(rItem, rIndex) in warningDatas.d2"
              :key="'warningKeys' + rIndex"
            >
              <el-row align="middle" justify="start" type="flex">
                <img class="img-warning" src="@/assets/img/screenData/Repair.svg" />
                <span>{{ rItem }}</span>
                <el-button type="text">故障诊断</el-button>
              </el-row>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="space">&nbsp;</div>
      </div>
      <div class="project-info-sub">
        <div class="items">项目机组功率（Kw）：{{ projectInfo.TotalPower }}</div>
        <div class="items">项目机组数量（台）：{{ projectInfo.Quantity }}</div>
        <div class="items">项目创建时间：{{ projectInfo.timeStr }}</div>
        <div class="items">项目地址：{{ projectInfo.ProjectAddress }}</div>
      </div> -->
      <div class="project-select">
        <project-select></project-select>
      </div>
      <div class="query-box" hidden>
        <el-row>
          <el-col :span="18">
            <el-row type="flex" align="middle">
              <el-col :span="5">
                <div class="a-items">
                  <div class="label">项目工作模式：</div>
                  <div v-if="!editType" class="value">就地并机模式</div>
                  <div v-else class="form-item">
                    <el-select v-model="queryForm.workType">
                      <el-option
                        v-for="item in queryOpt.workType"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      >
                      </el-option
                    ></el-select>
                  </div>
                </div>
              </el-col>
              <el-col :span="5">
                <div class="a-items">
                  <div class="label">云调度模式：</div>
                  <div v-if="!editType" class="value">云端+本地调度</div>
                  <div v-else class="form-item">
                    <el-select v-model="queryForm.dispatchType">
                      <el-option
                        v-for="item in queryOpt.dispatchType"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      >
                      </el-option
                    ></el-select>
                  </div>
                </div>
              </el-col>
              <el-col :span="5">
                <div class="a-items">
                  <div class="label">调度启动台数：</div>
                  <div v-if="!editType" class="value">2</div>
                  <div v-else class="form-item">
                    <el-input-number
                      controls-position="right"
                      :min="0"
                      v-model="queryForm.dispatchNumber"
                    ></el-input-number>
                    <!-- <el-input></el-input> -->
                  </div>
                </div>
              </el-col>
              <el-col :span="9">
                <el-row type="flex" justify="start">
                  <el-button v-show="!editType" @click="editType = true" size="mini" type="primary"
                    >编辑</el-button
                  >
                  <el-button v-show="editType" @click="editType = false" size="mini" type="primary"
                    >取消</el-button
                  >
                  <el-button v-show="editType" @click="editType = false" size="mini" type="primary"
                    >保存</el-button
                  >
                </el-row>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="6">
            <el-row class="time-info" align="middle" type="flex" justify="end">
              数据最近更新时间：{{ nowTime }}
              <svg-icon v-show="false" icon-class="refresh"></svg-icon>
            </el-row>
          </el-col>
        </el-row>
      </div>
      <!-- 统计数据 -->
      <div class="statistical-data-box">
        <el-row type="flex" justify="space-between" align="middle">
          <el-col :span="8" class="data-item-box">
            <el-row type="flex" justify="space-between" align="middle">
              <el-col :span="6" class="data-item">
                <p>{{ realTimeData.GensetAll }}</p>
                <p>机组总数</p>
              </el-col>
              <el-col :span="6" class="data-item">
                <p>{{ realTimeData.GensetSingleCount }}</p>
                <p>单机台数</p>
              </el-col>
              <el-col :span="6" class="data-item">
                <p>{{ realTimeData.GensetParallelCount }}</p>
                <p>并机组数</p>
              </el-col>
              <el-col :span="6" class="data-item">
                <p>{{ realTimeData.GensetCloseCount }}</p>
                <p>合闸总数</p>
              </el-col>
              <el-col :span="6" class="data-item">
                <p>{{ realTimeData.GensetGroupCount }}</p>
                <p>机组群容量</p>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="8" class="data-item-box">
            <el-row type="flex" justify="space-between" align="middle">
              <el-col :span="6" class="data-item color-alarm">
                <p>{{ realTimeData.GensetStatusAlarm }}</p>
                <p>报警</p>
              </el-col>
              <el-col :span="6" class="data-item color-warning">
                <p>{{ realTimeData.GensetStatusWarn }}</p>
                <p>警告</p>
              </el-col>
              <el-col :span="6" class="data-item color-run">
                <p>{{ realTimeData.GensetStatusRun }}</p>
                <p>运行</p>
              </el-col>
              <el-col :span="6" class="data-item color-stop">
                <p>{{ realTimeData.GensetStatusStop }}</p>
                <p>停机</p>
              </el-col>
              <el-col :span="6" class="data-item color-offline">
                <p>{{ realTimeData.GensetStatusOffline }}</p>
                <p>离线</p>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="8" class="data-item-box">
            <el-row type="flex" justify="space-between" align="middle">
              <!-- <el-col :span="6" class="data-item">
                <p>{{ defaultGensetData.ElectricityTotal }}h</p>
                <p>运行时长</p>
              </el-col> -->
              <div title="" class="item">
                <p>- h</p>
                <p>平均无故障时长</p>
              </div>
              <div title="=机组超功率运行时长之和" class="item">
                <p>- h</p>
                <p>超功率运行时长</p>
              </div>
              <div title="=机组平均负荷率之和/机组数" class="item">
                <p>-</p>
                <p>平均负荷率</p>
              </div>
              <div title="=机组平均负荷之和/机组数量" class="item">
                <p>-</p>
                <p>平均负荷</p>
              </div>
              <div title="=机组负荷强度之和/机组数量" class="item">
                <p>-</p>
                <p>负荷强度</p>
              </div>
            </el-row>
          </el-col>
        </el-row>
      </div>
      <div class="middle">
        <div class="progress">
          <div class="a-progress">
            <div title="=机组功率之和" class="label">
              项目总功率：{{ projectInfo.allP || '&nbsp;-&nbsp;' }}Kw(测试，接口获取)
            </div>
            <div title="=机组实时功率之和/机组总功率之和" class="label2">实时负荷率：-</div>
            <div class="chart">
              <div class="inner" :style="{ width: '40%' }"></div>
            </div>
            <div class="real-label">
              <span
                :style="{
                  left: '30%'
                }"
                >实际功率：{{ '108' }}Kw(测试)</span
              >
            </div>
          </div>
          <div class="a-progress" hidden>
            <div class="label">项目总负荷率：0%</div>
            <div class="chart">
              <div class="inner" style="width: 0%; background: #84d4f7"></div>
            </div>
            <div class="real-label">
              <span
                :style="{
                  color: '#84d4f7',
                  left:
                    defaultGensetData.Fpie_002_05 === 0
                      ? 0
                      : defaultGensetData.fpie_002_05 - 12 + '%'
                }"
                >实际负荷率：{{ defaultGensetData.Fpie_002_05 || '&nbsp;-&nbsp;' }}%</span
              >
            </div>
          </div>
          <div class="all-time">
            <div title="机组运行时长之和" class="label">
              项目累计运行时长：{{ defaultGensetData.RunHourTotal }}h(测试，接口获取)
            </div>
            <div class="chart">
              <!-- 斜角边显示方案：（进度条 inner1 长度小于17px时出现显示问题）
              1. 数据区间分段展示； ✔
