                      <span slot="reference" class="check-ecu" @click="checkEcuInfo(e)"
                        >查看故障详情(测试)</span
                      >
                    </el-popover>
                  </el-col>
                </el-row>
              </div>
              <div class="split"></div>
              <div class="mu-crew-info-item-one-sub2">
                <el-row>
                  <el-col :span="12"
                    >开始前油量：{{
                      (e.fpie_003_08 && e.fpie_003_08.Value) || '&nbsp;-&nbsp;'
                    }}L</el-col
                  >
                  <el-col :span="12"
                    >结束时油量：{{
                      (e.fpie_003_09 && e.fpie_003_09.Value) || '&nbsp;-&nbsp;'
                    }}L</el-col
                  >
                  <el-col :span="12"
                    >加油次数：{{
                      (e.fpie_003_07 && e.fpie_003_07.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                  <el-col :span="12"
                    >累计燃油：{{ (e.r_064 && e.r_064.Value) || '&nbsp;-&nbsp;' }}</el-col
                  >
                  <!-- <el-col :span="12">油耗速度：{{ (e.r_063 && e.r_063.Value) || '&nbsp;-&nbsp;' }}</el-col> -->
                  <el-col :span="12"
                    >平均能耗：{{
                      (e.fpie_003_18 && e.fpie_003_18.Value) || '&nbsp;-&nbsp;'
                    }}g/Kwh</el-col
                  >
                  <el-col :span="12"
                    >运行时长：{{
                      (e.fpie_003_02 && e.fpie_003_02.Value) || '&nbsp;-&nbsp;'
                    }}h</el-col
                  >
                  <el-col :span="12"
                    >超功率运行时长：{{
                      (e.fpie_003_19 && e.fpie_003_19.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                </el-row>
              </div>
              <div class="split"></div>
              <div class="mu-crew-info-item-one-sub3">
                <el-row>
                  <el-col :span="12">最大功率：{{ e.RatedPower }}Kw</el-col>
                  <el-col :span="12">限制功率：{{ e.PowerSection || 0 }}Kw</el-col>
                  <el-col :span="12"
                    >实时功率：{{ (e.r_032 && e.r_032.Value) || '&nbsp;-&nbsp;' }}Kw</el-col
                  >
                  <el-col :span="12"
                    >功率因数：cosδ={{ (e.r_044 && e.r_044.Value) || '&nbsp;-&nbsp;' }}</el-col
                  >
                  <el-col :span="12"
                    >APF功率因数：{{
                      (e.fpie_002_11 && e.fpie_002_11.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                  <el-col :span="12"
                    >频率：{{ (e.r_019 && e.r_019.Value) || '&nbsp;-&nbsp;' }}Hz</el-col
                  >
                  <el-col :span="12"
                    >相电压：{{ (e.r_013 && e.r_013.Value) || '&nbsp;-&nbsp;' }}V</el-col
                  >
                  <el-col :span="12"
                    >线电压：{{ (e.r_010 && e.r_010.Value) || '&nbsp;-&nbsp;' }}V</el-col
                  >
                  <el-col :span="12"
                    >线电流：{{ (e.r_025 && e.r_025.Value) || '&nbsp;-&nbsp;' }}A</el-col
                  >
                  <el-col :span="12"
                    >电度：{{ (e.r_076 && e.r_076.Value) || '&nbsp;-&nbsp;' }}kWh</el-col
                  >
                  <el-col :span="12"
                    >负载谐波含量：{{
                      (e.fpie_002_09 && e.fpie_002_09.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                  <el-col :span="12">总谐波畸变率：{{ '--' }}</el-col>
                  <el-col :span="12"
                    >三相不平衡：{{
                      (e.fpie_002_12 && e.fpie_002_12.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                </el-row>
              </div>
              <div class="split"></div>
              <div class="mu-crew-info-item-one-sub4">
                <el-row>
                  <el-col :span="12"
                    >负荷强度：{{
                      (e.fpie_003_11 && e.fpie_003_11.Value) || '&nbsp;-&nbsp;'
                    }}Hz</el-col
                  >
                  <el-col :span="12"
                    >实时负荷率：{{
                      (e.fpie_002_05 && e.fpie_002_05.Value) || '&nbsp;-&nbsp;'
                    }}V</el-col
                  >
                  <el-col :span="12"
                    >平均负荷：{{
                      (e.fpie_003_10 && e.fpie_003_10.Value) || '&nbsp;-&nbsp;'
                    }}V</el-col
                  >
                  <el-col :span="12"
                    >平均负荷率：{{
                      (e.fpie_002_06 && e.fpie_002_06.Value) || '&nbsp;-&nbsp;'
                    }}</el-col
                  >
                  <el-col :span="12"
                    >低负荷时长：{{
                      (e.fpie_003_16 && e.fpie_003_16.Value) || '&nbsp;-&nbsp;'
                    }}min</el-col
                  >
                  <el-col :span="12"
                    >高负荷时长：{{
                      (e.fpie_003_17 && e.fpie_003_17.Value) || '&nbsp;-&nbsp;'
                    }}min</el-col
                  >
                  <el-col :span="12"
                    >有功负荷强度：{{
                      (e.fpie_002_15 && e.fpie_002_15.Value) || '&nbsp;-&nbsp;'
                    }}%</el-col
                  >
                  <el-col :span="12"
                    >无功负荷强度：{{
                      (e.fpie_002_16 && e.fpie_002_16.Value) || '&nbsp;-&nbsp;'
                    }}%</el-col
                  >
                </el-row>
                <div class="btn-box">
                  <el-button
                    @click="linkTo(e.GeneratorSetId, '/equipment/crewmonitor/crew-monitor-info')"
                    class="btn"
                    >机组监控</el-button
                  >
                  <el-popover placement="top" trigger="click">
                    <div>
                      <el-button
                        @click="
                          linkTo(
                            e.GeneratorSetId,
                            '/operation/operationalChart/operationalChartDaily'
                          )
                        "
                        style="margin: 0 0 10px 0"
                        >日运维图</el-button
                      ><br />
                      <el-button
                        @click="
                          linkTo(
                            e.GeneratorSetId,
                            '/operation/operationalChart/operationalChartWeekly'
                          )
                        "
                        >周运维图</el-button
                      >
                    </div>
                    <el-button slot="reference">运维图</el-button>
                  </el-popover>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 机组详细数据 end -->
    </div>
  </div>
</template>
<style lang="less" scoped>
@import '~@/styles/variables.less';
@borderColor: #d7d7d7;
// 单机组数据信息展示面板高度
@crewHeight: 850px;
#project-monitoring-new {
  overflow-x: hidden;
  // margin-bottom: 24px;
  // display: initial !important;
  /deep/ .el-progress__text {
    color: #fff;
  }
  .check-ecu {
    cursor: pointer;
    color: #1ba8ee;
  }
  .project-info {
    display: flex;
    align-items: center;
    > div {
      margin: 0 10px 10px 0;
    }
    .name {
      font-size: 1.3rem;
      font-weight: bold;
    }
    .type {
      color: #000;
      padding: 2px 10px;
      border-radius: 5px;
      background: #d7d7d7;
    }
    .status {
      color: #000;
      padding: 2px 10px;
      border-radius: 5px;
      background: #d7d7d7;
    }
    .time {
      padding: 2px 10px;
      border-radius: 5px;
      background: #f39931;
    }
    .alert-container {
      flex: 1;
      height: 30px;
      border: 1px solid #fff;
      .img-warning {
        width: 20px;
        margin: 0 5px 0 10px;
      }
      /deep/ .el-carousel__item {
        display: flex;
      }
    }
    .space {
      flex: 1;
    }
  }
  .project-info-sub {
    display: flex;
    font-size: 0.8rem;
    > div {
      margin: 0 20px 20px 0;
    }
  }
  .project-select {
    /deep/ .project-select {
      .title {
        display: none;
      }
      .value {
        display: none;
      }
      .items {
        margin: 0 0 10px 0;
      }
    }
  }
  .query-box {
    /deep/ .el-input-number {
      width: 140px;
    }
    > div {
      display: flex;
      align-items: center;
      padding: 10px 0 20px 0;
    }
    .a-items {
      display: flex;
      align-items: center;
      .label {
        white-space: nowrap;
        &::before {
          content: '*';
          color: tomato;
        }
      }
    }
    .time-info {
      svg {
        font-size: 2rem;
        cursor: pointer;
      }
    }
  }
  // background-color: #f3f3f3;
  .statistical-data-box {
    margin-bottom: 16px;
    .data-item-box {
      height: 70px;
      border: 1px solid #ebeef5;
      &:not(:last-child) {
        margin-right: 16px;
      }
      > .el-row {
        height: 100%;
        > .item {
          width: 20%;
          text-align: center;
          p {
            font-size: 0.9rem;
            white-space: nowrap;
          }
        }
      }
    }
    .data-item {
      p {
        text-align: center;
      }
    }

    .color-alarm p {
      color: #d5cd91;
    }
    .color-warning {
      color: #f0c740;
    }
    .color-run {
      color: #1de9b6;
    }
    .color-stop {
      color: #ec807a;
    }
    .color-offline {
      color: #d7d7d7;
    }
    .crew-status-num {
      > .el-col {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        > span:first-child {
          width: 20px;
          height: 20px;
          display: inline-block;
        }
      }
    }
  }
  .middle {
    display: flex;
    padding: 30px 0;
    border: 1px solid #fff;
    .progress {
      flex: 3;
      .a-progress {
        position: relative;
        padding: 0 50px 40px 30px;
        .label2 {
          top: 0;
          right: 50px;
          position: absolute;
        }
        .chart {
          height: 17px;
          margin: 4px 0;
          background: gray;
          .inner {
            height: 100%;
            background: #2ee9b7;
            border-end-end-radius: 10px;
            border-start-end-radius: 10px;
          }
        }
        .real-label {
          width: 100%;
          position: relative;
          span {
            color: #2ee9b7;
            position: absolute;
            white-space: nowrap;
          }
        }
      }
      .all-time {
        padding: 0 50px 20px 30px;
        .chart {
          height: 17px;
          display: flex;
          margin: 4px 0;
          position: relative;
          .inner1 {
            height: percentage(1);
            background: #d7d7d7;
          }
          .tri {
            top: -17px;
            border: 17px solid;
            border-color: transparent transparent #d7d7d7 transparent;
            z-index: 999;
            position: absolute;
          }
          .inner2 {
            height: percentage(1);
            background: #70d8c5;
          }
          .tri2 {
            top: -17px;
            border: 17px solid;
            border-color: transparent transparent #70d8c5 transparent;
            z-index: 999;
            position: absolute;
          }
          .inner3 {
            height: percentage(1);
            background: #f9cd94;
          }
        }
        .real-label {
          font-size: 0.8rem;
          position: relative;
          .label1 {
            color: #aaaa95;
          }
          .label2 {
            right: 0;
            color: #e56648;
            position: absolute;
          }
        }
      }
    }
    .progress-chart {
      flex: 2;
      position: relative;
      .effective-power-box {
        margin-right: 40px;
      }
      .invalid-power-box {
        margin-left: 40px;
      }
      .target-power-box {
        width: 100%;
        bottom: 22px;
      }
      .inner-label {
        position: absolute;
        bottom: 50px;
      }
      span {
        flex: 1;
        height: 1px;
        margin: 0 20%;
        display: inline-block;
        border-top: 1px dashed #1abc9c;
      }
    }
    .triangle {
      @width: 120px;
      @width-border: 70px;
      flex: 2;
      height: 100px;
      color: white;
      font-size: 0.8rem;
      transform: translateX(50px);
      .left {
        position: absolute;
        font-size: 1.1rem;
        p {
          margin: 0 0 10px 0;
        }
      }
      .right {
        position: absolute;
        left: 150px;
      }
      .top {
        height: 0;
        width: @width;
        display: flex;
        border: @width-border solid;
        align-items: center;
        justify-content: center;
        border-color: #f39931 transparent transparent transparent;
        > div {
          transform: translateY(-40px);
          > div {
            text-align: center;
          }
        }
      }
      .bottom {
        width: 0;
        height: 0;
        display: flex;
        border: calc(@width / 2) solid;
        align-items: center;
        justify-content: center;
        transform: translate(@width-border, -@width-border);
        border-color: #1ba8ee transparent transparent transparent;
        > div {
          transform: translateY(-40px);
          > div {
            text-align: center;
          }
        }
      }
      .label-box {
        transform: translate(120px, -140px);
      }
    }
  }
  .crewInfo-box {
    display: flex;
    margin: 30px 0 0 0;
    overflow-x: hidden;
    padding-bottom: 20px;
    justify-content: flex-start;
    &:hover {
      overflow-x: auto;
    }
    .crewInfo-item {
      width: 240px;
      display: flex;
      min-width: 240px;
      flex-direction: column;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    .crew-type {
      width: 100%;
      color: #fff;
      text-align: center;
      border-radius: 4px;
      background-color: @theme;
    }
    .mu-crew-info-item {
      display: flex;
      margin: 0 10px 0 0;
      &-one {
        > div:last-child {
          height: @crewHeight;
          padding: 10px 15px;
          border: 1px solid #ffffff80;
        }
        width: 350px;
        .split {
          width: 100%;
          height: 1px;
          border-top: 1px solid #ffffff80;
        }
        &-title {
          display: flex;
          margin: 0 0 10px 0;
          > div {
            height: 25px;
            line-height: 25px;
          }
          .name {
            width: 99%;
            overflow: hidden;
            font-size: 1.2rem;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
          }
          .status {
            color: #000;
            margin: 0 20px;
            padding: 0 5px;
            border-radius: 4px;
            background: #1abc9c;
          }
          .type {
            padding: 0 5px;
            border-radius: 4px;
            border: 1px solid #fff;
          }
        }
        &-sub-label {
          .btn {
            display: flex;
            padding: 0 5px;
            border-radius: 4px;
            justify-content: center;
            border: 1px solid #fff;
          }
        }
        &-sub-info {
          .info {
            margin: 8px 0;
          }
        }
        &-sub1 {
          padding: 0 0 10px 0;
          .text {
            /deep/ .el-col {
              white-space: nowrap;
              padding: 10px 0 0 0;
            }
          }
        }
        &-sub2 {
          .el-row {
            padding: 0 0 10px 0;
            .el-col {
              white-space: nowrap;
              padding: 10px 0 0 0;
            }
          }
        }
        &-sub3 {
          .el-row {
            padding: 0 0 10px 0;
            .el-col {
              white-space: nowrap;
              padding: 10px 0 0 0;
            }
          }
        }
        &-sub4 {
          .el-row {
            padding: 0 0 10px 0;
            .el-col {
              white-space: nowrap;
              padding: 10px 0 0 0;
            }
          }
          .btn-box {
            display: flex;
            position: relative;
            justify-content: center;
            .btn {
              left: 2px;
              position: absolute;
            }
            span {
              right: 10px;
              position: absolute;
            }
          }
        }
      }
      > div:first-child {
        border-left: 0;
      }
      > div:last-child {
        border-right: 0;
      }
    }
  }
}
.crew-info-echarts {
  display: flex;
  height: 170px;
  justify-content: center;
  .crew-speed-echart {
    width: 45%;
  }
  .crew-info-power {
    flex: 1;
    height: 150px;
    display: flex;
    position: relative;
    justify-content: flex-start;
    .target-power-box {
      z-index: 1;
      width: 110%;
      display: flex;
      font-size: 12px;
      margin-top: 10%;
      position: absolute;
      align-items: center;
      em {
        display: inline-block;
        &.left-text {
          padding-left: 4px;
        }
        &.right-text {
          padding-right: 4px;
        }
      }
      span {
        flex: 1;
        height: 1px;
        margin: 0 4px;
        display: inline-block;
        border-top: 1px dashed #1abc9c;
      }
    }
    .progress-box {
      width: 50%;
      font-size: 12px;
      position: relative;
      /deep/.el-progress {
        top: 55px;
        width: 126px;
        height: 15px;
        position: absolute;
        transform: rotateZ(-90deg);
        .el-progress-bar {
          padding-right: 24px;
          margin-right: -40px;
        }
        .el-progress__text {
          position: absolute;
          transform: rotateZ(90deg);
        }
      }
      > p {
        bottom: 0;
        font-weight: 700;
        position: absolute;
      }
    }
    .effective-power-box {
      margin-right: 10px;
      /deep/.el-progress {
        right: -40px;
      }
      > p {
        right: 0;
      }
    }
    .invalid-power-box {
      /deep/.el-progress {
        left: -40px;
      }
      > p {
        left: 0;
      }
    }
  }
}
.crew-info-box {
  // min-height: 500px;
  margin-bottom: 16px;
  padding: 4px;
  border: 1px solid;
  border-radius: 4px;
  font-size: 12px;
  .crew-info-item {
    line-height: 20px;
    padding: 6px 0;
    &:not(:last-child) {
      border-bottom: 1px solid @borderColor;
    }
  }
  .crew-info-item1 {
    > div {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .tip {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        margin: 0 8px 0 16px;
      }
      .name {
        flex: 1;
      }
      .type {
        display: inline-block;
        padding: 0px 8px;
        border: 1px solid @borderColor;
        border-radius: 4px;
      }
    }
    p {
      padding-left: 36px;
    }
  }
  .crew-info-item2,
  .crew-info-item3,
  .crew-info-item5 {
    p {
      display: flex;
      span {
        width: 50%;
        margin-right: 20px;
        font-weight: 700;
        text-align: right;
      }
      em {
        width: 50%;
      }
    }
  }
  .crew-info-item5 {
    min-height: 152px;
  }
}
</style>
<style lang="less" scoped>
@import '~@/styles/variables.less';
// 机组状态对应标签颜色设置
.crew-status-num {
  // 运行
  .color_type_4 {
    color: @crew_status_4;
    span {
      background: @crew_status_4;
    }
  }
  // 报警
  .color_type_2 {
    color: @crew_status_2;
    span {
      background: @crew_status_2;
    }
  }
  // 警告
  .color_type_3 {
    color: @crew_status_3;
    span {
      background: @crew_status_3;
    }
  }
  // 停机
  .color_type_5 {
    color: @crew_status_5;
    span {
      background: @crew_status_5;
    }
  }
  // 离线
  .color_type_6 {
    color: @crew_status_6;
    span {
      background: @crew_status_6;
    }
  }
  // 全部
  .color_type_0 {
    color: @crew_status_0;
    span {
      background: @crew_status_0;
    }
  }
}
.crew-info-box {
  // 运行
  &.color_type_4 {
    border-color: @crew_status_4 !important;
    span.tip {
      background: @crew_status_4;
    }
  }
  // 报警
  &.color_type_2 {
    border-color: @crew_status_2 !important;
    span.tip {
      background: @crew_status_2;
    }
  }
  // 警告
  &.color_type_3 {
    border-color: @crew_status_3 !important;
    span.tip {
      background: @crew_status_3;
    }
  }
  // 停机
  &.color_type_5 {
    border-color: @crew_status_5 !important;
    span.tip {
      background: @crew_status_5;
    }
  }
  // 离线
  &.color_type_6 {
    border-color: @crew_status_6 !important;
    span.tip {
      background: @crew_status_6;
    }
  }
}
</style>



