<template>
  <div>
    <el-dialog
      :title="title"
      class="progress-record-detail-dialog"
      width="60%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <div class="dialog-content">
        <!-- 添加 -->
        <div class="operation-box" v-if="operateType === 1">
          <div class="box-left"></div>
          <div class="box-right">
            <!-- <el-button type="primary" @click="downloadTemplate()">下载模板</el-button> -->
            <!-- <el-button type="primary" @click="uploadFile()">上传并解析</el-button> -->
          </div>
        </div>
        <!-- 操作按钮 -->
        <div class="operation-box" v-if="operateType === 4">
          <div class="box-left">
            <!-- <el-button type="primary" @click="downloadTemplate()">下载模板</el-button> -->
            <!-- <el-button type="primary" @click="uploadFile()">上传并解析</el-button> -->
          </div>
          <div class="box-right">
            <el-button type="primary" @click="handleCancelClick()">取消</el-button>
            <el-button type="primary" @click="editSave()">保存</el-button>
          </div>
        </div>
        <div class="operation-box" v-if="operateType == 2">
          <div class="box-left">
            <el-button type="primary" @click="handleBackList()">返回列表</el-button>
            <!-- <el-button type="primary" @click="handleAppPreview()">app预览</el-button> -->
          </div>
          <div class="box-right">
            <el-button type="primary" @click="downloadFile()">下载</el-button>
          </div>
        </div>
        <!-- 记录表 详情 -->
        <ProjectRecordSheet
          ref="ProjectRecordSheet"
          id="ProjectRecordSheet"
          v-model="dataList"
          :edit="isEdit"
        ></ProjectRecordSheet>
      </div>
      <!-- <div slot="footer" class="dialog-footer" v-if="operateType == 2">
        <el-button type="primary" @click="close()">确 定</el-button>
      </div> -->
      <div slot="footer" class="dialog-footer" v-if="operateType == 1">
        <el-button type="primary" @click="close()">取消</el-button>
        <el-button type="primary" @click="addSave()">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import ProjectRecordSheet from '../../projectRecordSheet.vue'
import data from '@/views/projectManage/components/data.js'
const { ProjectTypeEnum } = data
import { htmlToPdf } from '@/utils/util'

export default {
  name: 'RecordFormDialog',
  components: {
    ProjectRecordSheet
  },
  props: {
    ProjectProgressId: {
      type: Number,
      default: null
    },
    RecordId: {
      type: Number,
      default: null
    },
    OperateType: {
      type: Number,
      default: null
    },
    GensetList: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapGetters(['currentProject']),
    title() {
      let str = ''
      switch (this.operateType) {
        case 1:
          str = '新增'
          break
        case 2:
          str = '查看'
          break
        case 4:
          str = '编辑'
          break
        default:
          str = ''
      }
      return `${this.ProjectProgressId == 13 ? '客户满意度' : '完工报告'}${str}`
    },
    isEdit() {
      return this.operateType == 1 || this.operateType == 4 ? true : false
    }
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          this.$set(this, 'ProjectId', nv.ProjectId)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,

      ProjectId: parseInt(this.$route.query.ProjectId),
      IsGenset: this.$route.query.IsGenset,
      IsGensetMult: this.$route.query.IsGensetMult,

      // operateType 操作类型 {1：项目-添加, 2: 项目-查看, 3: 单机-查看, 4:项目-编辑}
      operateType: this.OperateType,
      // 记录表数据
      dataList: [],

      ProjectTypeEnum
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    console.log(
      this.ProjectProgressId,
      this.RecordId,
      this.OperateType,
      this.GensetList,
      this.ProjectId
    )
    if (!this.RecordId) {
      // 添加
      this.GetProgressStencil()
    } else {
      // 查看、编辑
      this.getProgressFormData()
    }
  },
  methods: {
    // 获取模板
    GetProgressStencil() {
      const params = {
        ProgressId: this.ProjectProgressId,
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectProgressStencilProject(params).then((res) => {
        if (res.Code === 1) {
          const projectInfo = res.Data.ProjectDetail
          const data = {
            ProjectDetail: {
              ...projectInfo,
              ProjectTypeStr: projectInfo.ProjectType
                ? this.ProjectTypeEnum.find((item) => item.value == projectInfo.ProjectType).label
                : '',
              StartDateStr: this.$moment(projectInfo.StartDate).format('YYYY-MM-DD'),
              EndDateStr: this.$moment(projectInfo.EndDate).format('YYYY-MM-DD')
            },
            ...res.Data.StencilDetail
          }
          data.StencilList = _.cloneDeep(data.StencilModel)
          data.ItemList = _.cloneDeep(data.ItemModel) || [{}]

          console.log(res, data)

          if (data.IsGenset) {
            // 绑定机组
            data.GensetList = _.cloneDeep(this.GensetList)
          }
          this.$set(this, 'dataList', _.cloneDeep(data))
        }
      })
    },
    // 查看 获取表单数据
    getProgressFormData() {
      const query = {
        RecordId: this.RecordId
      }
      this.$api.project.GetGensetProjectProgressRecordDetail({}, query).then((res) => {
        if (res.Code === 1) {
          console.log('bianji', res)

          const data = res.Data
          this.$set(
            data,
            'GensetList',
            data.GensetList
              ? data.GensetList.map((item) => {
                  return {
                    RecordJoinGensetId: item.RecordJoinGensetId,
                    ...item.GeneratorSet
                  }
                })
              : []
          )
          // 项目信息
          this.$set(data, 'ProjectDetail', {
            ...data.ProjectDetail,
            ProjectTypeStr: data.ProjectDetail.ProjectType
              ? this.ProjectTypeEnum.find((item) => item.value == data.ProjectDetail.ProjectType)
                  .label
              : '',
            StartDateStr: this.$moment(data.ProjectDetail.StartDate).format('YYYY-MM-DD'),
            EndDateStr: this.$moment(data.ProjectDetail.EndDate).format('YYYY-MM-DD')
          })

          this.$nextTick(() => {
            this.$set(this, 'dataList', _.cloneDeep(data))
          })
        }
      })
    },
    close() {
      this.$emit('closeDialog')
    },

    // 新建保存
    addSave() {
      this.$refs.ProjectRecordSheet.$refs.generateSheet.getData().then((data) => {
        console.log('formdata-----------', this.dataList, data)
        let params = {
          ProjectId: this.ProjectId,
          ProgressId: this.ProjectProgressId,
          IsGenset: this.dataList.IsGenset,
          GensetList: this.GensetList
            ? this.GensetList.map((item) => {
                return {
                  GeneratorSetId: item.GeneratorSetId
                }
              })
            : [],
          FileUrl: '',
          StencilList: this.dataList.StencilModel ? _.cloneDeep(this.dataList.StencilModel) : null,
          ItemList: data[`key_table`] || this.dataList.ItemModel
        }
        params.StencilList &&
          params.StencilList.forEach((item) => {
            item[`KeyValue`] = data[`key_${item.KeyId}`]
          })

        if (data[`key_image`]) {
          params.FileUrl = data[`key_image`].map((item) => item.url).join(';')
        }
        console.log('params', params)

        this.$api.project.AddGensetProjectProgressRecord(params).then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$emit('closeDialog', { refresh: true })
          }
        })
      })
    },
    // 编辑-保存
    editSave() {
      const query = {
        id: this.RecordId
      }
      this.$refs.ProjectRecordSheet.$refs.generateSheet.getData().then((data) => {
        console.log('formdata-----------', this.dataList, data)
        let params = {
          ProjectId: this.ProjectId,
          ProgressId: this.ProjectProgressId,
          IsGenset: this.dataList.IsGenset,
          IsGensetMult: this.dataList.IsGensetMult,
          GensetList: this.dataList.GensetList.map((item) => {
            return {
              GeneratorSetId: item.GeneratorSetId
            }
          }),
          FileUrl: '',
          StencilList: this.dataList.StencilList ? _.cloneDeep(this.dataList.StencilList) : null,
          ItemList: data[`key_table`] || this.dataList.ItemModel
        }
        params.StencilList &&
          params.StencilList.forEach((item) => {
            item[`KeyValue`] = data[`key_${item.KeyId}`]
          })

        if (data[`key_image`]) {
          params.FileUrl = data[`key_image`].map((item) => item.url).join(';')
        }
        console.log('params', params)

        this.$api.project.EditGensetProjectProgressRecord(params, query).then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$emit('closeDialog', { refresh: true })
          }
        })
      })
    },
    // 取消
    handleCancelClick() {
      this.close()
      // this.operateType = 2
      // this.getProgressFormData()
    },
    // 下载模板
    downloadTemplate() {},
    // 上传并解析
    uploadFile() {},

    // 返回列表
    handleBackList() {
      this.close()
    },
    // 下载
    downloadFile() {
      const props = {
        title: '页面导出PDF文件名',
        contentId: '#ProjectRecordSheet' //this.$refs.ProjectRecordSheet
      }
      htmlToPdf(props)
    },
    // app 预览
    handleAppPreview() {
      const link = this.$router.resolve({
        path: '/project/app-record-form',
        query: { RecordId: this.RecordId }
      })
      window.open(link.href, '_blank')
    }
  }
}
</script>

<style lang="less" scoped>
.progress-record-detail-dialog {
  /deep/.el-dialog .el-dialog__body {
    padding: 25px;
  }
  .operation-box {
    margin-bottom: 16px;
    display: flex;
    .box-left,
    .box-right {
      width: 50%;
    }
    .box-right {
      text-align: right;
    }
  }
}
.theme-dark #ProjectRecordSheet {
  background-color: #102447;
}
</style>



