<!--
 * @Autor: linxu
 * @Date: 2022-03-04 16:45:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-24 10:25:15
 * @Description: 添加/编辑 人员弹窗
-->
<template>
  <el-dialog
    :title="personnelInfo ? '编辑人员' : '添加人员'"
    class="personnel-addedit-dialog"
    width="400px"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <el-form :model="form" :rules="rules" ref="projectForm" :label-width="formLabelWidth">
      <el-form-item label="公司类型" prop="CustomerType">
        <el-select v-model="form.CustomerType" placeholder="请选择公司类型">
          <!-- <el-option label="请选择" value=""></el-option> -->
          <el-option
            v-for="item in CustomerTypeEnum"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="公司名称" prop="CustomerId">
        <el-select
          v-model="form.CustomerId"
          filterable
          placeholder="请选择公司"
          @change="customerChange"
          :disabled="personnelInfo"
        >
          <!-- <el-option label="请选择" value=""></el-option> -->
          <el-option
            v-for="item in customerList"
            :key="item.ID"
            :label="item.CompanyName"
            :value="item.ID"
          ></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="公司地址" prop="CustomerAddress">
        <el-input
          v-model="form.CustomerAddress"
          placeholder="不需填写。与公司关联"
          autocomplete="off"
          disabled
        ></el-input>
      </el-form-item> -->
      <!-- <el-divider class="form-divider"></el-divider> -->
      <!-- <el-form-item label="人员类型" prop="UserType">
        <el-select v-model="form.UserType" placeholder="请选择人员类型">
          <el-option
            v-for="(item, index) in UserTypeEnum"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item> -->
      <el-form-item label="负责人" prop="UserId">
        <el-select
          v-model="form.UserId"
          filterable
          :disabled="personnelInfo ? personnelInfo : !form.CustomerId"
          placeholder="请选择负责人"
        >
          <!-- <el-option label="请选择" value=""></el-option> -->
          <el-option
            v-for="item in userList"
            :key="item.ID"
            :label="item.DisplayName"
            :value="item.ID"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('projectForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('projectForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'AddOrEditPersonnelDialog',
  props: {
    CustomerTypeEnum: Array,
    UserTypeEnum: Array,
    projectId: {
      type: Number,
      default: null
    },
    personnelInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      dialigVisible: true,
      formLabelWidth: '100px',
      form: {
        CustomerType: '',
        CustomerId: '',
        CustomerAddress: '',
        // UserType: '',
        UserId: ''
      },
      rules: {
        CustomerType: [{ required: true, message: '请选择公司类型', trigger: 'change' }],
        CustomerId: [{ required: true, message: '请选择公司', trigger: 'change' }],
        // UserType: [{ required: true, message: '请选择人员类型', trigger: 'change' }],
        UserId: [{ required: true, message: '请选择负责人', trigger: 'change' }]
      },
      customerList: [], // 公司列表
      userList: [] // 用户列表
    }
  },
  mounted() {
    this.getAllCustomers()
    if (this.personnelInfo) {
      this.form = Object.assign({}, this.personnelInfo, {
        CustomerId: this.personnelInfo.CustomerID
      })
      this.getAllUsers(this.personnelInfo.CustomerID)
    }
  },
  methods: {
    // 获取公司列表
    getAllCustomers() {
      this.$api.project.getAllCustomers().then((res) => {
        if (res.Code === 1) {
          this.customerList = res.Data
          console.log(this.customerList)
        }
      })
    },
    // 切换选中公司
    customerChange(val) {
      this.form.CustomerAddress = this.customerList.find((item) => item.ID == val).Address
      this.form.UserId = ''
      this.getAllUsers()
    },
    // 获取用户列表
    async getAllUsers(id) {
      const query = {
        customerId: id || this.form.CustomerId,
        departmentId: 0
      }
      const params = {
        DisplayMode: 1,
        IsContainSelf: true
      }
      this.$api.project.GetUsersV1(params, query).then((res) => {
        if (res.Code === 1) {
          this.userList = res.Data
        }
      })
    },
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.personnelInfo) {
            this.editUser()
            return
          }
          this.addUser()
        } else {
          return false
        }
      })
    },
    // 添加
    addUser() {
      const params = {
        ...this.form,
        ProjectId: this.projectId
      }
      this.$api.project.AddGensetProjectJoinUser(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '添加用户成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '添加用户失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    // 编辑
    editUser() {
      const params = {
        // ...this.form,
        CustomerType: this.form.CustomerType,
        // UserType: this.form.UserType,
        ProjectId: this.projectId
      }
      const query = { id: this.personnelInfo.ID }
      this.$api.project.EditGensetProjectJoinUser(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '编辑用户成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '编辑用户失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.personnel-addedit-dialog {
  // width: 400px;
  .el-form {
    .el-select {
      width: 100%;
    }
  }
  .form-divider {
    width: 400px;
    margin-left: -25px;
  }
}
</style>



