<!--
 * @Autor: linxu
 * @Date: 2022-03-21 09:08:50
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:14:14
 * @Description: 完工报告/客户满意度
-->
<template>
  <div class="project-completion">
    <p class="content-title">完工报告</p>
    <!-- 完工报告表格 -->
    <TableList
      :options="options"
      :columns="CompletionReportColumns"
      :dataSource="CompletionReportData"
      border
    >
      <el-table-column
        slot="operation"
        label="操作"
        align="center"
        v-bind="options && options.props"
        v-loading="loading"
      >
        <template slot-scope="scope">
          <div v-if="scope.row.RecordCount == 0" class="operate-group">
            <el-button type="text" @click="handleAdd(scope.row)"> 添加 </el-button>
          </div>
          <div v-else class="operate-group">
            <el-button type="text" @click="handleShow(scope.row)"> 查看 </el-button>
            <el-button type="text" @click="handleEdit(scope.row)"> 编辑 </el-button>
            <!-- <el-button type="text" @click="handleDownloadClick(scope.row)"> 下载 </el-button> -->
          </div>
        </template>
      </el-table-column>
    </TableList>

    <p class="content-title">
      运维报告
      <span class="operation-btns">
        <el-button type="primary" class="comColor" @click="handleDownloadTemplate()"
          >下载模板</el-button
        >
        <!-- <el-button type="primary" class="comColor" @click="handleUploadOperationReport()"
          >上传报告</el-button
        > -->
        <!-- <el-upload
          :class="['avatar-uploader']"
          accept=".xls,.xlsx,.doc,.docx"
          action="#"
          style="display: inline-block"
          :on-change="getValChange"
          :file-list="fileList"
          :auto-upload="false"
          :show-file-list="false"
        >
          <el-button type="primary" class="comColor">上传报告</el-button>
        </el-upload> -->
      </span>
    </p>
    <!--运维报告表格 -->
    <TableList
      :options="options"
      :columns="OperationReportColumns"
      :dataSource="OperationReportData"
      border
    >
      <el-table-column
        slot="operation"
        label="操作"
        align="center"
        v-bind="options && options.props"
        v-loading="loading_2"
      >
        <template slot-scope="scope">
          <div class="operate-group">
            <!-- <el-button type="text" @click="handleShow(scope.row)"> 查看 </el-button> -->
            <el-button v-if="scope.row.RecordCount" type="text" @click="handleDelete(scope.row)">
              删除
            </el-button>
            <el-button
              v-if="scope.row.RecordCount"
              type="text"
              @click="handleDownloadClick(scope.row, 1)"
            >
              下载
            </el-button>
            <el-upload
              :class="['avatar-uploader']"
              accept=".xls,.xlsx,.doc,.docx"
              action="#"
              style="display: inline-block"
              :on-change="getValChange"
              :file-list="fileList"
              :auto-upload="false"
              :show-file-list="false"
            >
              <el-button type="text">{{
                scope.row.RecordCount ? '重新上传' : '上传报告'
              }}</el-button>
            </el-upload>
          </div>
        </template>
      </el-table-column>
    </TableList>

    <p class="content-title">客户满意度</p>
    <!-- 客户满意度 表格 -->
    <TableList
      :options="options"
      :columns="CustomerSatisfactionColumns"
      :dataSource="CustomerSatisfactionData"
      border
    >
      <el-table-column
        slot="operation"
        label="操作"
        align="center"
        v-bind="options && options.props"
        v-loading="loading_1"
      >
        <template slot-scope="scope">
          <div v-if="scope.row.RecordCount == 0" class="operate-group">
            <el-button type="text" @click="handleAdd(scope.row)"> 添加 </el-button>
          </div>
          <div v-else class="operate-group">
            <el-button type="text" @click="handleShow(scope.row)"> 查看 </el-button>
            <el-button type="text" @click="handleEdit(scope.row)"> 编辑 </el-button>
            <!-- <el-button type="text" @click="handleDownloadClick(scope.row)"> 下载 </el-button> -->
          </div>
        </template>
      </el-table-column>
    </TableList>

    <!-- 查看记录表弹窗 -->
    <RecordFormDialog
      v-if="RecordFormDialogVisible"
      :ProjectProgressId="ProjectProgressId"
      :RecordId="RecordId"
      :OperateType="OperateType"
      @closeDialog="(val) => closeDialog('RecordFormDialogVisible', val)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import RecordFormDialog from './components/RecordFormDialog.vue'
import _data from './components/data.js'
import { DownLoadFileByUrl, DownLoadFileByCreateObjectURL } from '@/utils/util'

export default {
  name: 'ProjectCompletion',
  components: {
    RecordFormDialog
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          this.$set(this, 'ProjectId', nv.ProjectId)
        }
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      loading_1: false,
      loading_2: false,
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 156 // 操作列宽度
        }
      },
      // 完工报告
      CompletionReportColumns: _data.CompletionReportColumns(this),
      CompletionReportData: [], // 完工报告数据
      // 客户满意度
      CustomerSatisfactionColumns: _data.CustomerSatisfactionColumns(this),
      CustomerSatisfactionData: [], // 客户满意度数据

      // 查看/编辑 记录表弹窗
      ProjectId: null,
      ProjectProgressId: null,
      RecordId: null, //操作行数据ID
      OperateType: null, // 操作类型  2--项目查看 4--项目编辑
      RecordFormDialogVisible: false,

      // 运维报告
      OperationReportColumns: _data.OperationReportColumns(this),
      OperationReportData: [], // 运维报告数据
      fileList: []
    }
  },
  mounted() {
    this.ProjectId = this.currentProject.ProjectId
    // console.log('searchForm------', this.searchForm, this.$route.query.ProjectId)
    this.initData()
  },
  methods: {
    initData() {
      this.queryCompletionReportTableData()
      this.queryCustomerSatisfactionTableData()
      this.queryOperationReportTableData()
    },
    handleBackClick() {
      this.$router.push({ path: '/project/project-list' })
    },
    // 查询完工报告
    queryCompletionReportTableData() {
      this.loading = true
      const params = {
        // ...this.searchForm,
        ProjectId: this.ProjectId,
        ProgressType: 2
      }
      this.$api.project.GetGensetProjectProgressSetting(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.$set(this, 'CompletionReportData', res.Data)
        }
        this.loading = false
      })
    },

    // 查询客户满意度
    queryCustomerSatisfactionTableData() {
      this.loading_1 = true
      const params = {
        // ...this.searchForm,
        ProjectId: this.ProjectId,
        ProgressType: 3
      }
      this.$api.project.GetGensetProjectProgressSetting(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.$set(this, 'CustomerSatisfactionData', res.Data)
        }
        this.loading_1 = false
      })
    },

    // 查询运维报告
    queryOperationReportTableData() {
      this.loading_2 = true
      const params = {
        ProjectId: this.ProjectId,
        ProgressType: 4
      }
      this.$api.project.GetGensetProjectProgressSetting(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res)
          this.$set(this, 'OperationReportData', res.Data)
        }
        this.loading_2 = false
      })
    },

    // 新增
    handleAdd(row) {
      this.ProjectProgressId = row.ProgressId
      this.RecordId = null
      this.OperateType = 1
      this.RecordFormDialogVisible = true
    },

    // 编辑
    handleEdit(row) {
      this.ProjectProgressId = row.ProgressId
      this.RecordId = row.LastRecordId
      this.OperateType = 4
      this.RecordFormDialogVisible = true
    },

    // 查看
    handleShow(row) {
      this.ProjectProgressId = row.ProgressId
      this.RecordId = row.LastRecordId
      this.OperateType = 2
      this.RecordFormDialogVisible = true
    },

    // 下载
    handleDownloadClick(row, type) {
      // 下载运维报告
      if (type) {
        // this.$api.project
        //   .DoDownloadProgressRecordFile({ RecordId: row.LastRecordId })
        //   .then((res) => {
        //     console.log(res)
        //     DownLoadFileByCreateObjectURL(res)
        //   })

        const url = `${this.$api.project.DoDownloadProgressRecordFileUrl}?RecordId=${row.LastRecordId}`
        DownLoadFileByUrl(url)
      }
    },

    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryCompletionReportTableData()
        this.queryCustomerSatisfactionTableData()
      }
    },

    /********** 运维报告 **********/
    // 下载模板
    handleDownloadTemplate() {
      // this.$api.project.DoDownloadProgressRecordTemplate({ ProgressId: 17 }).then((res) => {
      //   console.log(res)
      //   DownLoadFileByCreateObjectURL(res)
      // })
      const url = `${this.$api.project.DoDownloadProgressRecordTemplateUrl}?ProgressId=${this.OperationReportData[0].ProgressId}`
      DownLoadFileByUrl(url)
    },

    // 删除
    handleDelete(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '删除记录',
        message: h('div', [h('p', [h('span', '确定删除当前记录吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: row.LastRecordId }
          this.$api.project.DeleteGensetProjectProgressRecord(query).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '删除记录成功!'
              })
              this.queryOperationReportTableData()
            } else {
              this.$message({
                type: 'error',
                message: '删除记录失败，请重新尝试!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 上传报告
    // 文件值改变时触发 change事件
    getValChange(file, fileList) {
      const type = file.raw.type
      let isCorrectType = [
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        'application/vnd.ms-excel',
        'application/pdf',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'application/msword',
        'application/vnd.openxmlformats-officedocument.presentationml.presentation',
        'application/vnd.ms-powerpoint'
      ].includes(type)
      if (!isCorrectType) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.error('文件类型错误!')
        return false
      }
      const isLt30M = file.size / 1024 <= 30 * 1024
      if (!isLt30M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 30MB!')
        return false
      }

      this.getfileUpload(file, fileList)
    },
    // 上传调用后台接口
    getfileUpload(file, fileList) {
      const loading = this.$loading({
        lock: true,
        text: '处理中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      let formData = new FormData()
      formData.append('file', file.raw)

      this.$api.common.uploadFile(formData).then((res) => {
        if (res.Code == 1) {
          const list = []
          res.Data.map((item) => {
            let fileObj = {
              url: item
            }
            list.push(fileObj)
          })
          this.$set(this, 'fileList', this.fileList.concat(list))
          this.handleUploadOperationReport()
        } else {
          this.fileList = []
        }
        setTimeout(() => {
          loading.close()
        }, 300)
      })
    },
    async handleUploadOperationReport() {
      const template_params = {
        ProgressId: this.OperationReportData[0].ProgressId,
        ProjectId: this.ProjectId
      }
      const templateData = await this.$api.project
        .GetGensetProjectProgressStencilProject(template_params)
        .then((res) => {
          return res.Data
        })

      let params = {
        ProjectId: templateData.ProjectDetail.ProjectId,
        ProgressId: templateData.StencilDetail.ProgressId,
        IsGenset: templateData.StencilDetail.IsGenset,
        GensetList: [],
        FileUrl: this.fileList.map((item) => item.url).join(';'),
        StencilList: templateData.StencilDetail.StencilModel,
        ItemList: templateData.StencilDetail.ItemModel
      }
      if (!this.OperationReportData[0].LastRecordId) {
        // 新增运维报告
        this.$api.project.AddGensetProjectProgressRecord(params).then((res) => {
          if (res.Code === 1) {
            this.$message.success('上传报告成功!')
            this.queryOperationReportTableData()
          }
        })
        return
      }
      // 编辑运维报告--重新上传
      const query = {
        id: this.OperationReportData[0].LastRecordId
      }
      this.$api.project.EditGensetProjectProgressRecord(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message.success('重新上传报告成功!')
          this.queryOperationReportTableData()
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.project-completion {
  .content-title {
    margin-top: 32px;
    position: relative;
    .operation-btns {
      position: absolute;
      right: 16px;
    }
  }
}
</style>



