 * @Autor: linxu
 * @Date: 2022-08-16 12:00:17
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-19 15:49:02
 * @Description: 项目信息页面
-->
<template>
  <div>
    <el-container>
      <el-main>
        <div id="project-mould-page">
          <project-info
            ref="projectInfo"
            :projectBasicInfo="projectBasicInfo"
            :hasBackBtn="hasBackBtn"
            @handleRefresh="handleRefresh"
          ></project-info>

          <el-tabs v-model="activeName" @tab-click="handleClickTab" class="project-tabs">
            <el-tab-pane label="项目信息" name="1"></el-tab-pane>
            <el-tab-pane label="项目机组" name="2"></el-tab-pane>
            <el-tab-pane label="项目监控" name="3"></el-tab-pane>
            <el-tab-pane label="工程管理" name="4"></el-tab-pane>
            <el-tab-pane label="完工记录" name="5"></el-tab-pane>
          </el-tabs>

          <!-- 动态组件 -->
          <component :is="currentView" ref="subComponent"></component>

          <!-- 标签对应组件引入 -->
          <!-- 项目信息 -->
          <!-- <project-details v-if="activeName == 1"></project-details> -->
          <!-- 项目机组 -->
          <!-- <project-gensets v-if="activeName == 2"></project-gensets> -->
          <!-- 项目监控 -->
          <!-- <project-monitoring v-if="activeName == 3"></project-monitoring> -->
          <!-- 工程管理 -->
          <!-- <project-progress-manage v-if="activeName == 4"></project-progress-manage> -->
          <!-- 完工记录 -->
          <!-- <project-completion-record v-if="activeName == 5"></project-completion-record> -->
        </div>
      </el-main>
      <!-- <toggle-crew ></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import ProjectInfo from './components/ProjectInfo'
import { formatTime } from '@/utils/common'

import ProjectDetails from '../projectManage/projectDetails'
import ProjectGensets from '../projectManage/projectGensets'
import ProjectMonitoring from '../projectMonitoring/projectMonitorNew.vue'
import ProjectProgressManage from '../projectManage/projectProgressManage'
import ProjectCompletionRecord from '../projectManage/projectCompletionRecord'
const tabList = [
  { key: '1', value: '项目信息', componentName: 'ProjectDetails' },
  { key: '2', value: '项目机组', componentName: 'ProjectGensets' },
  { key: '3', value: '项目监控', componentName: 'ProjectMonitoring' },
  { key: '4', value: '工程管理', componentName: 'ProjectProgressManage' },
  { key: '5', value: '完工记录', componentName: 'ProjectCompletionRecord' }
]
export default {
  name: 'sensorMonitor',
  inject: ['reload'],
  components: {
    ProjectInfo,
    ProjectDetails,
    ProjectGensets,
    ProjectMonitoring,
    ProjectProgressManage,
    ProjectCompletionRecord
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // from.name === 'project-list' &&
      // if (
      //   to.name === 'project-detail' ||
      //   to.name === 'completion-record' ||
      //   to.name === 'project-progress-record'
      // ) {
      //   vm.hasBackBtn = true
      // }
    })
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    currentProject: {
      handler: function (nv) {
        if (Object.getOwnPropertyNames(nv).length !== 1) {
          // console.log(nv)
          this.$set(this, 'projectId', nv.ProjectId)
          this.$set(this, 'projectStatus', nv.Status)
        }
      },
      deep: true,
      immediate: true
    },
    $route: {
      handler: function (nv, ov) {
        // console.log('watch', nv, this.$route.query.active)
        this.activeName = this.$route.query.active || nv.meta.activeNum
        this.currentView = this.tabList.filter(
          (item) => item.key == this.activeName
        )[0].componentName
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      hasBackBtn: false,
      projectId: null,
      projectBasicInfo: {},
      projectStatus: null, // 项目状态
      tabList,
      activeName: '1',
      currentView: ''
    }
  },
  mounted() {},
  methods: {
    handleClickTab() {
      this.currentView = this.tabList.filter((item) => item.key == this.activeName)[0].componentName
    },
    handleRefresh() {
      this.$refs.projectInfo.getMchInfo()
      this.$refs.subComponent.initData && this.$refs.subComponent.initData()
    }
  },
  beforeDestroy() {}
}
</script>

<style lang="less" scoped>
#project-mould-page {
  .project-tabs {
    margin-bottom: 9px;
  }
}
.theme-dark {
  .maintenance-record-dialog {
    /deep/ .el-timeline {
      .el-timeline-item__timestamp {
        color: #fff;
      }
    }
  }
}
.theme-light {
  .maintenance-matter {
    li.title {
      background: #f3f3f3 !important;
    }
  }
}
</style>



